import React from "react";

import {
  ModalContainer,
  ModalHeader,
  ModalDetail,
  ModalButtons,
  ButtonModal
} from "./styles";
import Modal from "react-awesome-modal";

export default function Alert(props) {
  return (
    <Modal
      visible={props.viewModal}
      height="300"
      effect="fadeInUp"
      onClickAway={() => props.setViewModal()}
    >
      <ModalContainer>
        <ModalHeader>Alertas</ModalHeader>
        <ModalDetail>
          Movendo para a lista {props.detail.toList} na posio
          {props.detail.position}
        </ModalDetail>
        <ModalButtons>
          <ButtonModal
            type="button"
            color="#d21212"
            onClick={() => props.setViewModal()}
          >
            cancelar
          </ButtonModal>
          <ButtonModal type="submit" color="#4A6971">
            Criar
          </ButtonModal>
        </ModalButtons>
      </ModalContainer>
    </Modal>
  );
}
