import { createActions, createReducer } from "reduxsauce";
export const { Types, Creators } = createActions({
  fetchRouteRequest: ["query"],
  fetchRouteSuccess: ["data"],
  fetchRouteIdSuccess: ["data"],
  fetchRouteFailure: [],
  updateRouteRequest: ["data"],
  deleteRouteRequest: ["id"],
  createRouteRequest: ["data"],
  clearRoute: []
});

const INITIAL_STATE = {
  routes: [],
  route: {},
  routeByTracker: {},
  loading: false
};

const clearRoute = (state = INITIAL_STATE, actions) => ({
  ...state,
  route: {}
});

const fetchRouteRequest = (state = INITIAL_STATE, actions) => {
  console.log("fetch route request");
  return {
    ...state,
    loading: true
  };
};

const fetchRouteSuccess = (state = INITIAL_STATE, actions) => {
  console.log("scces");
  return {
    ...state,
    routes: actions.data,
    loading: false
  };
};

const fetchRouteIdSuccess = (state = INITIAL_STATE, actions) => {
  return {
    ...state,
    route: actions.data,
    loading: false
  };
};

const fetchRouteFailure = (state = INITIAL_STATE, actions) => ({
  ...state,
  loading: false,
  routes: []
});

const createRouteRequest = (state = INITIAL_STATE, actions) => (
  console.log(actions, "actrions"),
  {
    ...state,
    loading: true
  }
);

const updateRouteRequest = (state = INITIAL_STATE, actions) => ({
  ...state,
  loading: true
});

const deleteRouteRequest = (state = INITIAL_STATE, actions) => ({});
export default createReducer(INITIAL_STATE, {
  [Types.FETCH_ROUTE_REQUEST]: fetchRouteRequest,
  [Types.FETCH_ROUTE_SUCCESS]: fetchRouteSuccess,
  [Types.FETCH_ROUTE_ID_SUCCESS]: fetchRouteIdSuccess,
  [Types.FETCH_ROUTE_FAILURE]: fetchRouteFailure,
  [Types.UPDATE_ROUTE_REQUEST]: updateRouteRequest,
  [Types.DELETE_ROUTE_REQUEST]: deleteRouteRequest,
  [Types.CREATE_ROUTE_REQUEST]: createRouteRequest,
  [Types.CLEAR_ROUTE]: clearRoute
});
