import { call, put, all, takeEvery } from "redux-saga/effects";
import api from "../../services/api";
import { Types } from "../ducks/commandSauce";
import { toast } from "react-toastify";
import { push } from "connected-react-router";
import _ from "lodash";

export function* commandSaga(payload) {
  try {
    let response = yield call(api.fetchCommand, payload.query);
    if (!_.isArray(response.data)) {
      yield put({
        type: Types.FETCH_COMMAND_ID_SUCCESS,
        data: response.data
      });
    } else {
      yield put({
        type: Types.FETCH_COMMAND_SUCCESS,
        data: response.data
      });
    }
  } catch (error) {
    console.log(error);
    yield put({ type: Types.FETCH_FAILURE });
  }
}

export function* createCommandSaga(payload) {
  try {
    yield call(api.createCommand, payload.data);
    yield put({
      type: Types.FETCH_COMMAND_REQUEST
    });
    toast.success("cadastrado com sucesso!");
    yield put(push("/command"));
  } catch (error) {
    yield put({ type: Types.FETCH_COMMAND_FAILURE });
    toast.error("Ocorreu um erro");
  }
}

export default function* rootSaga() {
  yield all([takeEvery(Types.FETCH_COMMAND_REQUEST, commandSaga)]);
  yield all([takeEvery(Types.CREATE_COMMAND_REQUEST, createCommandSaga)]);
}
