import { call, put, all, takeEvery } from "redux-saga/effects";
import Api from "../../services/api";
import { Types } from "../ducks/bufferSauce";

export function* bufferSaga(payload) {
  try {
    let buffer = yield call(Api.fetchBuffer, payload.id);
    console.log("!buffer", buffer);
    let groups = yield call(Api.fetchGroups);
    yield put({
      type: Types.BUFFER_SUCCESS,
      data: buffer.data,
      groups: groups.data
    });
  } catch (error) {
    console.log(error);
    yield put({ type: Types.BUFFER_FAILURE });
  }
}

export default function* rootSaga() {
  yield all([takeEvery(Types.BUFFER_REQUEST, bufferSaga)]);
}
