import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import { useFormik } from "formik";
import { toast } from "react-toastify";
import { MdCheck } from "react-icons/md";
import api from "../../services/api";
import "pretty-checkbox/dist/pretty-checkbox.min.css";
import {
  Container,
  ContainerButtons,
  Button,
  Inputstyle,
  ContainerInput,
  ContainerCars,
  Formstyle,
  CheckContainer
} from "./styles";

import { bindActionCreators } from "redux";
import { Creators as BufferActions } from "../../store/ducks/bufferSauce";

function Group(props) {
  const formik = useFormik({
    initialValues: {
      name: "",
      cars: []
    },
    validate: values => {
      const err = {};
      const message = "Campo obrigatrio";
      if (!values.name) err.name = message;
      return err;
    },
    onSubmit: async (values, bag) => {
      const carsSelect = await values.cars
        .filter(item => {
          if (item.checked) {
            return item;
          }
        })
        .map(item => item.tracker_id);
      const data = {
        name: values.name,
        id_trackers: carsSelect
      };
      if (!values.id) {
        const response = await api.setGroup(data);
        if (response.status === 200) {
          toast.success("Grupo cadastrado com sucesso!");
          props.bufferRequest();
          props.setViewGroup();
        }
      } else {
        const response = await api.updateGroups(values.id, data);
        if (response.status === 200) {
          toast.success("Grupo atualizado com sucesso!");
          props.bufferRequest();
          props.setViewGroup();
        }
      }
    },
    enableReinitialize: true
  });

  const [buffer, setBuffer] = useState([]);

  async function getBuffer() {
    const response = await api.getBuffer();
    if (response.status === 200) {
      setBuffer(response.data);
    }
  }

  function setCars() {
    const cars = buffer.map(item => {
      return { ...item, checked: false };
    });
    console.log(cars);
    formik.setValues({ name: "", cars });
  }
  async function getGroupSelected(id) {
    if (id) {
      const response = await api.fetchGroups(id);
      await setCars();
      const data = response.data;
      const cars = await buffer.map(item => {
        if (data.id_trackers.includes(item.tracker_id)) {
          return { ...item, checked: true };
        } else {
          return { ...item, checked: false };
        }
      });
      formik.setValues({ name: data.name, cars, id: data.id });
    } else {
      setCars();
    }
  }
  useEffect(() => {
    getBuffer();
  }, []);
  useEffect(() => {
    getGroupSelected(props.groupSelected);
  }, [props.groupSelected, buffer]);

  return (
    <>
      <Formstyle onSubmit={formik.handleSubmit}>
        <Container>
          <ContainerInput>
            <Inputstyle
              name="name"
              placeholder="Nome do grupo"
              type="text"
              onChange={formik.handleChange}
              value={formik.values.name}
              // error={metadataName.touch && metadataName.error}
            />
          </ContainerInput>

          <ContainerCars>
            {formik.values.cars.map((car, index) => {
              //  formik.getFieldProps(`cars[${index}].checked`);
              // let [i] = formik.values.cars[index].checked;
              return (
                <CheckContainer key={index}>
                  {/* <div className="pretty p-icon p-curve  p-smooth p-plain"> */}
                  <input
                    type="checkbox"
                    // id={index}
                    checked={car.checked}
                    onChange={event =>
                      formik.setFieldValue(
                        `cars[${index}].checked`,
                        event.target.checked
                      )
                    }
                    value={formik.values.cars[index].checked}
                    // {...i}
                  />
                  {`  ${car.label}`}
                  {/* <div className="state p-primary">
                      <MdCheck className="icon" />
                      <label htmlFor={index}>{car.label}</label>
                    </div> */}
                  {/* </div> */}
                </CheckContainer>
              );
            })}
          </ContainerCars>
          <ContainerButtons>
            <Button
              type="button"
              color="#E33939"
              onClick={() => props.setViewGroup()}
            >
              <span>Sair</span>
            </Button>
            <Button color="#555" type="submit">
              <span>Salvar</span>
            </Button>
          </ContainerButtons>
        </Container>
      </Formstyle>
    </>
  );
}

const mapDispatchToProps = dispatch =>
  bindActionCreators(BufferActions, dispatch);

export default connect(
  null,
  mapDispatchToProps
)(Group);
