import React from "react";
import {
  ContainerFilters,
  HeaderFilter,
  ButtonsFilter,
  BodyFilter,
  FilterButton,
  LabelStyled,
  FieldContainer,
  MaskStyle
} from "../Global";
import { connect } from "react-redux";
import { Creators as DriverActions } from "../../store/ducks/driverSauce";
import { bindActionCreators } from "redux";
import { mountParams } from "../util/Mountparams";
import { useFormik } from "formik";
import SelectDriver from "../Global/SelectDriver";

function Driver(props) {
  const formik = useFormik({
    initialValues: {
      name: "",
      cpf: ""
    },
    onSubmit: values => {
      console.log(values);
      const query = mountParams(values);
      props.fetchDriverRequest(query);
    }
  });

  function clearButton() {
    props.fetchDriverRequest();
  }

  return (
    <ContainerFilters>
      <form onSubmit={formik.handleSubmit}>
        <HeaderFilter>MOTORISTA</HeaderFilter>
        <BodyFilter>
          <FieldContainer>
            <SelectDriver
              parentCallback={option => formik.setFieldValue("name", option.id)}
            />
          </FieldContainer>
          <FieldContainer>
            <div>
              <LabelStyled>CPF</LabelStyled>
            </div>
            <MaskStyle
              onChange={formik.handleChange}
              value={formik.values.cpf}
              id="cpf"
              name="cpf"
              mask={"999.999.999-99"}
              maskChar={null}
            />
          </FieldContainer>
        </BodyFilter>
        <ButtonsFilter>
          <FilterButton
            front="#000000"
            back="#bfbfbf"
            type="button"
            onClick={() => clearButton()}
          >
            Limpar
          </FilterButton>
          <FilterButton
            back="#737373"
            onClick={() => props.history.push("/driver/form")}
          >
            Novo
          </FilterButton>
          <FilterButton back="#4A6971" type="submit">
            Pesquisar
          </FilterButton>
        </ButtonsFilter>
      </form>
    </ContainerFilters>
  );
}

const mapStateToProps = state => ({
  drivers: state.driverState.drivers,
  loading: state.driverState.loading
});

const mapDispatchToProps = dispatch =>
  bindActionCreators(DriverActions, dispatch);

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Driver);
