import React, { useState, useEffect } from "react";
import {
  ContainerFilters,
  HeaderFilter,
  ButtonsFilter,
  SelectStyled,
  BodyFilter,
  LabelStyled,
  FilterButton,
  FieldContainer
} from "../Global";
import { connect } from "react-redux";
import { Creators as TrackerActions } from "../../store/ducks/trackerSauce";
import { bindActionCreators } from "redux";
import { useFormik } from "formik";
import { mountParams } from "../util/Mountparams";
import SelectTracker from "../Global/SelectTracker";

function Tracker(props) {
  const [formatedTracker, setFormatedTracker] = useState([]);
  const formik = useFormik({
    initialValues: {
      plate: "",
      active: ""
    },
    onSubmit: data => {
      console.log(data);
      const query = mountParams(data);
      props.fetchTrackerRequest(query);
    }
  });

  useEffect(() => {
    props.fetchTrackerRequest();
  }, []);

  function clearButton() {
    props.fetchTrackerRequest();
  }

  const statusSelect = [
    { value: true, label: "Ativo" },
    { value: false, label: "Inativo" }
  ];

  useEffect(() => {
    setFormatedTracker(
      props.trackers.map(item => ({
        value: item.plate,
        label: `${item.plate}`
      }))
    );
  }, [props.trackers]);

  return (
    <ContainerFilters>
      <form onSubmit={formik.handleSubmit}>
        <HeaderFilter>VECULOS</HeaderFilter>
        <BodyFilter>
          <FieldContainer>
            <SelectTracker
              parentCallback={option =>
                formik.setFieldValue("plate", option.plate)
              }
            />
          </FieldContainer>
          <FieldContainer>
            <LabelStyled>Status</LabelStyled>
            <SelectStyled
              id="active"
              name="active"
              options={statusSelect}
              placeholder="Selecione um status"
              noOptionsMessage={() => "Nenhum status encontrado"}
              // value={formik.values.active}
              // onChange={formik.handleChange}
              // getOptionValue={option => option.id}
            />
          </FieldContainer>
        </BodyFilter>
        <ButtonsFilter>
          <FilterButton
            front="#000000"
            back="#bfbfbf"
            type="button"
            onClick={() => clearButton()}
          >
            Limpar
          </FilterButton>
          <FilterButton type="submit" back="#4A6971">
            Pesquisar
          </FilterButton>
        </ButtonsFilter>
      </form>
    </ContainerFilters>
  );
}

const mapStateToProps = state => ({
  trackers: state.trackerState.trackers,
  loading: state.trackerState.loading
});

const mapDispatchToProps = dispatch =>
  bindActionCreators(TrackerActions, dispatch);

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Tracker);
