import React, { useState, useEffect } from "react";
import { Row } from "./styles";
import {
  ContainerTitle,
  FormContainer,
  FormData,
  DataContainer,
  ContainerStyled,
  ButtonsContainer,
  Button,
  ProfileContainer,
  ErrorMessage,
  FieldContainer,
  TabContainer,
  TabView,
  LabelStyled,
  Input
} from "../Global";
import { useFormik } from "formik";
import { connect } from "react-redux";
import { Creators as TrackerActions } from "../../store/ducks/trackerSauce";
import { bindActionCreators } from "redux";
import _ from "lodash";
import SelectTracker from "../Global/SelectTracker";
import SelectFence from "../Global/SelectFence";
import Switch from "react-switch";

function Tracker(props) {
  const [tab, setTab] = useState(1);
  const [check, setCheck] = useState(false);

  useEffect(() => {
    props.fetchTrackerRequest(`/${props.match.params.id}`);
  }, []);

  useEffect(() => {
    if (!_.isEmpty(props.tracker)) {
      formik.setValues({
        ...props.tracker,
        active: props.tracker.active ? "Ativo" : "Inativo",
        hour_meter: props.tracker.hour_meter ? "Hormetro" : "Odmetro"
      });
    }
  }, [props.tracker]);

  const formik = useFormik({
    initialValues: {
      label: "",
      label_icon: "",
      type: "",
      brand: "",
      model: "",
      year: "",
      color: "",
      plate: "",
      chassis: "",
      hour_meter: "",
      active: "",
      device_id: "",
      client: {},
      speed_max: "",
      driving_time_start: "",
      driving_time_end: "",
      fence_id: "",
      weekend: true
    },
    validate: values => {
      const error = {};
      const message = "Campo obrigatrio";
      if (!values.label) error.label = message;
      if (!values.label_icon) error.label_icon = message;
      return error;
    },
    onSubmit: values => {
      props.updateTrackerRequest(values);
    }
  });

  return (
    <ContainerStyled>
      <FormContainer>
        <TabContainer>
          <TabView active={tab === 1} onClick={() => setTab(1)}>
            DADOS
          </TabView>
          <TabView active={tab === 2} onClick={() => setTab(2)}>
            CONFIGURAES
          </TabView>
          {/* <TabView active={tab === 3} onClick={() => setTab(3)}>
            RELATRIOS
          </TabView> */}
        </TabContainer>
        <form onSubmit={formik.handleSubmit}>
          <FormData>
            <DataContainer>
              {tab === 1 ? (
                <ProfileContainer>
                  <ContainerTitle>DADOS</ContainerTitle>
                  <div>
                    <Row>
                      <FieldContainer>
                        <div>
                          <LabelStyled>IDENTIFICADOR</LabelStyled>
                          {!formik.values.label && formik.errors.label ? (
                            <ErrorMessage>Campo obrigatrio</ErrorMessage>
                          ) : (
                            <></>
                          )}
                        </div>
                        <Input
                          error={!formik.values.label && formik.errors.label}
                          name="label"
                          onChange={formik.handleChange}
                          value={formik.values.label}
                          inputValue={formik.values.label}
                        />
                      </FieldContainer>
                      <FieldContainer>
                        <div>
                          <LabelStyled>TIPO ITEM RASTREAMENTO</LabelStyled>
                        </div>
                        <Input
                          name="type"
                          disabled={true}
                          onChange={formik.handleChange}
                          value={formik.values.type}
                          inputValue={formik.values.type}
                        />
                      </FieldContainer>
                    </Row>
                    <Row>
                      <FieldContainer>
                        <div>
                          <LabelStyled>MARCA</LabelStyled>
                        </div>
                        <Input
                          name="brand"
                          disabled={true}
                          onChange={formik.handleChange}
                          value={formik.values.brand}
                          inputValue={formik.values.brand}
                        />
                      </FieldContainer>
                      <FieldContainer>
                        <div>
                          <LabelStyled>MODELO</LabelStyled>
                        </div>
                        <Input
                          name="model"
                          disabled={true}
                          onChange={formik.handleChange}
                          value={formik.values.model}
                          inputValue={formik.values.model}
                        />
                      </FieldContainer>
                      <FieldContainer>
                        <div>
                          <LabelStyled>ANO</LabelStyled>
                        </div>
                        <Input
                          name="model"
                          disabled={true}
                          onChange={formik.handleChange}
                          value={formik.values.year}
                          inputValue={formik.values.year}
                        />
                      </FieldContainer>
                      <FieldContainer>
                        <div>
                          <LabelStyled>COR</LabelStyled>
                        </div>
                        <Input
                          name="color"
                          disabled={true}
                          onChange={formik.handleChange}
                          value={formik.values.color}
                          inputValue={formik.values.color}
                        />
                      </FieldContainer>
                      <FieldContainer>
                        <div>
                          <LabelStyled>PLACA</LabelStyled>
                        </div>
                        <Input
                          name="plate"
                          disabled={true}
                          onChange={formik.handleChange}
                          value={formik.values.plate}
                          inputValue={formik.values.plate}
                        />
                      </FieldContainer>
                    </Row>
                    <Row>
                      <FieldContainer>
                        <div>
                          <LabelStyled>CHASSI</LabelStyled>
                        </div>
                        <Input
                          name="chassis"
                          disabled={true}
                          onChange={formik.handleChange}
                          value={formik.values.chassis}
                          inputValue={formik.values.chassis}
                        />
                      </FieldContainer>
                      <FieldContainer>
                        <div>
                          <LabelStyled>HODOMETRO/HORIMETRO</LabelStyled>
                        </div>
                        <Input
                          name="hour_meter"
                          onChange={formik.handleChange}
                          value={formik.values.hour_meter}
                          disabled
                          mask={undefined}
                          labelName="HODOMETRO/HORIMETRO"
                          inputValue={
                            formik.values.hour_meter
                              ? { value: "Hodometro" }
                              : { value: "Horimetro" }
                          }
                        />
                      </FieldContainer>
                      <FieldContainer>
                        <div>
                          <LabelStyled>ATIVO</LabelStyled>
                        </div>
                        <Input
                          name="active"
                          onChange={formik.handleChange}
                          value={formik.values.active}
                          mask={undefined}
                          disabled
                          labelName="Ativo"
                          inputValue={
                            formik.values.active
                              ? { value: "Ativo" }
                              : { value: "Inativo" }
                          }
                        />
                      </FieldContainer>
                      <FieldContainer>
                        <div>
                          <LabelStyled>IMEI DO DISPOSITIVO</LabelStyled>
                        </div>
                        <Input
                          name="chassis"
                          disabled={true}
                          onChange={formik.handleChange}
                          value={formik.values.device_id}
                          inputValue={formik.values.device_id}
                        />
                      </FieldContainer>
                    </Row>
                  </div>
                </ProfileContainer>
              ) : tab === 2 ? (
                <ProfileContainer>
                  <ContainerTitle>CONFIGURAES</ContainerTitle>
                  <div>
                    <Row>
                      <FieldContainer>
                        <div>
                          <LabelStyled>ALERTA DE VELOCIDADE MXIMA</LabelStyled>
                        </div>
                        <Input
                          disabled={false}
                          mask="999"
                          name="speed_max"
                          onChange={formik.handleChange}
                          value={formik.values.speed_max}
                          inputValue={formik.values.speed_max}
                        />
                      </FieldContainer>
                    </Row>
                    <LabelStyled>
                      ALERTA DE MOVIMENTAO NO FIM DE SEMANA
                    </LabelStyled>
                    {/* Campo weekend no esta alterando o valor */}
                    <label>
                      <Switch
                        name="weekend"
                        onChange={() => setCheck(!check)}
                        checked={check}
                        value={formik.values.weekend}
                        onColor="#0388fc"
                      />
                    </label>
                    <LabelStyled>ALERTAS DE TEMPO</LabelStyled>
                    <Row>
                      <FieldContainer>
                        <div>
                          <LabelStyled>TEMPO INCIO</LabelStyled>
                        </div>
                        <Input
                          disabled={false}
                          mask="99:99:99"
                          name="driving_time_start"
                          onChange={formik.handleChange}
                          value={formik.values.driving_time_start}
                          inputValue={formik.values.driving_time_start}
                        />
                      </FieldContainer>
                      <FieldContainer>
                        <div>
                          <LabelStyled>TEMPO FINAL</LabelStyled>
                        </div>
                        <Input
                          name="driving_time_end"
                          disabled={false}
                          mask="99:99:99"
                          onChange={formik.handleChange}
                          value={formik.values.driving_time_end}
                          inputValue={formik.values.driving_time_end}
                        />
                      </FieldContainer>
                    </Row>
                    <LabelStyled>ALERTAS DE CERCA</LabelStyled>
                    <Row>
                      <FieldContainer>
                        <FieldContainer>
                          <LabelStyled>Nome da Cerca</LabelStyled>
                          <SelectFence
                            defaultValue={formik.values.fence_id}
                            parentCallback={option =>
                              formik.setFieldValue("fence_id", option.id)
                            }
                          />
                        </FieldContainer>
                      </FieldContainer>
                    </Row>
                  </div>
                </ProfileContainer>
              ) : (
                // ) : tab === 3 ? (
                //   <ProfileContainer>
                //     <ContainerTitle>RELATRIOS</ContainerTitle>
                //   </ProfileContainer>
                // )
                <></>
              )}
            </DataContainer>
            <ButtonsContainer>
              <Button
                onClick={() => props.history.push("/tracker")}
                type="button"
                color="#E33939"
              >
                Cancelar
              </Button>
              <Button type="submit" color="#555">
                Salvar
              </Button>
            </ButtonsContainer>
          </FormData>
        </form>
      </FormContainer>
    </ContainerStyled>
  );
}

const mapStateToProps = state => ({
  tracker: state.trackerState.tracker,
  loading: state.trackerState.loading
});

const mapDispatchToProps = dispatch =>
  bindActionCreators(TrackerActions, dispatch);

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Tracker);
