import React, { useState, useEffect } from "react";
import { Form } from "@rocketseat/unform";
import axios from "../../services/axios";
import { ButtonStyled, InputStyled } from "../Global";
import { Container, Main, Error } from "./styles";

import Loading from "../Loading";

export default function Password(props) {
  const [linkExpired, setLinkExpired] = useState(false);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [error, setError] = useState(null);

  useEffect(() => {
    const {
      match: { params }
    } = props;

    axios.get(`/auth/forgot/${params.token}`).catch(err => {
      setLinkExpired(true);
    });
  }, [props, props.history]);

  async function handleSubmit(data) {
    setIsSubmitting(true);
    try {
      const response = await axios.post("/auth/login", data);
      localStorage.setItem("token", response.data.token);
      setIsSubmitting(false);
      props.history.push("/");
    } catch (error) {
      console.log(error);
      const { data } = error.response;
      setIsSubmitting(false);
      setError(data.error);
    }
  }

  // function handleForgot() {
  //   props.history.push("/auth/forgot");
  // }

  return (
    <>
      <Container>
        {linkExpired ? (
          <>Link expirado!</>
        ) : (
          <>
            {!isSubmitting ? (
              <Main>
                <h3>Cadastre a nova senha</h3>
                <Form onSubmit={handleSubmit}>
                  <InputStyled
                    type="password"
                    required
                    name="password"
                    placeholder="Senha"
                  />
                  <InputStyled
                    type="password"
                    required
                    name="password"
                    placeholder="Repita a senha"
                  />
                  <ButtonStyled color="#227AD7">Salvar</ButtonStyled>
                  <Error>{error ? error : ""}</Error>
                </Form>
              </Main>
            ) : (
              <Loading />
            )}
          </>
        )}
      </Container>
    </>
  );
}
