import React, { useState } from "react";
import { loadList } from "../../services/alertsExample";
import { ContainerBoard } from "./styles";
import BoardContext from "./context";
import List from "./list";
import produce from "immer";
import Modal from "./modal";

const data = loadList();

export default function Alert() {
  const [lists, setLists] = useState(data);
  const [modal, setModal] = useState(false);
  const [detail, setDetail] = useState({});

  function move(fromList, toList, from, to) {
    setLists(
      produce(lists, draft => {
        const dragged = draft[fromList].cards[from];
        draft[fromList].cards.splice(from, 1);
        draft[toList].cards.splice(to, 0, dragged);
      })
    );
  }
  function moveToList(fromList, toList, from) {
    setLists(
      produce(lists, draft => {
        const dragged = draft[fromList].cards[from];
        draft[fromList].cards.splice(from, 1);
        draft[toList].cards.push(dragged);
      })
    );
  }
  function showModal(toList, position) {
    console.log("show Modal", toList, position);
    setViewModal();
    setDetail({ toList, position });
  }

  function setViewModal() {
    setModal(!modal);
  }

  return (
    <BoardContext.Provider value={{ lists, move, moveToList, showModal }}>
      <ContainerBoard>
        {lists.map((list, index) => (
          <List key={list.title} index={index} data={list} />
        ))}
      </ContainerBoard>
      <Modal
        viewModal={modal}
        setViewModal={() => setViewModal()}
        detail={detail}
      />
    </BoardContext.Provider>
  );
}
