import React, { useEffect } from "react";
import { connect } from "react-redux";
import { Creators as MaintenanceActions } from "../../store/ducks/maintenanceSauce";
import { bindActionCreators } from "redux";
import Table from "../Table";
import moment from "moment";

function Maintenance(props) {
  useEffect(() => {
    props.fetchMaintenanceRequest();
  }, []);

  return (
    <>
      <Table
        data={props.maintenances.map(item => ({
          ...item,
          date_start: moment(item.date_start).format("DD/MM/YYYY"),
          date_end: moment(item.date_end).format("DD/MM/YYYY"),
          tracker_id: `${item.trackerItem.label} - ${item.trackerItem.plate}`,
          maintenanceType:
            item.maintenanceType === "corrective"
              ? "Corretiva"
              : item.maintenanceType === "preventive"
              ? "Preventiva"
              : item.maintenanceType === "detective"
              ? "Detectiva"
              : "Aguardando..."
        }))}
        labelsNames={[
          "tracker_id",
          "maintenanceType",
          "date_start",
          "date_end",
          "price"
        ]}
        header={[
          "Veculo",
          "Tipo de Manuteno",
          "Data de Incio",
          "Data Final",
          "Custo Total"
        ]}
        changeValue={data => props.history.push(`/maintenance/form/${data.id}`)}
      />
    </>
  );
}

const mapStateToProps = state => ({
  maintenances: state.maintenanceState.maintenances,
  loading: state.maintenanceState.loading
});

const mapDispatchToProps = dispatch =>
  bindActionCreators(MaintenanceActions, dispatch);

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Maintenance);
