import React, { useEffect, useState } from "react";
import {
  ContainerFilters,
  FieldContainer,
  LabelStyled,
  DateStyle,
  SelectStyled,
  FilterButton,
  BodyFilter,
  HeaderFilter,
  ButtonsFilter
} from "../Global";
import InputMask from "react-input-mask";
import { Creators as JourneyActions } from "../../store/ducks/journeySauce";
import { useFormik } from "formik";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { mountParams } from "../util/Mountparams";
import moment from "moment";
import pt from "date-fns/locale/pt";
import { toast } from "react-toastify";
import _ from "lodash";
import SelectTracker from "../Global/SelectTracker";
import SelectDriver from "../Global/SelectDriver";

function JourneyFilter(props) {
  const formik = useFormik({
    initialValues: {
      tracker_id: "",
      driver_id: "",
      initialDate: "",
      finalDate: ""
    },
    validate: values => {
      console.log(values);
      const err = {};
      if (values.initialDate) {
        if (!values.finalDate) {
          err.finalDate = "Data Invalida";
        }
      }
      if (values.finalDate) {
        if (!values.initialDate) {
          err.initialDate = "Data Invalida";
        }
      }
      if (values.initialDate !== "" && values.finalDate !== "") {
        if (values.initialDate > values.finalDate) {
          toast.error("Data Inicial no pode ser superior  Data Final.");
        }
      }
      return err;
    },
    onSubmit: values => {
      console.log(values);

      const body = _.clone(values);
      if (values.initialDate)
        body.initialDate = moment(values.initialDate).format("YYYY-MM-DD");
      if (values.finalDate)
        body.finalDate = moment(values.finalDate).format("YYYY-MM-DD");
      const query = mountParams(body);
      props.fetchJourneyRequest(query);
    }
  });

  function clearButton() {
    props.fetchJourneyRequest();
  }

  // const [tracker_id, metadataTracker_id] = getFieldProps("tracker_id", "text");
  // const [initialDate, metadataInitialDate] = getFieldProps(
  //   "initialDate",
  //   "text"
  // );
  // const [finalDate, metadataFinalDate] = getFieldProps("finalDate", "text");

  return (
    <ContainerFilters>
      <form onSubmit={formik.handleSubmit}>
        <HeaderFilter>JORNADA</HeaderFilter>
        <BodyFilter>
          <FieldContainer>
            <SelectTracker
              parentCallback={option =>
                formik.setFieldValue("tracker_id", option.id)
              }
            />
          </FieldContainer>
          <FieldContainer>
            <SelectDriver
              parentCallback={option =>
                formik.setFieldValue("driver_id", option.id)
              }
            />
          </FieldContainer>
          <FieldContainer>
            <LabelStyled>Data Inicial</LabelStyled>
            <DateStyle
              showTimeSelect
              dateFormat="dd/MM/yyyy HH:mm:ss"
              selected={formik.values.initialDate}
              onChange={value =>
                formik.setFieldValue("initialDate", moment(value).toDate())
              }
              locale={pt}
              customInput={<InputMask mask={"99/99/9999 99:99:99"} />}
              // error={metadataDate.error}
            />
          </FieldContainer>
          <FieldContainer>
            <LabelStyled>Data Final</LabelStyled>
            <DateStyle
              showTimeSelect
              dateFormat="dd/MM/yyyy HH:mm:ss"
              selected={formik.values.finalDate}
              onChange={value =>
                formik.setFieldValue("finalDate", moment(value).toDate())
              }
              locale={pt}
              customInput={<InputMask mask={"99/99/9999 99:99:99"} />}
              // error={metadataDate.error}
            />
          </FieldContainer>
        </BodyFilter>
        <ButtonsFilter>
          <FilterButton
            front="#000000"
            back="#bfbfbf"
            onClick={() => clearButton()}
          >
            Limpar
          </FilterButton>
          <FilterButton
            back="#737373"
            onClick={() => props.history.push("/journey/form")}
          >
            Novo
          </FilterButton>
          <FilterButton back="#4A6971" type="submit">
            Pesquisar
          </FilterButton>
        </ButtonsFilter>
      </form>
    </ContainerFilters>
  );
}

const mapDispatchToProps = dispatch =>
  bindActionCreators(JourneyActions, dispatch);

export default connect(
  null,
  mapDispatchToProps
)(JourneyFilter);
