export function iconMarker(icon, rotate, alert) {
  const div = document.createElement("div");
  div.style.cursor = "pointer";

  const ImageIcon = document.createElement("img");
  ImageIcon.src = icon;
  ImageIcon.style.height = "30px";
  ImageIcon.style.width = "30px";
  ImageIcon.style.transform = `rotate(${rotate}deg)`;

  div.appendChild(ImageIcon);

  const ImageAlert = document.createElement("img");
  ImageAlert.src = alert;
  ImageAlert.style.height = "22px";
  ImageAlert.style.width = "22px";
  ImageAlert.style.marginTop = "-15px";
  ImageAlert.style.zIndex = "2";
  ImageAlert.style.position = "absolute";
  // div.appendChild(ImageAlert);

  return div;
}
