import React, { useState, useEffect } from "react";

import {
  ChartContainer,
  ChartPages,
  ChartDetail,
  PageContainer
} from "./styles";
import ChartSpeed from "./chartSpeed";
import { connect } from "react-redux";
function Daily(props) {
  const [tab, setTab] = useState(1);
  const [heigthChart, setHeigthChart] = useState(100);
  useEffect(() => {
    setHeigthChart(document.getElementById("teste").offsetHeight - 1);
  }, []);

  return (
    <ChartContainer>
      <ChartPages>
        <PageContainer active={tab === 1} onClick={() => setTab(1)}>
          Velocidade
        </PageContainer>
        <PageContainer active={tab === 2} onClick={() => setTab(2)}>
          Ociosidade
        </PageContainer>
        <PageContainer active={tab === 3} onClick={() => setTab(3)}>
          Velocidade
        </PageContainer>
      </ChartPages>
      <ChartDetail id="teste">
        {tab === 1 && <ChartSpeed heigth={heigthChart} speed={props.speed} />}
      </ChartDetail>
    </ChartContainer>
  );
}

const mapStateToProps = state => ({
  speed: state.dailyState.speed
});

export default connect(mapStateToProps)(Daily);
