import React, { useEffect, useState } from "react";
import { HorizontalBarResize } from "../Global";
import _ from "lodash";
const axios = require("axios");

function IdlenessChart() {
  const [driverIdleness, setDriverIdleness] = useState({});
  const [labelName, setLabelName] = useState([]);
  const [infractionNumber, setInfractionNumber] = useState([]);

  function getDriverIdleness() {
    axios
      .get("http://localhost:3001/driversIdleness")
      .then(resp => {
        setDriverIdleness(resp.data);
      })
      .catch(error => {
        console.log(error);
      });
  }

  useEffect(() => {
    getDriverIdleness();
  }, []);

  useEffect(() => {
    console.log(driverIdleness, "driverViolation");
    if (driverIdleness.length > 0) {
      console.log("teste");
      setLabelName(driverIdleness.map(item => item.name));
      setInfractionNumber(driverIdleness.map(item => item.idleTime));
    }
    console.log(labelName, "peopleArray");
  }, [driverIdleness]);

  const data = {
    labels: labelName,
    datasets: [
      {
        label: "Tempo Ocioso por Motorista",
        backgroundColor: "rgba(75,192,192,0.4)",
        borderColor: "rgba(75,192,192,1)",
        borderWidth: 1,
        hoverBackgroundColor: "rgba(75,192,192,1)",
        hoverBorderColor: "rgba(220,220,220,1)",
        data: infractionNumber
        // type: "time"
      }
    ]
  };

  return (
    <>
      {driverIdleness !== undefined ? (
        <HorizontalBarResize
          width={null}
          height={300}
          options={{ maintainAspectRatio: false, responsive: false }}
          // options={{ maintainAspectRatio: 1 }}
          data={data}
        />
      ) : (
        ""
      )}
    </>
  );
}

export default IdlenessChart;
