import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import { defaults } from "react-chartjs-2";
import { useLocation } from "react-router-dom";
import { LineStyled } from "./styles";

import _ from "lodash";

function ChartSpeed(props) {
  const [speedData, setSpeedData] = useState([]);
  useEffect(() => {
    defaults.global.defaultFontSize = 15;
  }, []);

  useEffect(() => {
    if (!_.isEmpty(props.speed)) {
      setSpeedData(props.speed);
    }
  }, [props.speed]);

  const data = {
    labels: speedData.map(x => ""),
    datasets: [
      {
        label: "Velocidade",
        fill: false,
        lineTension: 0.1,
        backgroundColor: "rgba(75,192,192,0.4)",
        borderColor: "rgba(75,192,192,1)",
        borderCapStyle: "butt",
        borderDash: [],
        borderDashOffset: 0.0,
        borderJoinStyle: "miter",
        pointBorderColor: "rgba(75,192,192,1)",
        pointBackgroundColor: "#fff",
        pointBorderWidth: 1,
        pointHoverRadius: 5,
        pointHoverBackgroundColor: "rgba(75,192,192,1)",
        pointHoverBorderColor: "rgba(220,220,220,1)",
        pointHoverBorderWidth: 2,
        pointRadius: 1,
        pointHitRadius: 10,
        data: speedData.map(item => item.speed).reverse()
      }
    ]
  };
  return (
    <LineStyled
      data={data}
      options={{
        maintainAspectRatio: false,
        responsive: true
      }}
    />
  );
}

export default connect()(ChartSpeed);
