import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import { Creators as CommandActions } from "../../store/ducks/commandSauce";
import { bindActionCreators } from "redux";
import {
  ModalContainer,
  ModalHeader,
  ModalTextHeader,
  ConfirmBox,
  ConfirmDataContainer,
  ConfirmButtonsContainer,
  CommandNameStyled,
  ItemList,
  BodyFilterConfirm,
  CardInfo,
  CommandConfirmButton,
  PasswordInput,
  PasswordLabelStyled,
  CarregandoMessageStyled,
  DivFlex,
  InlineFlex,
  TextStyled,
  TextStyledBold
} from "./styles";
import { toast } from "react-toastify";
import Modal from "react-awesome-modal";
import positionIcon from "../../assets/infobox/position.svg";
import Driver from "../../assets/side/driver.svg";
import Plate from "../../assets/commandbox/plate.svg";
import Car from "../../assets/commandbox/car.svg";
import Transmitter from "../../assets/commandbox/transmitter.svg";
import Tooltip from "rc-tooltip";
import api from "../../services/api";
import Loading from "./Loading";
import { FaExclamationTriangle, FaCheck, FaTimes } from "react-icons/fa";

function ActionItem(props) {
  return (
    <ItemList rotate={props.rotate}>
      <Tooltip
        placement="right"
        overlay={props.tooltip}
        mouseEnterDelay={0}
        mouseLeaveDelay={0}
        arrowContent={<div className="rc-tooltip-arrow-inner"></div>}
      >
        <img src={props.icon} alt="" />
      </Tooltip>
      <span>{props.text}</span>
    </ItemList>
  );
}

function CommandModal(props) {
  const [trackerId, setTrackerId] = useState("");
  const [type, setType] = useState("");
  const [password, setPassword] = useState("");
  const [commandName, setCommandName] = useState("");
  const [options, setOptions] = useState([]);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    setTrackerId(props.commandAction.id);
    console.log(props, "props");
    setType(props.command);
  }, [props.commandAction, props.command]);

  useEffect(() => {
    console.log(commandName, "command name");
  }, [commandName]);

  useEffect(() => {
    if (props.command == "BLOCK") {
      if (props.commandValue == true) {
        setCommandName("DESBLOQUEAR");
      } else {
        setCommandName("BLOQUEAR");
      }
    }
    if (props.command == "SIREN") {
      if (props.commandValue == true) {
        setCommandName("DESLIGAR SIRENE");
      } else {
        setCommandName("LIGAR SIRENE");
      }
    }
  }, [props.command, props.commandValue]);

  function OnClickButton(props) {
    let dataCommand = {
      tracker_id: trackerId,
      type: type,
      password: password
    };
    console.log(dataCommand, "Comando enviado");
    // api.createCommand(dataCommand);
    sendCommand(dataCommand);
  }

  async function sendCommand(dataCommand) {
    setLoading(true);
    try {
      await api.createCommand(dataCommand);
      toast.success("Comando enviado com sucesso");
    } catch (error) {
      toast.error("Ocorreu um erro. Verifique sua senha.");
    }
    // setOptions(response.data);
    // setLoading(true);
    WindowClose();
    props.setLoadingButton(dataCommand);
  }

  useEffect(() => {
    setLoading(false);
  }, [props.loadingButton]);

  useEffect(() => {
    setLoading(false);
  }, []);

  useEffect(() => {
    console.log(options, "options");
  }, [options]);

  function WindowClose() {
    props.setViewModal();
    setPassword("");
  }

  useEffect(() => {
    console.log(props.viewModal, "props.viewModal");
  }, [props]);

  return (
    <Modal
      // visible={props.viewModal}
      visible={props.viewModal != undefined ? props.viewModal : false}
      effect="fadeInUp"
      onClickAway={() => WindowClose()}
      width="500px"
      height="230px"
    >
      {/* {console.log(props, "teste")} */}
      {!loading ? (
        <ModalContainer>
          {props.commandAction ? (
            <>
              <ModalHeader>
                {/* <ModalTextHeader>{props.commandAction.plate}</ModalTextHeader> */}
                <ModalTextHeader>Confirmao de Comando</ModalTextHeader>
              </ModalHeader>
              <ConfirmBox>
                <ConfirmDataContainer>
                  <BodyFilterConfirm>
                    <CardInfo>
                      <DivFlex>
                        <TextStyled>Placa:&nbsp;</TextStyled>
                        <TextStyledBold>
                          {props.commandAction.plate}
                        </TextStyledBold>
                      </DivFlex>
                      {/* <DivFlex> */}
                      <TextStyled>
                        <FaExclamationTriangle />
                        <span>&nbsp;</span>Tem certeza que deseja &nbsp;
                        {commandName} esse veculo?
                      </TextStyled>
                      {/* </DivFlex> */}
                      <PasswordLabelStyled>
                        Confirme sua Senha
                      </PasswordLabelStyled>
                      <PasswordInput
                        onChange={event => setPassword(event.target.value)}
                        value={password}
                        type="password"
                      />
                    </CardInfo>
                  </BodyFilterConfirm>
                  <hr />
                </ConfirmDataContainer>
              </ConfirmBox>
              <ConfirmButtonsContainer>
                {/* {!loading ? ( */}
                <>
                  <CommandConfirmButton
                    color={"#ff0015"}
                    onClick={() => WindowClose()}
                  >
                    <CommandNameStyled>
                      <FaTimes />
                      &nbsp;No
                    </CommandNameStyled>
                  </CommandConfirmButton>
                  <CommandConfirmButton
                    front="#000000"
                    back="#4A6971"
                    onClick={() => OnClickButton(props)}
                  >
                    {/* <CommandNameStyled> {commandName} </CommandNameStyled> */}
                    <CommandNameStyled>
                      {/* <FaCheck /> &nbsp;{commandName} */}
                      <FaCheck /> &nbsp;Sim
                    </CommandNameStyled>
                  </CommandConfirmButton>
                </>
                {/* ) : (
            <Loading />
          )} */}
              </ConfirmButtonsContainer>
            </>
          ) : (
            ""
          )}
        </ModalContainer>
      ) : (
        <>
          <Loading />
          <CarregandoMessageStyled>Carregando...</CarregandoMessageStyled>
        </>
      )}
    </Modal>
  );
}
const mapStateToProps = state => ({
  viewModal: state.commandState.viewModal,
  loadingButton: state.commandState.loadingButton
});

const mapDispatchToProps = dispatch =>
  bindActionCreators(CommandActions, dispatch);

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(CommandModal);
