import { createActions, createReducer } from "reduxsauce";

export const { Types, Creators } = createActions({
  fetchDailyRequest: ["data"],
  fetchDailySuccess: ["data"],
  fetchDailyFailure: [],
  setShowDetail: []
});

const INITIAL_STATE = {
  daily: {},
  speed: {},
  history: [],
  stops: [],
  showDetail: true,
  loading: false
};

const fetchDailyRequest = (state = INITIAL_STATE, actions) => ({
  ...state,
  loading: true
});

const fetchDailySuccess = (state = INITIAL_STATE, actions) => ({
  ...state,
  daily: actions.data,
  speed: actions.speed,
  history: actions.history,
  stops: actions.stops,
  loading: false
});

const fetchDailyFailure = (state = INITIAL_STATE, actions) => {
  return {
    ...state,
    loading: false,
    daily: {},
    speed: {},
    history: [],
    stops: []
  };
};

const setShowDetail = (state = INITIAL_STATE, actions) => {
  return { ...state, showDetail: !state.showDetail };
};

export default createReducer(INITIAL_STATE, {
  [Types.FETCH_DAILY_REQUEST]: fetchDailyRequest,
  [Types.FETCH_DAILY_SUCCESS]: fetchDailySuccess,
  [Types.FETCH_DAILY_FAILURE]: fetchDailyFailure,
  [Types.SET_SHOW_DETAIL]: setShowDetail
});
