import React, { useEffect, useState } from "react";
import {
  FieldContainer,
  LabelStyled,
  SelectStyled,
  ContainerFilters,
  HeaderFilter,
  ButtonsFilter,
  BodyFilter,
  DateStyle,
  FilterButton,
  Input
} from "../Global";
import InputMask from "react-input-mask";
import { Creators as RouteActions } from "../../store/ducks/routeSauce";
import { useFormik } from "formik";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import api from "../../services/api";
import { mountParams } from "../util/Mountparams";
import moment from "moment";
import pt from "date-fns/locale/pt";
import { toast } from "react-toastify";
import _ from "lodash";
import SelectTracker from "../Global/SelectTracker";

function RouteFilter(props) {
  const [trackers, setTrackers] = useState([]);
  const [formatedTracker, setFormatedTracker] = useState([]);

  const formik = useFormik({
    initialValues: {
      name: "",
      origin: "",
      destination: ""
    },
    validate: values => {
      console.log(values);
      const err = {};
      return err;
    },
    onSubmit: values => {
      console.log(values);
      const body = _.clone(values);
      if (values.initialDate)
        body.initialDate = moment(values.initialDate).format("YYYY-MM-DD");
      if (values.finalDate)
        body.finalDate = moment(values.finalDate).format("YYYY-MM-DD");
      const query = mountParams(body);
      props.fetchRouteRequest(query);
    }
  });

  function clearButton() {
    props.fetchRouteRequest();
  }

  return (
    <ContainerFilters>
      <form onSubmit={formik.handleSubmit}>
        <HeaderFilter>ROTAS</HeaderFilter>
        <BodyFilter>
          <FieldContainer>
            <LabelStyled>Nome</LabelStyled>
            <Input
              name="nome"
              onChange={formik.handleChange}
              value={formik.values.nome}
              inputValue={formik.values.nome}
            />
          </FieldContainer>
          <FieldContainer>
            <LabelStyled>Origem</LabelStyled>
            <Input
              name="origin"
              onChange={formik.handleChange}
              value={formik.values.origin}
              inputValue={formik.values.origin}
            />
          </FieldContainer>
          <FieldContainer>
            <LabelStyled>Destino</LabelStyled>
            <Input
              name="destination"
              onChange={formik.handleChange}
              value={formik.values.destination}
              inputValue={formik.values.destination}
            />
          </FieldContainer>
        </BodyFilter>
        <ButtonsFilter>
          <FilterButton
            front="#000000"
            back="#bfbfbf"
            type="button"
            onClick={() => clearButton()}
          >
            Limpar
          </FilterButton>
          <FilterButton
            color="#737373"
            onClick={() => props.history.push("/route/map")}
          >
            Novo
          </FilterButton>
          <FilterButton color="#4A6971" type="submit">
            Pesquisar
          </FilterButton>
        </ButtonsFilter>
      </form>
    </ContainerFilters>
  );
}

const mapDispatchToProps = dispatch =>
  bindActionCreators(RouteActions, dispatch);

export default connect(
  null,
  mapDispatchToProps
)(RouteFilter);
