import React, { useState, useEffect } from "react";
import {
  ContainerTitle,
  FormData,
  FormContainer,
  DataContainer,
  ContainerStyled,
  ButtonsContainer,
  Button,
  ProfileContainer,
  FormDocs,
  TabContainer,
  Input,
  FieldContainer,
  Row,
  LabelStyled,
  TabView,
  ErrorMessage,
  DateStyle,
  SelectStyled,
  MoneyDiv,
  MaskMoneyStyle,
  NoRow,
  DoubleRow,
  DescContainer,
  TextAreaStyled
} from "../Global";
import { toast } from "react-toastify";
import moment from "moment";
import _ from "lodash";
import { registerLocale } from "react-datepicker";
import el from "date-fns/locale/pt-BR";
import InputMask from "react-input-mask";
import pt from "date-fns/locale/pt";
import { useFormik } from "formik";
import { connect } from "react-redux";
import { Creators as ViolationActions } from "../../store/ducks/violationSauce";
import { bindActionCreators } from "redux";
import SelectDriver from "../Global/SelectDriver";
import SelectTracker from "../Global/SelectTracker";
import FineList from "../../assets/json/fineList.json";
import FineLevel from "../../assets/json/level.json";

function Forms(props) {
  registerLocale("pt-BR", el);
  const [tab, setTab] = useState(1);
  const formik = useFormik({
    initialValues: {
      tracker_id: "",
      driver_id: "",
      date: "",
      reason: "",
      level: "",
      price: "",
      expiry: "",
      description: ""
    },
    validate: values => {
      const err = {};
      const message = "Campo obrigatrio";
      if (!values.tracker_id) err.tracker_id = message;
      if (!values.driver_id) err.driver_id = message;
      if (!values.date) err.date = message;
      if (!values.reason) err.reason = message;
      if (!values.level) err.level = message;
      if (!values.price) err.price = message;
      if (!values.expiry) err.expiry = message;
      if (values.date !== "" && values.expiry !== "") {
        if (values.date > values.expiry) {
          toast.error(
            "Data da Multa no pode ser superior  Data de Vencimento."
          );
          err.date = message;
          err.expiry = message;
        }
      }
      return err;
    },
    onSubmit: data => {
      console.log(data, "data");
      if (data.id) {
        props.updateViolationRequest(data);
      } else {
        props.createViolationRequest(data);
      }
      props.clearViolation();
    }
  });

  useEffect(() => {
    if (props.match.params.id) {
      props.fetchViolationRequest(`/${props.match.params.id}`);
    }
  }, []);

  function cancelButton() {
    props.history.push("/violation");
    props.clearViolation();
  }

  useEffect(() => {
    if (!_.isEmpty(props.violation)) {
      console.log(props.violation, "props");
      formik.setValues({
        ...props.violation,
        date: moment(props.violation.date).toDate(),
        expiry: moment(props.violation.expiry).toDate()
      });
    }
  }, [props.violation]);

  return (
    <ContainerStyled>
      <FormContainer>
        <TabContainer>
          <TabView active={tab === 1} onClick={() => setTab(1)}>
            DADOS
          </TabView>
          {/* <TabView active={tab === 2} onClick={() => setTab(2)}>
            RELATRIOS
          </TabView> */}
        </TabContainer>
        {tab === 1 ? (
          <form onSubmit={formik.handleSubmit}>
            <FormData>
              <DataContainer>
                <ContainerTitle>INFRAES</ContainerTitle>
                <ProfileContainer>
                  <Row>
                    <FieldContainer>
                      {(props.match.params.id && formik.values.tracker_id) ||
                      !props.match.params.id ? (
                        <SelectTracker
                          defaultValue={formik.values.tracker_id}
                          parentCallback={option =>
                            formik.setFieldValue("tracker_id", option.id)
                          }
                          error={
                            formik.touched.tracker_id &&
                            formik.errors.tracker_id
                          }
                        />
                      ) : (
                        <></>
                      )}
                    </FieldContainer>
                    <FieldContainer>
                      {(props.match.params.id && formik.values.driver_id) ||
                      !props.match.params.id ? (
                        <SelectDriver
                          defaultValue={formik.values.driver_id}
                          parentCallback={option =>
                            formik.setFieldValue("driver_id", option.id)
                          }
                          error={
                            formik.touched.driver_id && formik.errors.driver_id
                          }
                        />
                      ) : (
                        <></>
                      )}
                    </FieldContainer>
                    <FieldContainer>
                      <div>
                        <LabelStyled>Gravidade</LabelStyled>
                        {formik.touched.level && formik.errors.level ? (
                          <ErrorMessage>{formik.errors.level}</ErrorMessage>
                        ) : (
                          ""
                        )}
                      </div>
                      {(props.match.params.id && formik.values.level) ||
                      !props.match.params.id ? (
                        <SelectStyled
                          options={FineLevel}
                          placeholder="Selecione a gravidade da violao"
                          noOptionsMessage={() => "Nenhum gravidade encontrada"}
                          onChange={option =>
                            formik.setFieldValue("level", option.label)
                          }
                          defaultValue={
                            FineLevel[
                              FineLevel.findIndex(
                                item => item.label === formik.values.level
                              )
                            ]
                          }
                          error={formik.touched.level && formik.errors.level}
                        />
                      ) : (
                        <></>
                      )}
                    </FieldContainer>
                  </Row>
                  <Row>
                    <FieldContainer>
                      <div>
                        <LabelStyled>Valor</LabelStyled>
                        {formik.touched.price && formik.errors.price ? (
                          <ErrorMessage>{formik.errors.price}</ErrorMessage>
                        ) : (
                          <></>
                        )}
                      </div>
                      <div>
                        <MoneyDiv
                          error={formik.touched.price && formik.errors.price}
                        >
                          R$
                        </MoneyDiv>
                        <MaskMoneyStyle
                          type="number"
                          onChange={formik.handleChange}
                          value={formik.values.price}
                          id="price"
                          name="price"
                          error={formik.touched.price && formik.errors.price}
                        />
                      </div>
                    </FieldContainer>
                    <FieldContainer>
                      <div>
                        <LabelStyled>Data da Multa</LabelStyled>
                        {formik.touched.date && formik.errors.date ? (
                          <ErrorMessage>{formik.errors.date}</ErrorMessage>
                        ) : (
                          ""
                        )}
                      </div>
                      <DateStyle
                        dateFormat="dd/MM/yyyy"
                        selected={formik.values.date}
                        onChange={value =>
                          formik.setFieldValue("date", moment(value).toDate())
                        }
                        locale={pt}
                        customInput={<InputMask mask={"99/99/9999"} />}
                        error={formik.touched.date && formik.errors.date}
                      />
                    </FieldContainer>
                    <FieldContainer>
                      <div>
                        <LabelStyled>Data de Vencimento</LabelStyled>
                        {formik.touched.expiry && formik.errors.expiry ? (
                          <ErrorMessage>{formik.errors.expiry}</ErrorMessage>
                        ) : (
                          ""
                        )}
                      </div>
                      <DateStyle
                        dateFormat="dd/MM/yyyy"
                        selected={formik.values.expiry}
                        onChange={value =>
                          formik.setFieldValue("expiry", moment(value).toDate())
                        }
                        locale={pt}
                        customInput={<InputMask mask={"99/99/9999"} />}
                        error={formik.touched.expiry && formik.errors.expiry}
                      />
                    </FieldContainer>
                  </Row>
                  <DoubleRow>
                    <FieldContainer>
                      <div>
                        <LabelStyled>Motivo</LabelStyled>
                        {formik.touched.reason && formik.errors.reason ? (
                          <ErrorMessage>{formik.errors.reason}</ErrorMessage>
                        ) : (
                          ""
                        )}
                      </div>
                      {(props.match.params.id && formik.values.reason) ||
                      !props.match.params.id ? (
                        <SelectStyled
                          options={FineList}
                          getOptionLabel={option => option.value}
                          placeholder="Selecione o motivo da violao"
                          noOptionsMessage={() => "Nenhuma opo encontrada"}
                          onChange={option =>
                            formik.setFieldValue("reason", option.value)
                          }
                          defaultValue={
                            FineList[
                              FineList.findIndex(
                                item => item.value === formik.values.reason
                              )
                            ]
                          }
                          error={formik.touched.reason && formik.errors.reason}
                        />
                      ) : (
                        <></>
                      )}
                    </FieldContainer>
                  </DoubleRow>
                  <NoRow>
                    <DescContainer full>
                      <div>
                        <LabelStyled>Descrio</LabelStyled>
                        {formik.touched.description &&
                        formik.errors.description ? (
                          <ErrorMessage>
                            {formik.errors.description}
                          </ErrorMessage>
                        ) : (
                          ""
                        )}
                      </div>
                      <TextAreaStyled
                        rows="3"
                        name="description"
                        onChange={formik.handleChange}
                        value={formik.values.description}
                        error={
                          formik.touched.description &&
                          formik.errors.description
                        }
                      />
                    </DescContainer>
                  </NoRow>
                </ProfileContainer>
              </DataContainer>
              <ButtonsContainer>
                <Button
                  onClick={() => cancelButton()}
                  type="button"
                  color="#E33939"
                >
                  Cancelar
                </Button>
                <Button type="submit" color="#555">
                  Salvar
                </Button>
              </ButtonsContainer>
            </FormData>
          </form>
        ) : (
          <FormDocs>RELATRIOS</FormDocs>
        )}
      </FormContainer>
    </ContainerStyled>
  );
}
const mapStateToProps = state => ({
  violation: state.violationState.violation,
  loading: state.violationState.loading
});

const mapDispatchToProps = dispatch =>
  bindActionCreators(ViolationActions, dispatch);

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Forms);
