import React, { useEffect, useState } from "react";
import {
  FieldContainer,
  ContainerFilters,
  HeaderFilter,
  ButtonsFilter,
  BodyFilter,
  FilterButton
} from "../Global";
import { Creators as ViolationActions } from "../../store/ducks/violationSauce";
import { useFormik } from "formik";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { mountParams } from "../util/Mountparams";
import SelectDriver from "../Global/SelectDriver";
import SelectTracker from "../Global/SelectTracker";

function ViolationFilter(props) {
  const formik = useFormik({
    initialValues: {
      tracker_id: "",
      driver_id: ""
      // cars: [
      //   { status: false, name: "carro1" },
      //   { status: false, name: "carro1" }
      // ]
    },
    validate: values => {
      const err = {};
      return err;
    },
    onSubmit: data => {
      console.log(data);
      const query = mountParams(data);
      props.fetchViolationRequest(query);
    }
  });

  function clearButton() {
    formik.handleReset();
    props.fetchViolationRequest();
  }

  return (
    <ContainerFilters>
      <form onSubmit={formik.handleSubmit}>
        <HeaderFilter>INFRAES</HeaderFilter>
        <BodyFilter>
          <FieldContainer>
            <SelectTracker
              parentCallback={option =>
                formik.setFieldValue("tracker_id", option.id)
              }
            />
          </FieldContainer>
          <FieldContainer>
            <SelectDriver
              parentCallback={option =>
                formik.setFieldValue("driver_id", option.id)
              }
            />
          </FieldContainer>
          {/* {formik.values.cars.map((car, index) => {
            return (
              <FieldContainer>
                <input
                  type="checkbox"
                  // id={index}
                  name={`cars[${index}].status`}
                  // checked={car.checked}
                  onChange={formik.handleChange}
                  value={formik.values.cars[index].status}
                  // {...i}
                /> */}
          {/* {car.name} */}
          {/* </FieldContainer> */}
          {/* ); })} */}
        </BodyFilter>
        <ButtonsFilter>
          <FilterButton
            front="#000000"
            back="#bfbfbf"
            onClick={() => clearButton()}
            type="button"
          >
            Limpar
          </FilterButton>
          <FilterButton
            color="#737373"
            onClick={() => props.history.push("/violation/form")}
            type="button"
          >
            Novo
          </FilterButton>
          <FilterButton color="#4A6971" type="submit">
            Pesquisar
          </FilterButton>
        </ButtonsFilter>
      </form>
    </ContainerFilters>
  );
}

const mapDispatchToProps = dispatch =>
  bindActionCreators(ViolationActions, dispatch);

export default connect(
  null,
  mapDispatchToProps
)(ViolationFilter);
