import React, { useState } from "react";

import {
  ChartContainer,
  ChartPages,
  ChartDetail,
  PageContainer
} from "./styles";
import ChartSpeed from "./chartSpeed";
export default function Dashboard() {
  const [tab, setTab] = useState(1);
  return (
    <ChartContainer>
      <ChartPages>
        <PageContainer active={tab === 1} onClick={() => setTab(1)}>
          Velocidade
        </PageContainer>
        <PageContainer active={tab === 2} onClick={() => setTab(2)}>
          Ociosidade
        </PageContainer>
        <PageContainer active={tab === 3} onClick={() => setTab(3)}>
          Velocidade
        </PageContainer>
      </ChartPages>
      <ChartDetail>{tab === 1 && <ChartSpeed />}</ChartDetail>
    </ChartContainer>
  );
}
