import React, { useEffect, useState } from "react";
import _ from "lodash";
import { HorizontalBarResize } from "../Global";
const axios = require("axios");

function TrackerOffTimeChart(props) {
  const [trackerOffTime, setTrackerOffTime] = useState({});
  const [labelName, setLabelName] = useState([]);
  const [time, setTime] = useState([]);

  function getDriverViolation() {
    axios
      .get("http://localhost:3001/trackerOffTime")
      .then(resp => {
        setTrackerOffTime(resp.data);
      })
      .catch(error => {
        console.log(error);
      });
  }

  useEffect(() => {
    getDriverViolation();
  }, []);

  useEffect(() => {
    if (trackerOffTime.length > 0) {
      setLabelName(trackerOffTime.map(item => item.plate));
      setTime(trackerOffTime.map(item => item.time));
    }
  }, [trackerOffTime]);

  const data = {
    labels: labelName,
    datasets: [
      {
        label: "Tempo Parado por Veculo",
        backgroundColor: "rgba(75,192,192,0.4)",
        borderColor: "rgba(75,192,192,1)",
        borderWidth: 1,
        hoverBackgroundColor: "rgba(75,192,192,1)",
        hoverBorderColor: "rgba(220,220,220,1)",
        data: time
      }
    ]
  };

  return (
    <>
      {trackerOffTime !== undefined ? (
        <HorizontalBarResize
          width={null}
          height={300}
          options={{ maintainAspectRatio: false, responsive: false }}
          // options={{ maintainAspectRatio: 1 }}
          data={data}
        />
      ) : (
        ""
      )}
    </>
  );
}

export default TrackerOffTimeChart;
