import React, { useEffect, useState } from "react";
import api from "../../../services/api";
import { SelectStyled, LabelStyled, ErrorMessage } from "../index";

export default function SelectDriver(props) {
  const [options, setOptions] = useState([]);
  const [loading, setLoading] = useState(false);

  async function getDrivers() {
    const response = await api.fetchDriver();
    setOptions(response.data);
    setLoading(true);
  }

  useEffect(() => {
    getDrivers();
  }, []);

  return (
    <>
      <div>
        <LabelStyled>MOTORISTA</LabelStyled>
        {props.error ? <ErrorMessage>Campo obrigatrio</ErrorMessage> : <></>}
      </div>
      {loading ? (
        <SelectStyled
          onChange={option => props.parentCallback(option)}
          getOptionValue={option => option.id}
          getOptionLabel={option => option.name}
          defaultValue={
            options[options.findIndex(item => item.id === props.defaultValue)]
          }
          options={options}
          placeholder="Selecione o motorista"
          error={props.error}
        />
      ) : (
        <></>
      )}
    </>
  );
}
