import { createActions, createReducer } from "reduxsauce";

export const { Types, Creators } = createActions({
  bufferRequest: ["id"],
  bufferSuccess: ["data", "groups"],
  bufferFailure: [],
  setViewGroup: [],
  setViewModal: [],
  updateBuffer: ["data"],
  filterBuffer: ["filter"],
  setDrawingMode: []
});

// console.log(Types);
// console.log(Creators);

const INITIAL_STATE = {
  loading: false,
  buffer: [],
  bufferImutable: [],
  viewGroup: false,
  viewModal: false,
  groups: [],
  drawingMode: false,
  filterActive: "ALL"
};

const requestBuffer = (state = INITIAL_STATE, action) => ({
  ...state,
  loading: true
});

const successBuffer = (state = INITIAL_STATE, action) => ({
  ...state,
  buffer: action.data,
  bufferImutable: action.data,
  groups: action.groups,
  loading: false
});

const failureBuffer = (state = INITIAL_STATE, action) => ({
  ...state,
  loading: false,
  buffer: [],
  bufferImutable: []
});

const changeDrawingMode = (state = INITIAL_STATE, action) => {
  console.log(action, "sauce");
  return {
    ...state,
    drawingMode: !state.drawingMode
  };
};

const changeView = (state = INITIAL_STATE, action) => ({
  ...state,
  viewGroup: !state.viewGroup
});

const changeModal = (state = INITIAL_STATE, action) => ({
  ...state,
  viewModal: !state.viewModal
});

const changeBuffer = (state = INITIAL_STATE, action) => ({
  ...state,
  buffer: state.buffer.map(item => {
    if (item.tracker_id == action.data.tracker_id) {
      return action.data;
    }
    return item;
  }),
  bufferImutable: state.bufferImutable.map(item => {
    if (item.tracker_id == action.data.tracker_id) {
      return action.data;
    }
    return item;
  })
});

const filterBuffer = (state = INITIAL_STATE, action) => ({
  ...state,
  filterActive: action.filter,
  buffer:
    action.filter === "ALL"
      ? state.bufferImutable
      : state.bufferImutable.filter(item => action.filter === item.status)
});

export default createReducer(INITIAL_STATE, {
  [Types.BUFFER_REQUEST]: requestBuffer,
  [Types.BUFFER_SUCCESS]: successBuffer,
  [Types.BUFFER_FAILURE]: failureBuffer,
  [Types.SET_VIEW_GROUP]: changeView,
  [Types.SET_VIEW_MODAL]: changeModal,
  [Types.SET_DRAWING_MODE]: changeDrawingMode,
  [Types.UPDATE_BUFFER]: changeBuffer,
  [Types.FILTER_BUFFER]: filterBuffer
});
