import React from "react";
import { connect } from "react-redux";
import { Creators as TrackerActions } from "../../store/ducks/trackerSauce";
import { bindActionCreators } from "redux";
import { Img, BoldMessage, Message, ImgContainer } from "./styles";
import image from "../../assets/report_image_n1.svg";

function EmptyState() {
  return (
    <>
      <ImgContainer>
        <Img src={image} />
        <BoldMessage>Nenhum relatrio selecionado!</BoldMessage>
        <Message>
          Escolha o tipo de relatrio, preencha os campos e clique em visualizar
          para preparar o relatrio!
        </Message>
      </ImgContainer>
    </>
  );
}

const mapStateToProps = state => ({
  trackers: state.trackerState.trackers,
  loading: state.trackerState.loading
});

const mapDispatchToProps = dispatch =>
  bindActionCreators(TrackerActions, dispatch);

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(EmptyState);
