import { createActions, createReducer } from "reduxsauce";

export const { Types, Creators } = createActions({
  fetchDriverRequest: ["query"],
  fetchDriverSuccess: ["data"],
  fetchDriverIdSuccess: ["data"],
  fetchDriverFailure: [],
  updateDriverRequest: ["data"],
  deleteDriverRequest: ["id"],
  createDriverRequest: ["data"],
  clearDriver: []
});

const INITIAL_STATE = {
  drivers: [],
  driver: {},
  driverByTracker: {},
  loading: false
};

const clearDriver = (state = INITIAL_STATE, actions) => ({
  ...state,
  driver: {}
});

const fetchDriverRequest = (state = INITIAL_STATE, actions) => ({
  ...state,
  loading: true
});

const fetchDriverSuccess = (state = INITIAL_STATE, actions) => ({
  ...state,
  drivers: actions.data,
  loading: false
});

const fetchDriverIdSuccess = (state = INITIAL_STATE, actions) => {
  return {
    ...state,
    driver: actions.data,
    loading: false
  };
};

const fetchDriverFailure = (state = INITIAL_STATE, actions) => {
  return {
    ...state,
    loading: false,
    drivers: []
  };
};

const createDriverRequest = (state = INITIAL_STATE, actions) => ({
  ...state,
  loading: true
});

const updateDriverRequest = (state = INITIAL_STATE, actions) => ({
  ...state,
  loading: true
});

const deleteDriverRequest = (state = INITIAL_STATE, actions) => ({});

export default createReducer(INITIAL_STATE, {
  [Types.FETCH_DRIVER_REQUEST]: fetchDriverRequest,
  [Types.FETCH_DRIVER_SUCCESS]: fetchDriverSuccess,
  [Types.FETCH_DRIVER_ID_SUCCESS]: fetchDriverIdSuccess,
  [Types.FETCH_DRIVER_FAILURE]: fetchDriverFailure,
  [Types.UPDATE_DRIVER_REQUEST]: updateDriverRequest,
  [Types.DELETE_DRIVER_REQUEST]: deleteDriverRequest,
  [Types.CREATE_DRIVER_REQUEST]: createDriverRequest,
  [Types.CLEAR_DRIVER]: clearDriver
});
