import React from "react";
import "react-perfect-scrollbar/dist/css/styles.css";

import {
  Table,
  TableHeader,
  TableBody,
  TableFooter,
  Th,
  Tr,
  Td,
  TableContainer,
  Scrollbar
} from "./styles";
import _ from "lodash";
import Loading from "../Loading";
export default function TableComponent(props) {
  function getData(data) {
    props.changeValue(data);
  }
  return (
    <Scrollbar>
      <TableContainer>
        <Table>
          <TableHeader>
            <tr>
              {props.header.map((item, index) => (
                <Th
                  scope="col"
                  key={item}
                  onClick={() => console.log(props.labelsNames[index])}
                >
                  {item}
                </Th>
              ))}
            </tr>
          </TableHeader>
          <TableBody>
            {props.loading ? (
              <Tr>
                <Td colSpan={props.header.length} noData>
                  <Loading />
                </Td>
              </Tr>
            ) : _.isEmpty(props.data) ? (
              <Tr>
                <Td colSpan={props.header.length} noData>
                  Sem dados para exibir
                </Td>
              </Tr>
            ) : (
              props.data.map((item, index) => {
                return (
                  <Tr key={index} onClick={() => getData(item)}>
                    {props.labelsNames.map((teste, index) => {
                      return (
                        <Td key={index} data-label={props.header[index]}>
                          {item[teste] ? item[teste].toString() : ""}
                        </Td>
                      );
                    })}
                  </Tr>
                );
              })
            )}
          </TableBody>
          <TableFooter></TableFooter>
        </Table>
      </TableContainer>
    </Scrollbar>
  );
}

