import React, { useEffect } from "react";
import styled from "styled-components";
import espiritoSanto from "../../assets/flags/espirito-santo.svg";
import parking from "../../assets/carPanel/noun_Parking_2672865.svg";
import speedometer from "../../assets/carPanel/noun_Speedometer_2171226.svg";
import noSignal from "../../assets/carPanel/noun_no signal_2452207.svg";
import Moment from "react-moment";

const CarContainer = styled.div`
  background: #f2f1f0;
  margin: 2px 0px 2px 0px;
  color: #333;
  cursor: pointer;
  display: grid;
  grid-template:
    "vehicle drive" 1fr
    / 1fr 1fr;
`;

const Vehicle = styled.div`
  grid-area: vehicle;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 5px;
`;

const Drive = styled.div`
  grid-area: drive;
  display: grid;
  grid-template:
    "monitor" 1fr
    / 1fr;
  background: #fafafa;
  font-size: 13px;
  padding-right: 2px;

  img {
    height: 35px;
  }
`;

const Label = styled.div`
  font-size: 13px;
  font-weight: bold;

  img {
    height: 15px;
    margin-right: 5px;
  }
`;

const Driver = styled.div`
  font-size: 12px;
`;

const Triangle = styled.div`
  width: 0;
  height: 0;
  border-top: 10px solid #666;
  border-right: 10px solid transparent;
  position: absolute;
`;

const MonitorContainer = styled.div`
  grid-area: monitor;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
`;

const Disconnected = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-size: 12px;

  img {
    max-height: 30px;
  }
`;

export default function CarItem(props) {
  return (
    <CarContainer
      onClick={() => props.history.push(`/monitoring?id=${props.tracker_id}`)}
    >
      <Triangle />
      <Vehicle>
        <img
          src={`https://rastreador.el.com.br/img/monitor/truck_cacamba.svg`}
          height="20"
          alt=""
        />
        <Label>{props.label}</Label>
        <Driver>{props.driver ? props.driver : "No identificado"}</Driver>
      </Vehicle>
      <Drive>
        <MonitorContainer>
          {props.status != "DISCONNECTED" ? (
            props.ignition ? (
              <>
                <img src={speedometer} alt="" />
                {parseInt(props.speed)} km/h
              </>
            ) : (
              <>
                <img src={parking} alt="" />
                <Moment add={{ hours: 3 }} durationFromNow>
                  {props.last_time_on}
                </Moment>
              </>
            )
          ) : (
            <Disconnected>
              <img src={noSignal} alt="" />
              <Moment add={{ hours: 3 }} durationFromNow>
                {props.date}
              </Moment>
            </Disconnected>
          )}
        </MonitorContainer>
      </Drive>
    </CarContainer>
  );
}
