import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import MeasureTool from "measuretool-googlemaps-v3/lib/MeasureTool.min.js";
import { geoJson } from "./geojson";
import { initControls } from "./controlsInit";
import { iconMarker } from "./marker";
import { ContainerStyled, InputPlaces } from "./styles";
import markerStop from "../../assets/markerStop.svg";
import alert from "../../assets/alert.svg";
import markerRun from "../../assets/markerRun.svg";
import markerAlert from "../../assets/markerAlert.svg";
import "googlemaps-v3-utility-library/richmarker/src/richmarker-compiled";
import "googlemaps-v3-utility-library/infobox/src/infobox.es6";
import mapStyle from "../../assets/map.json";
import Modal from "./Modal";
import { Creators as BufferActions } from "../../store/ducks/bufferSauce";
import { bindActionCreators } from "redux";
import { saveZone, cancelZone } from "./controls";

function Map(props) {
  const [map, setMap] = useState();
  const [initial, setInitial] = useState(true);
  const [marker, setMarker] = useState([]);
  const [typeModal, setTypeModal] = useState("");
  const [GeoJson, setGeoJson] = useState("");
  const [hideSearchBox, setHideSearchBox] = useState(false);
  const [measureTool, setMeasureTool] = useState("");

  useEffect(() => {
    const map = new window.google.maps.Map(document.getElementById("map"), {
      center: { lat: -20.361797, lng: -40.660631 },
      fullscreenControl: false,
      mapTypeControl: false,
      zoomControl: true,
      scaleControl: true,
      streetViewControl: true,
      streetViewControlOptions: {
        position: window.google.maps.ControlPosition.RIGHT_BOTTOM
      },
      zoom: 15,
      controlSize: 25,
      styles: mapStyle
    });
    setMeasureTool(
      new MeasureTool(map, {
        showSegmentLength: true,
        contextMenu: false,
        tooltip: true,
        unit: MeasureTool.UnitTypeId.METRIC
      })
    );

    // Include measure tools options
    initControls(map, measureTool);

    var input = document.getElementById("pac-input");
    var searchBox = new window.google.maps.places.SearchBox(input);
    map.controls[window.google.maps.ControlPosition.TOP_RIGHT].push(input);

    map.addListener("bounds_changed", function() {
      searchBox.setBounds(map.getBounds());
    });

    var markers = [];
    searchBox.addListener("places_changed", function() {
      var places = searchBox.getPlaces();

      if (places.length === 0) {
        return;
      }

      // Clear out the old markers.
      markers.forEach(function(marker) {
        marker.setMap(null);
      });
      markers = [];

      // For each place, get the icon, name and location.
      var bounds = new window.google.maps.LatLngBounds();
      places.forEach(function(place) {
        if (!place.geometry) {
          console.log("Returned place contains no geometry");
          return;
        }
        var icon = {
          url: place.icon,
          size: new window.google.maps.Size(71, 71),
          origin: new window.google.maps.Point(0, 0),
          anchor: new window.google.maps.Point(17, 34),
          scaledSize: new window.google.maps.Size(25, 25)
        };

        // Create a marker for each place.
        markers.push(
          new window.google.maps.Marker({
            map: map,
            icon: icon,
            title: place.name,
            position: place.geometry.location
          })
        );

        // eslint-disable-next-line no-undef
        new MarkerClusterer(map, markers, {
          imagePath:
            "https://github.com/googlemaps/v3-utility-library/blob/master/markerclusterer/images/m1.png"
        });

        if (place.geometry.viewport) {
          bounds.union(place.geometry.viewport);
        } else {
          bounds.extend(place.geometry.location);
        }
      });
      map.fitBounds(bounds);
    });

    let drawingManager = new window.google.maps.drawing.DrawingManager({
      drawingControl: true,
      circleOptions: {
        editable: true
      },
      polygonOptions: {
        editable: true
      },
      rectangleOptions: {
        editable: true
      },
      drawingControlOptions: {
        position: window.google.maps.ControlPosition.RIGHT_TOP,
        drawingModes: ["marker", "polygon", "rectangle"]
      }
    });

    drawingManager.setMap(map);

    window.google.maps.event.addListener(
      drawingManager,
      "overlaycomplete",
      function(event) {
        drawingManager.setDrawingMode(null);
        setGeoJson(geoJson(event));
        setTypeModal(event.type);
        if (event.type === "circle") {
          window.google.maps.event.addListener(
            event.overlay,
            "radius_changed",
            function() {
              setGeoJson(geoJson(event));
            }
          );
        } else if (event.type === "polygon") {
          window.google.maps.event.addListener(
            event.overlay.getPath(),
            "set_at",
            function() {
              setGeoJson(geoJson(event));
            }
          );

          window.google.maps.event.addListener(
            event.overlay.getPath(),
            "insert_at",
            function() {
              setGeoJson(geoJson(event));
            }
          );
        } else if (event.type === "rectangle") {
          window.google.maps.event.addListener(
            event.overlay,
            "bounds_changed",
            function() {
              setGeoJson(geoJson(event));
            }
          );
        }
        // Change Drawing Mode
        props.setDrawingMode();

        // To hide drawing tools control
        drawingManager.setOptions({
          drawingControl: false
        });
        // Remove measure tools options
        map.controls[9].clear();
        console.log("removeu");

        // Create DIV for save and cancel buttons
        const zoneControlDiv = document.createElement("div");
        var count = zoneControlDiv.getElementsByTagName("div").length;
        if (count === 4) {
          map.controls[window.google.maps.ControlPosition.RIGHT_TOP].removeAt(
            1
          );
          // Destroy the old zoneControlDiv and create a new one if there is more than 2 buttons(save and cancel)
          const zoneControlDiv = document.createElement("div");
        }

        new saveZone(zoneControlDiv, map, props, event.overlay, drawingManager);
        new cancelZone(
          zoneControlDiv,
          map,
          props,
          event.overlay,
          drawingManager
        );
        zoneControlDiv.index = 1;
        map.controls[window.google.maps.ControlPosition.RIGHT_TOP].push(
          zoneControlDiv
        );
      }
    );

    setMap(map);
  }, []);

  useEffect(() => {
    marker.map(item => {
      item.setMap(null);
    });
    const point = [];
    const bounds = new window.google.maps.LatLngBounds();
    props.buffer.map(item => {
      const Icon =
        item.status === "ON"
          ? markerRun
          : item.status === "DANGER"
          ? markerAlert
          : item.status === "OFF"
          ? markerStop
          : markerStop;

      const pointer = new window.RichMarker({
        position: new window.google.maps.LatLng(item.lat, item.lng),
        map: map,
        draggable: false,
        content: iconMarker(Icon, item.angle, alert),
        flat: true,
        anchor: window.RichMarkerPosition.MIDDLE
      });
      point.push(pointer);
      bounds.extend(pointer.getPosition());
      if (map && initial) {
        map.fitBounds(bounds);
        setInitial(false);
      }
    });
    setMarker(point);
  }, [map, props.buffer]);

  useEffect(() => {
    setHideSearchBox(props.drawingMode);
    if (map !== undefined) {
      if (props.drawingMode) {
        map.controls[9].clear(); // Remove ruller tools
        map.controls[6].clear(); // Remove small map in the left
      } else {
        initControls(map, measureTool);
      }
    }
  }, [props.drawingMode]);

  return (
    <>
      <Modal type={typeModal} geoJson={GeoJson} />
      <ContainerStyled>
        <InputPlaces
          id="pac-input"
          type="text"
          placeholder="Pesquise o local"
          hide={hideSearchBox}
        />
        <div id="map"></div>
      </ContainerStyled>
    </>
  );
}

const mapStateToProps = state => ({
  buffer: state.bufferState.buffer,
  drawingMode: state.bufferState.drawingMode
});
const mapDispatchToProps = dispatch =>
  bindActionCreators(BufferActions, dispatch);
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Map);
