import { createActions, createReducer } from "reduxsauce";
export const { Types, Creators } = createActions({
  fetchFenceRequest: ["query"],
  fetchFenceSuccess: ["data"],
  fetchFenceIdSuccess: ["data"],
  fetchFenceFailure: [],
  updateFenceRequest: ["data"],
  deleteFenceRequest: ["id"],
  createFenceRequest: ["data"],
  clearFence: []
});

const INITIAL_STATE = {
  fences: [],
  fence: {},
  fenceByTracker: {},
  loading: false
};

const clearFence = (state = INITIAL_STATE, actions) => ({
  ...state,
  fence: {}
});

const fetchFenceRequest = (state = INITIAL_STATE, actions) => {
  console.log(actions);
  return {
    ...state,
    loading: true
  };
};

const fetchFenceSuccess = (state = INITIAL_STATE, actions) => {
  console.log("scces");
  return {
    ...state,
    fences: actions.data,
    loading: false
  };
};

const fetchFenceIdSuccess = (state = INITIAL_STATE, actions) => {
  return {
    ...state,
    fence: actions.data,
    loading: false
  };
};

const fetchFenceFailure = (state = INITIAL_STATE, actions) => ({
  ...state,
  loading: false,
  fences: []
});

const createFenceRequest = (state = INITIAL_STATE, actions) => ({
  ...state,
  loading: true
});

const updateFenceRequest = (state = INITIAL_STATE, actions) => ({
  ...state,
  loading: true
});

const deleteFenceRequest = (state = INITIAL_STATE, actions) => ({});
export default createReducer(INITIAL_STATE, {
  [Types.FETCH_FENCE_REQUEST]: fetchFenceRequest,
  [Types.FETCH_FENCE_SUCCESS]: fetchFenceSuccess,
  [Types.FETCH_FENCE_ID_SUCCESS]: fetchFenceIdSuccess,
  [Types.FETCH_FENCE_FAILURE]: fetchFenceFailure,
  [Types.UPDATE_FENCE_REQUEST]: updateFenceRequest,
  [Types.DELETE_FENCE_REQUEST]: deleteFenceRequest,
  [Types.CREATE_FENCE_REQUEST]: createFenceRequest,
  [Types.CLEAR_FENCE]: clearFence
});
