const mapStateToProps = state => ({
  commands: state.commandState.commands,
  loading: state.commandState.loading,
  loadingButton: state.commandState.loadingButton
});

const mapDispatchToProps = dispatch =>
  bindActionCreators(CommandActions, dispatch);

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Command);
