import React, { useEffect, useState } from "react";
import _ from "lodash";
import { HorizontalBarResize } from "../Global";
const axios = require("axios");

function DriverInfractionChart(props) {
  const [driverViolation, setDriverViolation] = useState({});
  const [labelName, setLabelName] = useState([]);
  const [infractionNumber, setInfractionNumber] = useState([]);

  function getDriverViolation() {
    axios
      .get("http://localhost:3001/driversInfraction")
      .then(resp => {
        setDriverViolation(resp.data);
      })
      .catch(error => {
        console.log(error);
      });
  }

  useEffect(() => {
    getDriverViolation();
  }, []);

  useEffect(() => {
    console.log(driverViolation, "driverViolation");
    if (driverViolation.length > 0) {
      console.log("teste");
      setLabelName(driverViolation.map(item => item.name));
      setInfractionNumber(driverViolation.map(item => item.infractions));
    }
    console.log(labelName, "peopleArray");
  }, [driverViolation]);

  const data = {
    labels: labelName,
    datasets: [
      {
        label: "Nmero de Infraes por Motorista",
        backgroundColor: "rgba(75,192,192,0.4)",
        borderColor: "rgba(75,192,192,1)",
        borderWidth: 1,
        hoverBackgroundColor: "rgba(75,192,192,1)",
        hoverBorderColor: "rgba(220,220,220,1)",
        data: infractionNumber,
        borderWidth: 1
      }
    ]
  };

  return (
    <>
      {driverViolation !== undefined ? (
        <HorizontalBarResize
          width={null}
          height={300}
          options={{ maintainAspectRatio: false, responsive: false }}
          // options={{ maintainAspectRatio: 1 }}
          data={data}
        />
      ) : (
        ""
      )}
    </>
  );
}

export default DriverInfractionChart;
