import React, { useEffect, useState } from "react";
import mapStyle from "../../assets/map.json";
import { Map } from "./styles";
import { connect } from "react-redux";
import { iconMarker } from "../Map/marker";
import markerStop from "../../assets/markerStop.svg";
import alert from "../../assets/alert.svg";
import markerRun from "../../assets/markerRun.svg";
import markerAlert from "../../assets/markerAlert.svg";
import { useLocation } from "react-router-dom";
import _ from "lodash";
function useQuery() {
  return new URLSearchParams(useLocation().search);
}
function Dashboard(props) {
  let query = useQuery();
  const id_tracker = query.get("id");
  const [map, setMap] = useState();
  const [marker, setMarker] = useState([]);
  const [data, setData] = useState({});

  function drawMarker() {
    if (!_.isEmpty(data)) {
      marker.map(item => {
        item.setMap(null);
      });
      const point = [];
      const Icon =
        data.status === "ON"
          ? markerRun
          : data.status === "DANGER"
          ? markerAlert
          : data.status === "OFF"
          ? markerStop
          : markerStop;
      const pointer = new window.RichMarker({
        position: new window.google.maps.LatLng(data.lat, data.lng),
        map: map,
        draggable: false,
        content: iconMarker(Icon, data.angle, alert),
        flat: true,
        anchor: window.RichMarkerPosition.MIDDLE
      });
      point.push(pointer);
      setMarker(point);
      map.setCenter(new window.google.maps.LatLng(data.lat, data.lng));
    }
  }
  useEffect(() => {
    const map = new window.google.maps.Map(document.getElementById("map"), {
      center: { lat: -20.361797, lng: -40.660631 },
      fullscreenControl: false,
      mapTypeControl: false,
      zoomControl: false,
      scaleControl: false,
      streetViewControl: false,
      streetViewControlOptions: {
        position: window.google.maps.ControlPosition.RIGHT_BOTTOM
      },
      zoom: 13,
      controlSize: 25,
      styles: mapStyle
    });
    setMap(map);
  }, []);

  useEffect(() => {
    if (props.buffer.length !== 0) {
      setData(props.buffer.find(item => item.tracker_id == id_tracker));
    }
  }, [props.buffer]);

  useEffect(() => {
    drawMarker();
  }, [data]);

  useEffect(() => {
    setData(props.buffer.find(item => item.tracker_id == id_tracker));
  }, [id_tracker]);

  return <Map id="map"></Map>;
}

const mapStateToProps = state => ({
  buffer: state.bufferState.buffer
});

export default connect(mapStateToProps)(Dashboard);
