import api from "../../services/api";
import { toast } from "react-toastify";

export function saveZone(controlDiv, map, props, event, coords) {
  let controlUI = document.createElement("div");
  controlUI.style.backgroundColor = "#2C8D14";
  controlUI.style.borderRadius = "3px";
  controlUI.style.boxShadow = "0 2px 6px rgba(0,0,0,.1)";
  controlUI.style.cursor = "pointer";
  controlUI.style.height = "25px";
  controlUI.style.width = "96px";
  controlUI.style.marginRight = "6px";
  controlUI.style.marginBottom = "6px";
  controlDiv.appendChild(controlUI);

  let controlText = document.createElement("span");
  controlText.innerHTML = "SALVAR";
  controlText.style.fontWeight = 600;
  controlText.style.color = "#fff";
  controlText.style.display = "flex";
  controlText.style.justifyContent = "center";
  controlText.style.alignItems = "center";
  controlText.style.height = "100%";
  controlText.style.fontSize = "14px";

  controlUI.appendChild(controlText);

  controlUI.addEventListener("click", function() {
    //converting to geojson
    let GeoJson = {
      type: "Feature",
      geometry: {
        coordinates: [[]]
      },
      properties: {}
    };

    GeoJson.geometry.type = props.fence.geojson.geometry.type;
    coords.map((item, index) => {
      GeoJson.geometry.coordinates[0].push([item[1], item[0]]);
    });

    const data = {
      id: props.fence.id,
      name: props.fence.name,
      geojson: GeoJson
    };

    try {
      api.updateGeometries(data);
      toast.success("Cerca alterada com sucesso!");
    } catch (error) {
      toast.error("Ocorreu um erro");
    }
  });
}

export function cancelZone(controlDiv, map, props, polygon, geoPaths) {
  let controlUI = document.createElement("div");
  controlUI.style.backgroundColor = "#ff0000";
  controlUI.style.borderRadius = "3px";
  controlUI.style.boxShadow = "0 2px 6px rgba(0,0,0,.1)";
  controlUI.style.cursor = "pointer";
  controlUI.style.height = "25px";
  controlUI.style.width = "96px";
  controlUI.style.marginRight = "6px";
  controlUI.style.marginBottom = "6px";
  controlDiv.appendChild(controlUI);

  let controlText = document.createElement("span");
  controlText.innerHTML = "CANCELAR";
  controlText.style.fontWeight = 600;
  controlText.style.color = "#fff";
  controlText.style.display = "flex";
  controlText.style.justifyContent = "center";
  controlText.style.alignItems = "center";
  controlText.style.height = "100%";
  controlText.style.fontSize = "14px";

  controlUI.appendChild(controlText);

  controlUI.addEventListener("click", function() {
    polygon.setPath(geoPaths);
  });
}
