import React, { useEffect, useState } from "react";
import _ from "lodash";
import { HorizontalBarResize } from "../Global";
const axios = require("axios");

function DrivingTimeChart() {
  const [drivingTime, setDrivingTime] = useState({});
  const [labelName, setLabelName] = useState([]);
  const [drivingMinutes, setDrivingMinutes] = useState([]);

  function getDrivingTime() {
    axios
      .get("http://localhost:3001/drivingTime")
      .then(resp => {
        setDrivingTime(resp.data);
      })
      .catch(error => {
        console.log(error);
      });
  }

  useEffect(() => {
    getDrivingTime();
  }, []);

  useEffect(() => {
    if (drivingTime.length > 0) {
      setLabelName(drivingTime.map(item => item.name));
      setDrivingMinutes(drivingTime.map(item => item.drivingMinutes));
    }
  }, [drivingTime]);

  const data = {
    labels: labelName,
    datasets: [
      {
        label: "Tempo de Direo por Motorista",
        backgroundColor: "rgba(75,192,192,0.4)",
        borderColor: "rgba(75,192,192,1)",
        borderWidth: 1,
        hoverBackgroundColor: "rgba(75,192,192,1)",
        hoverBorderColor: "rgba(220,220,220,1)",
        data: drivingMinutes
      }
    ]
  };

  return (
    <>
      {drivingTime !== undefined ? (
        <HorizontalBarResize
          options={{ maintainAspectRatio: false, responsive: true }}
          // options={{ maintainAspectRatio: 1 }}
          data={data}
        />
      ) : (
        ""
      )}
    </>
  );
}

export default DrivingTimeChart;
