import React, { useEffect, useState } from "react";
import {
  FieldContainer,
  LabelStyled,
  SelectStyled,
  ContainerFilters,
  HeaderFilter,
  ButtonsFilter,
  BodyFilter,
  DateStyle,
  FilterButton,
  ErrorMessage
} from "../Global";
import { connect } from "react-redux";
import { Creators as TrackerActions } from "../../store/ducks/trackerSauce";
import { Creators as DailyActions } from "../../store/ducks/dailySauce";
import { bindActionCreators } from "redux";
import { useFormik } from "formik";
import { mountParams } from "../util/Mountparams";
import _ from "lodash";
import moment from "moment";
import { registerLocale } from "react-datepicker";
import el from "date-fns/locale/pt-BR";
import pt from "date-fns/locale/pt";
import InputMask from "react-input-mask";
import SelectTracker from "../Global/SelectTracker";

function FilterDaily(props) {
  registerLocale("pt-BR", el);

  const formik = useFormik({
    initialValues: {
      tracker_id: "",
      date: moment().toDate()
    },
    validate: values => {
      const err = {};
      const message = "Campo obrigatrio";
      if (!values.tracker_id) err.tracker_id = message;
      return err;
    },
    onSubmit: data => {
      const query = {
        id: data.tracker_id,
        date: moment(data.date).format("YYYY-MM-DD")
      };
      props.fetchDailyRequest(query);
    }
  });

  return (
    <ContainerFilters>
      <form onSubmit={formik.handleSubmit}>
        <HeaderFilter>FILTROS</HeaderFilter>
        <BodyFilter>
          <FieldContainer>
            <SelectTracker
              parentCallback={option =>
                formik.setFieldValue("tracker_id", option.id)
              }
            />
          </FieldContainer>
          <FieldContainer>
            <div>
              <LabelStyled>Data</LabelStyled>
            </div>
            <DateStyle
              dateFormat="dd/MM/yyyy"
              selected={formik.values.date}
              onChange={value =>
                formik.setFieldValue("date", moment(value).toDate())
              }
              locale={pt}
              customInput={<InputMask mask={"99/99/9999"} />}
            />
          </FieldContainer>
        </BodyFilter>
        <ButtonsFilter>
          <FilterButton type="submit" color="#4A6971">
            Pesquisar
          </FilterButton>
        </ButtonsFilter>
      </form>
    </ContainerFilters>
  );
}

const mapDispatchToProps = dispatch =>
  bindActionCreators(DailyActions, dispatch);

export default connect(
  null,
  mapDispatchToProps
)(FilterDaily);
