import React from "react";
import Table from "../Table";
import { HistoryContainer } from "./styles";
import { connect } from "react-redux";
import moment from "moment";

function Daily(props) {
  return (
    <HistoryContainer>
      <Table
        loading={props.loading}
        data={props.history.map(item => ({
          ...item,
          time: moment(item.date, "YYYY-MM-DDTHH:mm:ss.SSSZ").format(
            "DD/MM HH:mm:ss"
          ),
          ignition: item.ignition ? "Ligado" : "Desligado",
          speed: item.speed
            ? `${item.speed.toFixed(0)} km/h`
            : item.ignition
            ? "0 km/h"
            : "-"
        }))}
        labelsNames={["time", "ignition", "speed"]}
        header={["Momento", "Iginio", "Velocidade"]}
        changeValue={data => console.log(data)}
        // changeValue={data => props.history.push(`/driver/form/${data.id}`)}
      />
    </HistoryContainer>
  );
}

const mapStateToProps = state => ({
  history: state.dailyState.history,
  loading: state.dailyState.loading
});

export default connect(mapStateToProps)(Daily);
