import React, { useState, useEffect } from "react";
import {
  TextAreaStyled,
  MaintenanceTitle,
  DescContainer,
  UploaderContainer,
  NoRow
} from "./styles";
import {
  FormContainer,
  FormData,
  DataContainer,
  ContainerStyled,
  ButtonsContainer,
  Row,
  ErrorMessage,
  FormDocs,
  FieldContainer,
  TabContainer,
  Input,
  LabelStyled,
  TabView,
  DateStyle,
  SelectStyled,
  Button,
  MoneyDiv,
  MaskMoneyStyle
} from "../Global";
import moment from "moment";
import _ from "lodash";
import { registerLocale } from "react-datepicker";
import pt from "date-fns/locale/pt";
import el from "date-fns/locale/pt-BR";
import InputMask from "react-input-mask";
import { useFormik } from "formik";
import { connect } from "react-redux";
import { Creators as MaintenanceActions } from "../../store/ducks/maintenanceSauce";
import { Creators as TrackerActions } from "../../store/ducks/trackerSauce";
import { bindActionCreators } from "redux";
import Dropzone from "react-dropzone-uploader";
import { getDroppedOrSelectedFiles } from "html5-file-selector";
import { toast, Slide } from "react-toastify";
import SelectTracker from "../Global/SelectTracker";
// import cogoToast from 'cogo-toast';

function Forms(props) {
  registerLocale("pt-BR", el);
  const [tab, setTab] = useState(1);
  const [showError, setShowError] = useState(false);
  const formik = useFormik({
    initialValues: {
      tracker_id: "",
      date_start: "",
      date_end: "",
      price: "",
      description: "",
      type: "",
      maintenanceType: ""
    },
    validate: values => {
      const err = {};
      const message = "Campo obrigatrio";
      if (!values.tracker_id) err.tracker_id = message;
      if (!values.date_start) err.date_start = message;
      if (!values.price) err.price = message;
      if (!values.description) err.description = message;
      if (!values.type) err.type = message;
      if (!values.maintenanceType) err.maintenanceType = message;
      if (values.date_start !== "" && values.date_end !== "") {
        if (values.date_start > values.date_end) {
          toast.error("Data da Incio no pode ser superior  Data Final.");
          err.date_start = message;
          err.date_end = message;
        }
      }
      return err;
    },
    onSubmit: data => {
      console.log(data);
      setShowError(true);
      // setSubmited(false);
      if (data.id) {
        props.maintenanceActions.updateMaintenanceRequest(data);
        console.log(props.maintenanceActions.updateMaintenanceRequest);
      } else {
        props.maintenanceActions.createMaintenanceRequest(data);
      }

      props.maintenanceActions.clearMaintenance();
    }
  });

  const DropzoneInput = ({ accept, onFiles, files, getFilesFromEvent }) => {
    return (
      <label
        style={{
          backgroundColor: "#227ad7", // aplicar hover effect background-color: #0e5fb5;
          color: "#fff",
          cursor: "pointer",
          padding: 15,
          borderRadius: 3
        }}
      >
        {"Clique ou arraste para anexar arquivos"}
        <input
          style={{ display: "none" }}
          type="file"
          accept={accept}
          multiple
          onChange={e => {
            getFilesFromEvent(e).then(chosenFiles => {
              onFiles(chosenFiles);
            });
          }}
        />
      </label>
    );
  };

  const CustomInput = () => {
    const handleSubmit = (files, allFiles) => {
      allFiles.forEach(f => f.remove());
    };

    const getFilesFromEvent = e => {
      return new Promise(resolve => {
        getDroppedOrSelectedFiles(e).then(chosenFiles => {
          resolve(chosenFiles.map(f => f.fileObject));
        });
      });
    };

    const handleChangeStatus = ({ meta, remove }, status) => {
      if (status === "headers_received") {
        // cogoToast(`${meta.name} - Enviado!`);
        toast.success(`${meta.name} - Enviado!`, {
          position: "top-right",
          autoClose: 4000,
          hideProgressBar: true,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          transition: Slide
        });
      } else if (status === "aborted") {
        // cogoToast(`${meta.name} - Falha de envio...`);
        toast.error(`${meta.name} - Falha de envio...`);
      }
    };

    return (
      <Dropzone
        accept="image/*,.jpg,.png"
        getUploadParams={() => ({ url: "https://httpbin.org/post" })}
        onSubmit={handleSubmit}
        InputComponent={DropzoneInput}
        getFilesFromEvent={getFilesFromEvent}
        submitButtonContent={"Enviar"}
        onChangeStatus={handleChangeStatus}
        styles={{
          dropzone: {
            minHeight: 220,
            maxHeight: 220,
            justifyContent: "center",
            overflow: "auto",
            borderStyle: "dashed",
            backgroundImage:
              "url(https://www.toptal.com/designers/subtlepatterns/patterns/stripes-light.png)"
          },
          preview: {
            borderBottom: "none"
          },
          submitButton: {
            backgroundColor: "#227ad7"
          },
          previewImage: {
            maxHeight: 60,
            maxWidth: 60,
            minHeight: 60,
            minWidth: 60
          }
        }}
      />
    );
  };

  useEffect(() => {
    if (props.match.params.id) {
      props.maintenanceActions.fetchMaintenanceRequest(
        `/${props.match.params.id}`
      );
    }
    console.log(formik, "FORMIK");
  }, []);

  useEffect(() => {
    if (!_.isEmpty(props.maintenance)) {
      formik.setValues({
        ...props.maintenance,
        date_start: moment(props.maintenance.date_start).toDate(),
        date_end: moment(props.maintenance.date_end).toDate()
      });
    }
  }, [props.maintenance]);

  const maintenanceTypeOptions = [
    { value: "corrective", label: "Corretiva" },
    { value: "preventive", label: "Preventiva" },
    { value: "detective", label: "Detectiva" }
  ];

  const typeOptions = [
    { value: "Troca de olo", label: "Troca de leo" },
    {
      value: "Reviso do Filtro de Combustvel",
      label: "Reviso do Filtro de Combustvel"
    },
    { value: "Reviso do Filtro de Ar", label: "Reviso do Filtro de Ar" },
    {
      value: "Reviso do Filtro de Cabine/Ar Condicionado",
      label: "Reviso do Filtro de Cabine/Ar Condicionado"
    },
    { value: "Reviso das Lmpadas", label: "Reviso das Lmpadas" },
    { value: "Reviso dos Pneus", label: "Reviso dos Pneus" },
    {
      value: "Reviso da Injeo Eletrnica",
      label: "Reviso da Injeo Eletrnica"
    },
    {
      value: "Reviso dos Limpadores de Para/Brisa",
      label: "Reviso dos Limpadores de Para/Brisa"
    },
    { value: "Peas", label: "Peas" },
    { value: "Outros", label: "Outros" }
  ];

  function cancelButton() {
    props.maintenanceActions.clearMaintenance();
    props.history.push("/maintenance");
  }

  return (
    <ContainerStyled>
      <FormContainer>
        <TabContainer>
          <TabView active={tab === 1} onClick={() => setTab(1)}>
            DADOS
          </TabView>
          {/* <TabView active={tab === 2} onClick={() => setTab(2)}>
            RELATRIOS
          </TabView> */}
        </TabContainer>
        {tab === 1 ? (
          <form onSubmit={formik.handleSubmit}>
            <FormData>
              <DataContainer>
                <MaintenanceTitle>MANUTENO</MaintenanceTitle>
                <div>
                  <Row>
                    <FieldContainer>
                      {(props.match.params.id && formik.values.tracker_id) ||
                      !props.match.params.id ? (
                        <SelectTracker
                          defaultValue={formik.values.tracker_id}
                          parentCallback={option =>
                            formik.setFieldValue("tracker_id", option.id)
                          }
                          error={
                            formik.touched.tracker_id &&
                            formik.errors.tracker_id
                          }
                        />
                      ) : (
                        <></>
                      )}
                    </FieldContainer>
                    <FieldContainer>
                      <div>
                        <LabelStyled>Tipo de Manuteno</LabelStyled>
                        {formik.touched.maintenanceType &&
                        formik.errors.maintenanceType ? (
                          <ErrorMessage>
                            {formik.errors.maintenanceType}
                          </ErrorMessage>
                        ) : (
                          ""
                        )}
                      </div>
                      {(props.match.params.id &&
                        formik.values.maintenanceType) ||
                      !props.match.params.id ? (
                        <SelectStyled
                          options={maintenanceTypeOptions}
                          placeholder="Selecione o tipo de manuteno"
                          noOptionsMessage={() => "Nenhuma opo encontrada"}
                          onChange={option =>
                            formik.setFieldValue(
                              "maintenanceType",
                              option.value
                            )
                          }
                          defaultValue={
                            maintenanceTypeOptions[
                              maintenanceTypeOptions.findIndex(
                                item =>
                                  item.value === formik.values.maintenanceType
                              )
                            ]
                          }
                          error={
                            formik.touched.maintenanceType &&
                            formik.errors.maintenanceType
                          }
                        />
                      ) : (
                        <></>
                      )}
                    </FieldContainer>
                    <FieldContainer>
                      <div>
                        <LabelStyled>Manuteno</LabelStyled>
                        {formik.touched.type && formik.errors.type ? (
                          <ErrorMessage>{formik.errors.type}</ErrorMessage>
                        ) : (
                          ""
                        )}
                      </div>
                      {(props.match.params.id && formik.values.type) ||
                      !props.match.params.id ? (
                        <SelectStyled
                          options={typeOptions}
                          placeholder="Selecione a manuteno"
                          noOptionsMessage={() => "Nenhuma opo encontrada"}
                          onChange={option =>
                            formik.setFieldValue("type", option.value)
                          }
                          defaultValue={
                            typeOptions[
                              typeOptions.findIndex(
                                item => item.value === formik.values.type
                              )
                            ]
                          }
                          error={formik.touched.type && formik.errors.type}
                        />
                      ) : (
                        <></>
                      )}
                    </FieldContainer>
                  </Row>
                  <Row>
                    <FieldContainer>
                      <div>
                        <LabelStyled>Data de Incio</LabelStyled>
                        {formik.touched.date_start &&
                        formik.errors.date_start ? (
                          <ErrorMessage>
                            {formik.errors.date_start}
                          </ErrorMessage>
                        ) : (
                          ""
                        )}
                      </div>
                      <DateStyle
                        dateFormat="dd/MM/yyyy"
                        selected={formik.values.date_start}
                        onChange={value =>
                          formik.setFieldValue(
                            "date_start",
                            moment(value).toDate()
                          )
                        }
                        locale={pt}
                        customInput={<InputMask mask={"99/99/9999"} />}
                        error={
                          formik.touched.date_start && formik.errors.date_start
                        }
                      />
                    </FieldContainer>
                    <FieldContainer>
                      <div>
                        <LabelStyled>Data Final</LabelStyled>
                      </div>
                      <DateStyle
                        dateFormat="dd/MM/yyyy"
                        selected={formik.values.date_end}
                        onChange={value =>
                          formik.setFieldValue(
                            "date_end",
                            moment(value).toDate()
                          )
                        }
                        locale={pt}
                        customInput={<InputMask mask={"99/99/9999"} />}
                      />
                    </FieldContainer>
                    <FieldContainer>
                      <div>
                        <LabelStyled>Custo Total</LabelStyled>
                        {formik.touched.price && formik.errors.price ? (
                          <ErrorMessage>{formik.errors.price}</ErrorMessage>
                        ) : (
                          ""
                        )}
                      </div>
                      <div>
                        <MoneyDiv
                          error={formik.touched.price && formik.errors.price}
                        >
                          R$
                        </MoneyDiv>
                        <MaskMoneyStyle
                          type="number"
                          onChange={formik.handleChange}
                          value={formik.values.price}
                          id="price"
                          name="price"
                          error={formik.touched.price && formik.errors.price}
                        />
                      </div>
                    </FieldContainer>
                  </Row>
                  <NoRow>
                    <DescContainer full>
                      <div>
                        <LabelStyled>Descrio</LabelStyled>
                      </div>
                      <TextAreaStyled
                        rows="6"
                        name="description"
                        onChange={formik.handleChange}
                        value={formik.values.description}
                      />
                    </DescContainer>
                  </NoRow>
                  <NoRow>
                    <UploaderContainer full>
                      <LabelStyled>ANEXOS</LabelStyled>
                      <div id="toast"></div>
                      <CustomInput id="addhover"></CustomInput>
                    </UploaderContainer>
                  </NoRow>
                </div>
              </DataContainer>
              <ButtonsContainer>
                <Button
                  onClick={() => cancelButton()}
                  type="button"
                  color="#E33939"
                >
                  Cancelar
                </Button>
                <Button type="submit" color="#555">
                  Salvar
                </Button>
              </ButtonsContainer>
            </FormData>
          </form>
        ) : (
          <FormDocs>RELATRIOS</FormDocs>
        )}
      </FormContainer>
    </ContainerStyled>
  );
}
const mapStateToProps = state => ({
  maintenances: state.maintenanceState.maintenances,
  maintenance: state.maintenanceState.maintenance,
  loading: state.maintenanceState.loading,
  trackers: state.trackerState.trackers,
  groups: state.bufferState.groups,
  viewGroup: state.bufferState.viewGroup,
  buffer: state.bufferState.buffer
});

function mapDispatchToProps(dispatch) {
  return {
    trackerActions: bindActionCreators(TrackerActions, dispatch),
    maintenanceActions: bindActionCreators(MaintenanceActions, dispatch)
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Forms);
