import { createActions, createReducer } from "reduxsauce";

export const { Types, Creators } = createActions({
  fetchSupplyRequest: ["query"],
  fetchSupplySuccess: ["data"],
  fetchSupplyIdSuccess: ["data"],
  fetchSupplyFailure: [],
  updateSupplyRequest: ["data"],
  deleteSupplyRequest: ["id"],
  createSupplyRequest: ["data"],
  clearSupply: []
});

const INITIAL_STATE = {
  supplies: [],
  supply: {},
  supplyByTracker: {},
  loading: false
};

const clearSupply = (state = INITIAL_STATE, actions) => ({
  ...state,
  supply: []
});

const fetchSupplyRequest = (state = INITIAL_STATE, actions) => ({
  ...state,
  loading: true
});

const fetchSupplySuccess = (state = INITIAL_STATE, actions) => ({
  ...state,
  supplies: actions.data,
  loading: false
});

const fetchSupplyIdSuccess = (state = INITIAL_STATE, actions) => {
  return {
    ...state,
    supply: actions.data,
    loading: false
  };
};

const fetchSupplyFailure = (state = INITIAL_STATE, actions) => {
  return {
    ...state,
    loading: false,
    supplies: []
  };
};

const createSupplyRequest = (state = INITIAL_STATE, actions) => ({
  ...state,
  loading: true
});

const updateSupplyRequest = (state = INITIAL_STATE, actions) => ({
  ...state,
  loading: true
});

const deleteSupplyRequest = (state = INITIAL_STATE, actions) => ({});

export default createReducer(INITIAL_STATE, {
  [Types.FETCH_SUPPLY_REQUEST]: fetchSupplyRequest,
  [Types.FETCH_SUPPLY_SUCCESS]: fetchSupplySuccess,
  [Types.FETCH_SUPPLY_ID_SUCCESS]: fetchSupplyIdSuccess,
  [Types.FETCH_SUPPLY_FAILURE]: fetchSupplyFailure,
  [Types.UPDATE_SUPPLY_REQUEST]: updateSupplyRequest,
  [Types.DELETE_SUPPLY_REQUEST]: deleteSupplyRequest,
  [Types.CREATE_SUPPLY_REQUEST]: createSupplyRequest,
  [Types.CLEAR_SUPPLY]: clearSupply
});
