import React from "react";
import {
  FieldContainer,
  LabelStyled,
  ContainerFilters,
  HeaderFilter,
  ButtonsFilter,
  BodyFilter,
  DateStyle,
  FilterButton
} from "../Global";
import InputMask from "react-input-mask";
import { Creators as SupplyActions } from "../../store/ducks/supplySauce";
import { useFormik } from "formik";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { mountParams } from "../util/Mountparams";
import moment from "moment";
import pt from "date-fns/locale/pt";
import { toast } from "react-toastify";
import _ from "lodash";
import SelectTracker from "../Global/SelectTracker";

function SupplyFilter(props) {
  const formik = useFormik({
    initialValues: {
      tracker_id: "",
      initialDate: "",
      finalDate: ""
    },
    validate: values => {
      console.log(values);
      const err = {};
      if (values.initialDate) {
        if (!values.finalDate) {
          err.finalDate = "Data Invalida";
        }
      }
      if (values.finalDate) {
        if (!values.initialDate) {
          err.initialDate = "Data Invalida";
        }
      }
      if (values.initialDate !== "" && values.finalDate !== "") {
        if (values.initialDate > values.finalDate) {
          toast.error("Data Inicial no pode ser superior  Data Final.");
        }
      }
      return err;
    },
    onSubmit: values => {
      console.log(values);
      const body = _.clone(values);
      if (values.initialDate)
        body.initialDate = moment(values.initialDate).format("YYYY-MM-DD");
      if (values.finalDate)
        body.finalDate = moment(values.finalDate).format("YYYY-MM-DD");
      const query = mountParams(body);
      props.fetchSupplyRequest(query);
    }
  });

  function clearButton() {
    props.fetchSupplyRequest();
  }

  return (
    <ContainerFilters>
      <form onSubmit={formik.handleSubmit}>
        <HeaderFilter>ABASTECIMENTO</HeaderFilter>
        <BodyFilter>
          <FieldContainer>
            <SelectTracker
              parentCallback={option =>
                formik.setFieldValue("tracker_id", option.id)
              }
            />
          </FieldContainer>
          <FieldContainer>
            <LabelStyled>Data Inicial</LabelStyled>
            <DateStyle
              dateFormat="dd/MM/yyyy"
              selected={formik.values.initialDate}
              onChange={value =>
                formik.setFieldValue("initialDate", moment(value).toDate())
              }
              locale={pt}
              customInput={<InputMask mask={"99/99/9999"} />}
              // error={metadataDate.error}
            />
          </FieldContainer>
          <FieldContainer>
            <LabelStyled>Data Final</LabelStyled>
            <DateStyle
              dateFormat="dd/MM/yyyy"
              selected={formik.values.finalDate}
              onChange={value =>
                formik.setFieldValue("finalDate", moment(value).toDate())
              }
              locale={pt}
              customInput={<InputMask mask={"99/99/9999"} />}
              // error={metadataDate.error}
            />
          </FieldContainer>
        </BodyFilter>
        <ButtonsFilter>
          <FilterButton
            front="#000000"
            back="#bfbfbf"
            type="button"
            onClick={() => clearButton()}
          >
            Limpar
          </FilterButton>
          <FilterButton
            color="#737373"
            onClick={() => props.history.push("/supply/form")}
          >
            Novo
          </FilterButton>
          <FilterButton color="#4A6971" type="submit">
            Pesquisar
          </FilterButton>
        </ButtonsFilter>
      </form>
    </ContainerFilters>
  );
}

const mapDispatchToProps = dispatch =>
  bindActionCreators(SupplyActions, dispatch);

export default connect(
  null,
  mapDispatchToProps
)(SupplyFilter);
