import { call, put, all, takeEvery } from "redux-saga/effects";
import Api from "../../services/api";
import { Types } from "../ducks/dailySauce";
import { toast } from "react-toastify";

export function* dailySaga(payload) {
  try {
    let response = yield call(Api.fetchDaily, payload.data);
    let speed = yield call(Api.fetchChartSpeed, payload.data.id);
    let history = yield call(Api.fetchHistory, payload.data);
    let stops = yield call(Api.fetchStops, payload.data);
    yield put({
      type: Types.FETCH_DAILY_SUCCESS,
      data: response.data,
      speed: speed.data,
      history: history.data,
      stops: stops.data
    });
  } catch (error) {
    yield put({ type: Types.FETCH_DAILY_FAILURE });
  }
}

export default function* rootSaga() {
  yield all([takeEvery(Types.FETCH_DAILY_REQUEST, dailySaga)]);
}
