import React, { useEffect } from "react";
import { useFormik } from "formik";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import { Creators as AuthActions } from "../../store/ducks/authSauce";
import {
  ButtonStyled,
  InputStyled,
  LinkStyled,
  CheckboxStyled
} from "../Global";
import { Container, Main, Error } from "./styles";
import Mask from "../util/Mask";
import Loading from "../Loading";

function Login(props) {
  const formik = useFormik({
    initialValues: {
      cpf: "",
      password: ""
    },
    onSubmit: data => {
      console.log(formik);
      props.loginRequest(data);
    }
  });

  function handleForgot() {
    props.history.push("/auth/forgot");
  }

  useEffect(() => {
    if (props.token) props.history.push("/monitoring");
  }, [props.token]);

  return (
    <Container>
      {!props.loading ? (
        <Main>
          <h3>Login</h3>
          <form onSubmit={formik.handleSubmit}>
            <Mask
              mask="999.999.999-99"
              name="cpf"
              onChange={formik.handleChange}
              value={formik.values.serial}
              placeholder="Cpf"
              required
            />
            <InputStyled
              placeholder="Senha"
              name="password"
              required
              type="password"
              onChange={formik.handleChange}
              value={formik.values.serial}
            />
            <CheckboxStyled>
              <input type="checkbox" name="remember" />
              &nbsp;&nbsp;Lembrar-me
            </CheckboxStyled>
            <LinkStyled onClick={() => handleForgot()}>
              Esqueceu a senha?
            </LinkStyled>
            <ButtonStyled color="#4a6971" type="submit">
              Entrar
            </ButtonStyled>
            {/* <Error>{formik.errors ? JSON.stringify(formik.errors) : ""}</Error> */}
          </form>
        </Main>
      ) : (
        <Loading />
      )}
    </Container>
  );
}

const mapStateToProps = state => ({
  loading: state.authState.loading,
  token: state.authState.token
});

const mapDispatchToProps = dispatch =>
  bindActionCreators(AuthActions, dispatch);

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Login);
