import React, { useRef, useContext } from "react";

import BoardContext from "./context";

import { ContainerCard } from "./styles";
import { useDrag, useDrop } from "react-dnd";

export default function Alert({ data, index, listIndex }) {
  const ref = useRef();
  const { move, showModal } = useContext(BoardContext);

  const [{ isDragging }, dragRef] = useDrag({
    item: { type: "CARD", index, listIndex },
    collect: monitor => {
      return {
        isDragging: monitor.isDragging()
      };
    },
    end: monitor => {
      showModal(monitor.listIndex, monitor.index);
    }
  });

  const [, dropRef] = useDrop({
    accept: "CARD",
    hover(item, monitor) {
      const draggedListIndex = item.listIndex;
      const targetListIndex = listIndex;

      const draggedIndex = item.index;
      const targetIndex = index;
      if (draggedIndex === targetIndex && draggedIndex === targetListIndex) {
        return;
      }
      const targetSize = ref.current.getBoundingClientRect();
      const targetCenter = (targetSize.bottom - targetSize.top) / 2;

      const draggedOffset = monitor.getClientOffset();
      const draggedTop = draggedOffset.y - targetSize.top;

      if (draggedIndex < targetIndex && draggedTop < targetCenter) {
        return;
      }

      if (draggedIndex > targetIndex && draggedTop > targetCenter) {
        return;
      }
      if (
        targetListIndex !== draggedListIndex &&
        draggedIndex === targetIndex
      ) {
        return;
      }

      move(draggedListIndex, targetListIndex, draggedIndex, targetIndex);
      item.index = targetIndex;
      item.listIndex = targetListIndex;
    }
  });
  dragRef(dropRef(ref));
  return (
    <ContainerCard
      color={data.bg}
      textColor={data.color}
      ref={ref}
      isDragging={isDragging}
    >
      <header>{data.title}</header>
      <p>{data.message}</p>
    </ContainerCard>
  );
}
