import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import { Creators as DriverActions } from "../../store/ducks/driverSauce";
import { bindActionCreators } from "redux";
import Table from "../Table";
import { TableChartContainer, ChartContainer } from "./styles";
import Chart from "./chart";
import { TableContainerBox } from "../Global";

function Driver(props) {
  useEffect(() => {
    props.fetchDriverRequest();
  }, []);

  return (
    // <TableChartContainer>
    <TableContainerBox>
      <Table
        data={props.driver || []}
        labelsNames={["name", "cpf", "email", "phone"]}
        header={["nome", "cpf", "email", "telefone"]}
        changeValue={data => props.history.push(`/driver/form/${data.id}`)}
      />
    </TableContainerBox>
    // <Chart />
    // </TableChartContainer>
  );
}

const mapStateToProps = state => ({
  driver: state.driverState.drivers,
  loading: state.driverState.loading
});

const mapDispatchToProps = dispatch =>
  bindActionCreators(DriverActions, dispatch);

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Driver);
