import { createActions, createReducer } from "reduxsauce";

export const { Types, Creators } = createActions({
  fetchCommandRequest: ["query"],
  fetchCommandSuccess: ["data"],
  fetchCommandIdSuccess: ["data"],
  fetchCommandFailure: [],
  createCommandRequest: ["data"],
  setViewModal: [],
  setLoadingButton: ["data"]
});

const INITIAL_STATE = {
  commands: [],
  command: {},
  commandByTracker: {},
  loading: false,
  viewModal: false,
  loadingButton: {}
};

const changeModal = (state = INITIAL_STATE, action) => ({
  ...state,
  viewModal: !state.viewModal
});

const setLoadingButton = (state = INITIAL_STATE, action) => {
  console.log(action, "LOADING BUTTON SAUCE");
  return {
    ...state,
    loadingButton: action.data
  };
};

const fetchCommandRequest = (state = INITIAL_STATE, actions) => ({
  ...state,
  loading: true
});

const fetchCommandSuccess = (state = INITIAL_STATE, actions) => ({
  ...state,
  commands: actions.data,
  loading: false
});

const fetchCommandIdSuccess = (state = INITIAL_STATE, actions) => {
  return {
    ...state,
    command: actions.data,
    loading: false
  };
};

const fetchCommandFailure = (state = INITIAL_STATE, actions) => {
  return {
    ...state,
    loading: false,
    commands: []
  };
};

const createCommandRequest = (state = INITIAL_STATE, actions) => ({
  ...state,
  loading: true
});

export default createReducer(INITIAL_STATE, {
  [Types.FETCH_COMMAND_REQUEST]: fetchCommandRequest,
  [Types.FETCH_COMMAND_SUCCESS]: fetchCommandSuccess,
  [Types.FETCH_COMMAND_ID_SUCCESS]: fetchCommandIdSuccess,
  [Types.FETCH_COMMAND_FAILURE]: fetchCommandFailure,
  [Types.CREATE_COMMAND_REQUEST]: createCommandRequest,
  [Types.SET_VIEW_MODAL]: changeModal,
  [Types.SET_LOADING_BUTTON]: setLoadingButton
});
