import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import { Creators as JourneyActions } from "../../store/ducks/journeySauce";
import { bindActionCreators } from "redux";
import Table from "../Table";
import moment from "moment";
import { toast } from "react-toastify";

function Journey(props) {
  useEffect(() => {
    props.fetchJourneyRequest();
  }, []);

  return (
    <Table
      data={props.journey.map(item => ({
        ...item,
        start: moment(item.start).format("DD/MM/YYYY - HH:mm:ss "),
        end: item.end ? moment(item.end).format("DD/MM/YYYY - HH:mm:ss ") : "",
        tracker_id: item.trackerItem.label,
        driver_id: item.driver
          ? item.driver.name
          : "Motorista no identificado",
        automatic: item.automatic ? "Carto identificador" : "Manual"
      }))}
      labelsNames={["tracker_id", "driver_id", "start", "end", "automatic"]}
      header={["veculo", "motorista", "incio", "Fim", "Tipo de Jornada"]}
      // changeValue={
      //   item.automatic && !item.end
      //     ? toast.success("Aguarde jornada ser finalizada para editar")
      //     : data => props.history.push(`/journey/form/${data.id}`)
      // }
      changeValue={data =>
        data.automatic && !data.end
          ? toast.warning("Aguarde jornada ser finalizada para editar")
          : props.history.push(`/journey/form/${data.id}`)
      }
    />
  );
}

const mapStateToProps = state => ({
  journey: state.journeyState.journeys,
  loading: state.journeyState.loading
});

const mapDispatchToProps = dispatch =>
  bindActionCreators(JourneyActions, dispatch);

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Journey);
