import React, { useEffect, useState } from "react";
import {
  FieldContainer,
  LabelStyled,
  OptionPlaceholder,
  ContainerFilters,
  HeaderFilter,
  ButtonsFilter,
  SelectStyles,
  BodyFilter,
  Input,
  FilterButton,
  SelectStyled
} from "../Global";
import { Creators as CommandActions } from "../../store/ducks/commandSauce";
import { useFormik } from "formik";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import api from "../../services/api";
import SelectTracker from "../Global/SelectTracker";

function CommandFilter(props) {
  const [trackers, setTrackers] = useState([]);

  const formik = useFormik({
    initialValues: {
      tracker_id: ""
      // initialDate: "",
      // finalDate: ""
    },
    validate: values => {
      const err = {};
      return err;
    },
    onSubmit: (values, bag) => {
      const query = `?tracker_id=${values.tracker_id}`;
      props.fetchCommandRequest(query);
    }
  });

  const commandOptions = [
    { value: "Bloquear/Desbloquear", label: "Bloquear/Desbloquear" },
    {
      value: "Sirene",
      label: "Sirene"
    }
  ];

  // const [tracker_id, metadataTracker_id] = getFieldProps("tracker_id", "text");

  function clearButton() {
    props.fetchCommandRequest();
    // formik.setValues({
    //   ...values,
    //   tracker_id: ""
    //   // initialDate: "",
    //   // finalDate: ""
    // });
  }

  return (
    <ContainerFilters>
      <form onSubmit={formik.handleSubmit}>
        <HeaderFilter>GESTO DE COMANDOS</HeaderFilter>
        <BodyFilter>
          <FieldContainer>
            <SelectTracker
              parentCallback={option =>
                formik.setFieldValue("tracker_id", option.plate)
              }
            />
          </FieldContainer>
          <FieldContainer>
            <LabelStyled>Tipo</LabelStyled>
            <SelectStyled
              options={commandOptions}
              placeholder="Selecione o tipo de comando"
              noOptionsMessage={() => "Nenhuma opo encontrada"}
              onChange={option =>
                formik.setFieldValue("maintenanceType", option.value)
              }
            />
          </FieldContainer>
        </BodyFilter>
        <ButtonsFilter>
          <FilterButton
            front="#000000"
            back="#bfbfbf"
            onClick={() => clearButton()}
          >
            Limpar
          </FilterButton>
          <FilterButton color="#4A6971" type="submit">
            Pesquisar
          </FilterButton>
        </ButtonsFilter>
      </form>
    </ContainerFilters>
  );
}

const mapDispatchToProps = dispatch =>
  bindActionCreators(CommandActions, dispatch);

export default connect(
  null,
  mapDispatchToProps
)(CommandFilter);
