import React, { useState } from "react";
import { Font } from "@react-pdf/renderer";
import {
  FieldContainer,
  LabelStyled,
  SelectStyled,
  DateStyle,
  ButtonsFilter,
  FilterButton
} from "../Global";
import SelectTracker from "../Global/SelectTracker";
import SelectDriver from "../Global/SelectDriver";
import el from "date-fns/locale/pt-BR";
import pt from "date-fns/locale/pt";
import InputMask from "react-input-mask";
import { useFormik } from "formik";
import { registerLocale } from "react-datepicker";
import { connect } from "react-redux";
import { Creators as SupplyActions } from "../../store/ducks/supplySauce";
import { bindActionCreators } from "redux";
import robotoBold from "../../assets/fonts/Roboto-Bold.ttf";
import robotoMediumItalic from "../../assets/fonts/Roboto-MediumItalic.ttf";
import robotoThinItalic from "../../assets/fonts/Roboto-ThinItalic.ttf";
import moment from "moment";
import ignitionOptions from "../../assets/json/ignition.json";

function HistoryFilter() {
  registerLocale("pt-BR", el);
  const [startDate, setStartDate] = useState(new Date());
  const [endDate, setEndDate] = useState(new Date());
  const formik = useFormik({
    initialValues: {
      tracker_id: "",
      driver_id: "",
      ignition: "",
      speed: ""
    }
  });

  Font.register({
    family: "Roboto",
    format: "sans-serif",
    src: robotoBold
  });

  Font.register({
    family: "RobotoThinItalic",
    format: "sans-serif",
    src: robotoThinItalic
  });

  Font.register({
    family: "RobotoMediumItalic",
    format: "sans-serif",
    src: robotoMediumItalic
  });

  Font.registerHyphenationCallback(word => {
    return [word];
  });

  const start = moment(startDate).format("YYYY/MM/DD HH:mm");
  const end = moment(endDate).format("YYYY/MM/DD HH:mm");

  console.log(start);
  console.log(end);
  console.log(formik.values.tracker_id);
  console.log(formik.values.driver_id);
  console.log(formik.values.ignition);

  return (
    <>
      <FieldContainer>
        <SelectTracker
          defaultValue={formik.values.tracker_id}
          parentCallback={option =>
            formik.setFieldValue("tracker_id", option.id)
          }
        />
      </FieldContainer>
      <FieldContainer>
        <SelectDriver
          parentCallback={option =>
            formik.setFieldValue("driver_id", option.id)
          }
        />
      </FieldContainer>
      <FieldContainer>
        <LabelStyled>Ignio</LabelStyled>
        <SelectStyled
          options={ignitionOptions}
          placeholder="Selecione uma opo"
          noOptionsMessage={() => "Nenhuma opo encontrada"}
          onChange={option => formik.setFieldValue("ignition", option.value)}
          defaultValue={
            ignitionOptions[
              ignitionOptions.findIndex(
                item => item.value === formik.values.ignition
              )
            ]
          }
        />
      </FieldContainer>
      <FieldContainer>
        <LabelStyled>Velocidade</LabelStyled>
        <SelectStyled
          placeholder="Selecione o valor"
          noOptionsMessage={() => "Nenhuma opo encontrada"}
        />
      </FieldContainer>
      <FieldContainer>
        <LabelStyled>Data de Incio</LabelStyled>
        <DateStyle
          showTimeSelect
          dateFormat={"dd/MM/yyyy HH:mm"}
          selected={startDate}
          onChange={start => setStartDate(start)}
          locale={pt}
          customInput={<InputMask mask={"99/99/9999 99:99"} />}
        />
      </FieldContainer>
      <FieldContainer>
        <LabelStyled>Data Final</LabelStyled>
        <DateStyle
          showTimeSelect
          dateFormat={"dd/MM/yyyy HH:mm"}
          selected={endDate}
          onChange={end => setEndDate(end)}
          locale={pt}
          customInput={<InputMask mask={"99/99/9999 99:99"} />}
        />
      </FieldContainer>
      <ButtonsFilter>
        <FilterButton type="submit" color="#555">
          Visualizar
        </FilterButton>
      </ButtonsFilter>
    </>
  );
}

const mapStateToProps = state => ({
  supply: state.supplyState.supply,
  loading: state.supplyState.loading
});

const mapDispatchToProps = dispatch =>
  bindActionCreators(SupplyActions, dispatch);

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(HistoryFilter);
