import React, { useState, useEffect } from "react";
import {
  AvatarContainer,
  Avatar,
  ProfileContainer,
  FileUpload
} from "./styles";
import {
  FormContainer,
  FormData,
  DataContainer,
  DateStyle,
  ContainerTitle,
  ContainerStyled,
  ButtonsContainer,
  Button,
  Row,
  FormDocs,
  MaskStyle,
  FieldContainer,
  TabView,
  ErrorMessage,
  Input,
  LabelStyled,
  TabContainer
} from "../Global";
import pt from "date-fns/locale/pt";
import { MdCameraAlt } from "react-icons/md";
import moment from "moment";
import _ from "lodash";
import { registerLocale } from "react-datepicker";
import el from "date-fns/locale/pt-BR";
import InputMask from "react-input-mask";
import { validate } from "cpf-check";
import { useFormik } from "formik";
import { connect } from "react-redux";
import { Creators as DriverActions } from "../../store/ducks/driverSauce";
import { bindActionCreators } from "redux";

function Forms(props) {
  registerLocale("pt-BR", el);
  const [tab, setTab] = useState(1);
  const [editor, setEditor] = useState("http://example.com/initialimage.jpg");
  const [zoom, setZoom] = useState(2.0);
  const [image, setImage] = useState("");
  const formik = useFormik({
    initialValues: {
      name: "",
      email: "",
      birthdate: "",
      phone: "",
      card: "",
      observation: "",
      cpf: "",
      rg: "",
      category: "",
      cnh: "",
      dateCnh: ""
    },
    validate: values => {
      let errors = {};
      const message = "Campo obrigatrio";
      if (!values.name) errors.name = message;
      if (values.email) {
        if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email))
          errors.email = "E-mail Invalido";
      }
      if (!values.birthdate) {
        errors.birthdate = message;
      }
      if (!values.phone) {
        errors.phone = message;
      } else if (values.phone.length < 16 && values.phone.length !== 11) {
        errors.phone = "Telefone Invalido";
      }
      if (!values.cpf) {
        errors.cpf = message;
      } else if (values.cpf.length < 14) {
        errors.cpf = "CPF Invalido";
      } else if (
        values.cpf.length === 14 &&
        values.cpf.length !== 11 &&
        !validate(values.cpf)
      ) {
        errors.cpf = "CPF Invalido";
      }
      return errors;
    },
    onSubmit: values => {
      if (values.id) {
        props.updateDriverRequest({
          ...values,
          birthdate: moment(values.birthdate).format("YYYY-MM-DD"),
          dateCnh: moment(values.dateCnh).format("YYYY-MM-DD")
        });
      } else {
        props.createDriverRequest({
          ...values,
          birthdate: moment(values.birthdate).format("YYYY-MM-DD"),
          dateCnh: moment(values.dateCnh).format("YYYY-MM-DD")
        });
      }
      props.clearDriver();
    }
  });

  useEffect(() => {
    if (props.match.params.id) {
      props.fetchDriverRequest(props.match.params.id);
    }
  }, []);

  useEffect(() => {
    if (!_.isEmpty(props.driver)) {
      console.log(props.driver, "props");
      formik.setValues({
        ...props.driver,
        birthdate: moment(props.driver.birthdate).toDate(),
        dateCnh: moment(props.driver.dateCnh).toDate()
      });
    }
  }, [props.driver]);

  // function setEditorRef(editor) {
  //   setEditor(editor);
  // }

  function changeImage(e) {
    console.log(e);
    setImage(e.target.files[0]);
  }

  function cancelButton() {
    props.history.push("/Driver");
    props.clearDriver();
  }

  function onChangeZoom(e) {
    zoom > 3.5
      ? setZoom(3.5)
      : zoom < 1.5
      ? setZoom(1.5)
      : e.deltaY < 0
      ? setZoom(zoom + 0.5)
      : setZoom(zoom - 0.5);
  }
  return (
    <ContainerStyled>
      <FormContainer>
        <TabContainer>
          <TabView active={tab === 1} onClick={() => setTab(1)}>
            DADOS
          </TabView>
          {/* <TabView active={tab === 2} onClick={() => setTab(2)}>
            RELATRIOS
          </TabView> */}
        </TabContainer>
        {tab === 1 ? (
          <form onSubmit={formik.handleSubmit}>
            <FormData>
              <DataContainer>
                <ContainerTitle>MOTORISTA</ContainerTitle>
                <ProfileContainer>
                  <AvatarContainer>
                    <Avatar
                      borderRadius={10}
                      image={image}
                      width={160}
                      height={160}
                      border={0}
                      color={[255, 255, 255, 0.6]} // RGBA
                      scale={zoom}
                      onWheel={onChangeZoom}
                    />
                    <FileUpload>
                      <input
                        type="file"
                        accept="image/*"
                        name="upload"
                        onChange={changeImage}
                      />
                      <MdCameraAlt size={25} color="#fff" />
                    </FileUpload>
                  </AvatarContainer>
                  <div>
                    <Row>
                      <FieldContainer>
                        <div>
                          <LabelStyled>Nome</LabelStyled>
                          {formik.touched.name && formik.errors.name ? (
                            <ErrorMessage>{formik.errors.name}</ErrorMessage>
                          ) : (
                            ""
                          )}
                        </div>
                        <Input
                          name="name"
                          onChange={formik.handleChange}
                          value={formik.values.name}
                          error={formik.touched.name && formik.errors.name}
                        />
                      </FieldContainer>
                      <FieldContainer>
                        <div>
                          <LabelStyled>E-mail</LabelStyled>
                        </div>
                        <Input
                          name="email"
                          onChange={formik.handleChange}
                          value={formik.values.email}
                        />
                      </FieldContainer>
                      <FieldContainer>
                        <div>
                          <LabelStyled>Data de Nascimento</LabelStyled>
                          {formik.touched.birthdate &&
                          formik.errors.birthdate ? (
                            <ErrorMessage>
                              {formik.errors.birthdate}
                            </ErrorMessage>
                          ) : (
                            ""
                          )}
                        </div>
                        <DateStyle
                          error={
                            formik.touched.birthdate && formik.errors.birthdate
                          }
                          dateFormat="dd/MM/yyyy"
                          selected={formik.values.birthdate}
                          onChange={value =>
                            formik.setFieldValue(
                              "birthdate",
                              moment(value).toDate()
                            )
                          }
                          locale={pt}
                          customInput={<InputMask mask={"99/99/9999"} />}
                        />
                      </FieldContainer>
                    </Row>
                    <Row>
                      <FieldContainer>
                        <div>
                          <LabelStyled>Telefone</LabelStyled>
                          {formik.touched.phone && formik.errors.phone ? (
                            <ErrorMessage>{formik.errors.phone}</ErrorMessage>
                          ) : (
                            ""
                          )}
                        </div>
                        <MaskStyle
                          onChange={formik.handleChange}
                          value={formik.values.phone}
                          id="phone"
                          name="phone"
                          mask={"(99) 9 9999-9999"}
                          maskChar={null}
                          error={formik.touched.phone && formik.errors.phone}
                        />
                      </FieldContainer>
                      <FieldContainer>
                        <LabelStyled>C. de Identificao</LabelStyled>
                        <Input
                          name="card"
                          onChange={formik.handleChange}
                          value={formik.values.card}
                        />
                      </FieldContainer>
                    </Row>
                    <Row>
                      <FieldContainer>
                        <LabelStyled>Observao</LabelStyled>
                        <Input
                          name="observation"
                          onChange={formik.handleChange}
                          value={formik.values.observation}
                        />
                      </FieldContainer>
                    </Row>
                  </div>
                </ProfileContainer>

                <ContainerTitle>DOCUMENTOS</ContainerTitle>
                <div>
                  <Row>
                    <FieldContainer>
                      <div>
                        <LabelStyled>CPF</LabelStyled>
                        {formik.touched.cpf && formik.errors.cpf ? (
                          <ErrorMessage>{formik.errors.cpf}</ErrorMessage>
                        ) : (
                          ""
                        )}
                      </div>
                      <MaskStyle
                        onChange={formik.handleChange}
                        value={formik.values.cpf}
                        id="cpf"
                        name="cpf"
                        mask={"999.999.999-99"}
                        maskChar={null}
                        error={formik.touched.cpf && formik.errors.cpf}
                      />
                    </FieldContainer>
                    <FieldContainer>
                      <LabelStyled>RG</LabelStyled>
                      <Input
                        name="rg"
                        onChange={formik.handleChange}
                        value={formik.values.rg}
                      />
                    </FieldContainer>
                  </Row>
                  <Row>
                    <FieldContainer>
                      <LabelStyled>Categoria CNH</LabelStyled>
                      <Input
                        name="category"
                        onChange={formik.handleChange}
                        value={formik.values.category}
                      />
                    </FieldContainer>
                    <FieldContainer>
                      <LabelStyled>CNH</LabelStyled>
                      <Input
                        name="cnh"
                        onChange={formik.handleChange}
                        value={formik.values.cnh}
                      />
                    </FieldContainer>
                    <FieldContainer>
                      <LabelStyled>Validade CNH</LabelStyled>
                      <DateStyle
                        error={
                          formik.touched.dateCnh && formik.errors.birthdate
                        }
                        dateFormat="dd/MM/yyyy"
                        selected={formik.values.dateCnh}
                        onChange={value =>
                          formik.setFieldValue(
                            "dateCnh",
                            moment(value).toDate()
                          )
                        }
                        locale={pt}
                        customInput={<InputMask mask={"99/99/9999"} />}
                      />
                    </FieldContainer>
                  </Row>
                </div>
              </DataContainer>
              <ButtonsContainer>
                <Button
                  onClick={() => cancelButton()}
                  type="button"
                  color="#E33939"
                >
                  Cancelar
                </Button>
                <Button type="submit" color="#555">
                  Salvar
                </Button>
              </ButtonsContainer>
            </FormData>
          </form>
        ) : (
          <FormDocs>RELATRIOS</FormDocs>
        )}
      </FormContainer>
    </ContainerStyled>
  );
}
const mapStateToProps = state => ({
  driver: state.driverState.driver,
  loading: state.driverState.loading
});

const mapDispatchToProps = dispatch =>
  bindActionCreators(DriverActions, dispatch);

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Forms);
