import { createActions, createReducer } from "reduxsauce";

export const { Types, Creators } = createActions({
  fetchJourneyRequest: ["query"],
  fetchJourneySuccess: ["data"],
  fetchJourneyIdSuccess: ["data"],
  fetchJourneyFailure: [],
  updateJourneyRequest: ["data"],
  deleteJourneyRequest: ["id"],
  createJourneyRequest: ["data"],
  clearJourney: []
});

const INITIAL_STATE = {
  journeys: [],
  journey: {},
  journeyByTracker: {},
  loading: false
};

const clearJourney = (state = INITIAL_STATE, actions) => ({
  ...state,
  journey: {}
});

const fetchJourneyRequest = (state = INITIAL_STATE, actions) => ({
  ...state,
  loading: true
});

const fetchJourneySuccess = (state = INITIAL_STATE, actions) => ({
  ...state,
  journeys: actions.data,
  loading: false
});

const fetchJourneyIdSuccess = (state = INITIAL_STATE, actions) => {
  return {
    ...state,
    journey: actions.data,
    loading: false
  };
};

const fetchJourneyFailure = (state = INITIAL_STATE, actions) => {
  return {
    ...state,
    loading: false,
    journeys: []
  };
};

const createJourneyRequest = (state = INITIAL_STATE, actions) => {
  console.log("CRIAR");
  return {
    ...state,
    loading: true
  };
};

const updateJourneyRequest = (state = INITIAL_STATE, actions) => ({
  ...state,
  loading: true
});

const deleteJourneyRequest = (state = INITIAL_STATE, actions) => ({});

export default createReducer(INITIAL_STATE, {
  [Types.FETCH_JOURNEY_REQUEST]: fetchJourneyRequest,
  [Types.FETCH_JOURNEY_SUCCESS]: fetchJourneySuccess,
  [Types.FETCH_JOURNEY_ID_SUCCESS]: fetchJourneyIdSuccess,
  [Types.FETCH_JOURNEY_FAILURE]: fetchJourneyFailure,
  [Types.UPDATE_JOURNEY_REQUEST]: updateJourneyRequest,
  [Types.DELETE_JOURNEY_REQUEST]: deleteJourneyRequest,
  [Types.CREATE_JOURNEY_REQUEST]: createJourneyRequest,
  [Types.CLEAR_JOURNEY]: clearJourney
});
