import React, { useState, useEffect } from "react";
import {
  ContainerFilters,
  HeaderFilter,
  SelectStyled,
  BodyFilter,
  LabelStyled,
  FieldContainer
} from "../Global";
import api from "../../services/api";
import { connect } from "react-redux";
import { Creators as FenceActions } from "../../store/ducks/fenceSauce";
import { bindActionCreators } from "redux";

function Fence(props) {
  const [options, setOptions] = useState([]);
  const [loading, setLoading] = useState(false);

  async function getFences() {
    const response = await api.fetchGeometries("?type=fence");
    setOptions(response.data);
    setLoading(true);
  }

  useEffect(() => {
    getFences();
  }, []);

  return (
    <ContainerFilters>
      <form>
        <HeaderFilter>CERCAS</HeaderFilter>
        <BodyFilter>
          <FieldContainer>
            <LabelStyled>Nome da Cerca</LabelStyled>
            {loading ? (
              <SelectStyled
                onChange={option => props.fetchFenceRequest(option.id)}
                getOptionValue={option => option.id}
                getOptionLabel={option => option.name}
                placeholder="Selecione a cerca"
                noOptionsMessage={() => "Nenhuma cerca encontrada"}
                options={options}
                error={props.error}
              />
            ) : (
              <></>
            )}
          </FieldContainer>
        </BodyFilter>
      </form>
    </ContainerFilters>
  );
}

const mapStateToProps = state => ({
  fences: state.fenceState.fences,
  loading: state.trackerState.loading
});

const mapDispatchToProps = dispatch =>
  bindActionCreators(FenceActions, dispatch);

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Fence);
