import React, { useEffect, useState } from "react";
import mapStyle from "../../assets/map.json";
import { Map } from "./styles";
import { connect } from "react-redux";
import _ from "lodash";
import { Creators as FenceActions } from "../../store/ducks/fenceSauce";
import { bindActionCreators } from "redux";
import { saveZone, cancelZone } from "./controls";

function FenceMap(props) {
  useEffect(() => {
    const map = new window.google.maps.Map(document.getElementById("map"), {
      center: { lat: -20.361797, lng: -40.660631 },
      fullscreenControl: false,
      mapTypeControl: false,
      zoomControl: false,
      scaleControl: false,
      streetViewControl: false,
      streetViewControlOptions: {
        position: window.google.maps.ControlPosition.RIGHT_BOTTOM
      },
      zoom: 13,
      controlSize: 25,
      styles: mapStyle
    });

    const geojson = props.fence.geojson;
    if (geojson !== undefined) {
      var geoPaths = [];
      var points = new Array();
      var bounds = new window.google.maps.LatLngBounds();

      props.fence.geojson.geometry.coordinates[0].map(item => {
        geoPaths.push({ lat: item[1], lng: item[0] });
        points = new window.google.maps.LatLng(item[1], item[0]);
        bounds.extend(points);
      });
      console.log(geoPaths, "POLY");
      map.fitBounds(bounds);

      var polygon = new window.google.maps.Polygon({
        editable: true,
        dragable: true,
        strokeOpacity: 0,
        strokeWeight: 0,
        fillOpacity: 0.6,
        paths: geoPaths,
        map: map
      });

      polygon.getPaths().forEach(function(path, index) {
        window.google.maps.event.addListener(path, "insert_at", function() {
          getPolygonCoords();
        });

        window.google.maps.event.addListener(path, "remove_at", function() {
          getPolygonCoords();
        });

        window.google.maps.event.addListener(path, "set_at", function() {
          getPolygonCoords();
        });
      });
      var newCoordinates_poly = [];
      function getPolygonCoords() {
        var coordinates_poly = polygon.getPath().getArray();
        var lat_poly = [];
        var lng_poly = [];
        var latlng_poly = [];
        for (var i = 0; i < coordinates_poly.length; i++) {
          lat_poly = coordinates_poly[i].lat();
          lng_poly = coordinates_poly[i].lng();

          latlng_poly = [lat_poly, lng_poly];
          newCoordinates_poly.push(latlng_poly);
        }

        if (
          map.controls[window.google.maps.ControlPosition.RIGHT_TOP].length < 1
        ) {
          const zoneControlDiv = document.createElement("div");
          new saveZone(zoneControlDiv, map, props, null, newCoordinates_poly);
          new cancelZone(zoneControlDiv, map, props, polygon, geoPaths);
          zoneControlDiv.index = 1;
          map.controls[window.google.maps.ControlPosition.RIGHT_TOP].push(
            zoneControlDiv
          );
        }
      }
    }
  }, [props.fence]);

  useEffect(() => {
    props.clearFence();
  }, []);

  return <Map id="map"></Map>;
}

const mapStateToProps = state => ({
  fences: state.fenceState.fences,
  fence: state.fenceState.fence,
  loading: state.fenceState.loading
});

const mapDispatchToProps = dispatch =>
  bindActionCreators(FenceActions, dispatch);

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(FenceMap);
