import React, { useEffect, useState } from "react";
import api from "../../../services/api";
import { SelectStyled } from "../index";

export default function SelectDriver(props) {
  const [options, setOptions] = useState([]);
  const [loading, setLoading] = useState(false);

  async function getFences() {
    const response = await api.fetchGeometries("?type=fence");
    setOptions(response.data);
    setLoading(true);
  }

  useEffect(() => {
    getFences();
  }, []);

  return (
    <>
      {loading ? (
        <SelectStyled
          onChange={option => props.parentCallback(option)}
          getOptionValue={option => option.id}
          getOptionLabel={option => option.name}
          defaultValue={
            options[options.findIndex(item => item.id === props.defaultValue)]
          }
          options={options}
          error={props.error}
        />
      ) : (
        <></>
      )}
    </>
  );
}
