import React, { useState, useEffect } from "react";
import { connect } from "react-redux";
import {
  Container,
  GroupContainer,
  IconContainer,
  IconHeader,
  Badge,
  SelectStyled,
  MoreOptions,
  MenuStyled,
  Item,
  CarContainer,
  ModalHeader,
  ModalDetail,
  ModalButtons,
  ButtonModal
} from "./styles";
import { MdAdd, MdCreate, MdDelete } from "react-icons/md";
import { toast } from "react-toastify";
import Group from "../Group";
import socketIOClient from "socket.io-client";
import api from "../../services/api";
import Dropdown from "rc-dropdown";
import totalIcon from "../../assets/totalIconSemSobra.svg";
import runIcon from "../../assets/runIconSemSobra.svg";
import stopIcon from "../../assets/stopIconSemSobra.svg";
import alertIcon from "../../assets/alertIconSemSobra.svg";
import noSignalIcon from "../../assets/noSignalIconSemSobra.svg";
import sleepIcon from "../../assets/sleepIconSemSobra.svg";
import CarItem from "./CarItem";
import Modal from "react-awesome-modal";
import { Creators as BufferActions } from "../../store/ducks/bufferSauce";
import { bindActionCreators } from "redux";

function CarPanel(props) {
  const [groupsFormated, setGroupsFormated] = useState([]);
  const [viewModal, setViewModal] = useState(false);
  const [groupSelected, setGroupSelected] = useState("");

  useEffect(() => {
    setGroupsFormated(
      props.groups.map(item => ({
        label: item.name,
        value: item.id
      }))
    );
  }, [props.groups]);

  useEffect(() => {
    props.bufferRequest();
    const socket = socketIOClient(process.env.REACT_APP_SERVER_URL);
    socket.on("buffer", data => {
      console.log(data);
      props.updateBuffer(JSON.parse(data));
    });
  }, []);

  async function deleteGroup() {
    const response = await api.deleteGroups(groupSelected);
    if (response.status === 200) {
      toast.success("Grupo atualizado com sucesso!");
      props.bufferRequest();
      setViewModal(false);
      setGroupSelected(null);
    }
  }

  async function handleChange(selectedOption) {
    props.history.push("/monitoring");
    props.bufferRequest(selectedOption.value);
    setGroupSelected(selectedOption.value);
  }

  const menu = (
    <MenuStyled selectable={false}>
      <Item
        onClick={() => {
          setGroupSelected("");
          return props.setViewGroup();
        }}
      >
        <MdAdd size={15} />
        <span>Criar grupo</span>
      </Item>
      <Item onClick={() => groupSelected && props.setViewGroup()}>
        <MdCreate size={15} />
        <span>Editar grupo</span>
      </Item>
      <Item onClick={() => groupSelected && setViewModal(true)}>
        <MdDelete size={15} />
        <span>Deletar grupo</span>
      </Item>
    </MenuStyled>
  );
  console.log(props.filterActive);

  return (
    <>
      {!props.viewGroup ? (
        <Container>
          <GroupContainer>
            <SelectStyled
              value={
                groupSelected
                  ? groupsFormated.filter(item => item.value === groupSelected)
                  : ""
              }
              onChange={handleChange}
              placeholder="Selecione o grupo"
              noOptionsMessage={() => "Nenhum grupo encontrado"}
              options={groupsFormated}
            />
            <Dropdown trigger={["click"]} overlay={menu} animation="slide-up">
              <MoreOptions size={30} color="#625f5f" />
            </Dropdown>
          </GroupContainer>
          <IconContainer>
            <IconHeader
              selected={props.filterActive === "ALL"}
              onClick={() => props.filterBuffer("ALL")}
            >
              <img src={totalIcon} alt="" />
              <Badge>{props.bufferImutable.length}</Badge>
            </IconHeader>
            <IconHeader
              selected={props.filterActive === "ON"}
              onClick={() => props.filterBuffer("ON")}
            >
              <img src={runIcon} alt="" />
              <Badge>
                {
                  props.bufferImutable.filter(item => item.status === "ON")
                    .length
                }
              </Badge>
            </IconHeader>
            <IconHeader
              selected={props.filterActive === "OFF"}
              onClick={() => props.filterBuffer("OFF")}
            >
              <img src={stopIcon} alt="" />
              <Badge>
                {
                  props.bufferImutable.filter(item => item.status === "OFF")
                    .length
                }
              </Badge>
            </IconHeader>
            <IconHeader
              selected={props.filterActive === "DANGER"}
              onClick={() => props.filterBuffer("DANGER")}
            >
              <img src={alertIcon} alt="" />
              <Badge>
                {
                  props.bufferImutable.filter(item => item.status === "DANGER")
                    .length
                }
              </Badge>
            </IconHeader>
            <IconHeader
              selected={props.filterActive === "DISCONNECTED"}
              onClick={() => props.filterBuffer("DISCONNECTED")}
            >
              <img src={noSignalIcon} alt="" />
              <Badge>
                {
                  props.bufferImutable.filter(
                    item => item.status === "DISCONNECTED"
                  ).length
                }
              </Badge>
            </IconHeader>
            <IconHeader
            // active={activeNumber === 6}
            // onClick={() => setActive(6)}
            >
              <img src={sleepIcon} alt="" />
              <Badge>0</Badge>
            </IconHeader>
          </IconContainer>
          <CarContainer>
            {props.buffer.map((item, i) => (
              <CarItem key={i} {...item} {...props} />
            ))}
          </CarContainer>
        </Container>
      ) : (
        <Container>
          <Group groupSelected={groupSelected} />
        </Container>
      )}
      <Modal
        visible={viewModal}
        height="200"
        effect="fadeInUp"
        onClickAway={() => setViewModal(false)}
      >
        <ModalHeader>Deletar grupo</ModalHeader>
        <ModalDetail>Deseja realmente deletar o grupo?</ModalDetail>
        <ModalButtons>
          <ButtonModal color="#555" onClick={() => setViewModal(false)}>
            No
          </ButtonModal>
          <ButtonModal color="#d21212" onClick={deleteGroup}>
            Sim
          </ButtonModal>
        </ModalButtons>
      </Modal>
    </>
  );
}

const mapStateToProps = state => ({
  viewGroup: state.bufferState.viewGroup,
  buffer: state.bufferState.buffer,
  bufferImutable: state.bufferState.bufferImutable,
  loading: state.bufferState.loading,
  groups: state.bufferState.groups,
  filterActive: state.bufferState.filterActive
});

const mapDispatchToProps = dispatch =>
  bindActionCreators(BufferActions, dispatch);

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(CarPanel);
