import React, { useEffect, useState } from "react";
import {
  ActionGrid,
  ListContainer,
  ActionsContainer,
  ItemList,
  DriverContainer,
  AvatarImage,
  DriverTitle,
  DriverDetail,
  ActionImage
} from "./styles";
import Tooltip from "rc-tooltip";
import "rc-tooltip/assets/bootstrap.css";
import { MdPhone, MdTimer } from "react-icons/md";
import addressIcon from "../../assets/infobox/address.svg";
import batteryIcon from "../../assets/infobox/battery.svg";
import directionsIcon from "../../assets/infobox/directions.svg";
import iginitionIcon from "../../assets/infobox/iginition.svg";
import lastComunicationIcon from "../../assets/infobox/lastComunication.svg";
import odometerIcon from "../../assets/infobox/odometer.svg";
import positionIcon from "../../assets/infobox/position.svg";
import Alert from "../../assets/side/alerts.svg";
import Block from "../../assets/infobox/block.svg";
import { connect } from "react-redux";
import moment from "moment";
import { useLocation } from "react-router-dom";
function useQuery() {
  return new URLSearchParams(useLocation().search);
}
function Action(props) {
  let query = useQuery();
  const id_tracker = query.get("id");
  const [tracker, setTracker] = useState({});
  const [angleName, setAngle] = useState("Norte");
  useEffect(() => {
    if (props.buffer.length !== 0) {
      const data = props.buffer.find(item => item.tracker_id == id_tracker);
      if (data) {
        setTracker(data);
        getDirections(data.angle);
      }
    }
  }, [props.buffer]);
  useEffect(() => {
    if (props.buffer.length !== 0) {
      const data = props.buffer.find(item => item.tracker_id == id_tracker);
      setTracker(data);
      getDirections(data.angle);
    }
  }, [id_tracker]);

  function getDirections(angle) {
    let nameAngle;
    if (angle > 337.5 && angle < 22.5) nameAngle = "Norte";
    if (angle > 22.5 && angle < 67.5) nameAngle = "Nordeste";
    if (angle > 67.5 && angle < 112.5) nameAngle = "Leste ";
    if (angle > 112.5 && angle < 157.5) nameAngle = "Sudeste";
    if (angle > 157.5 && angle < 202.5) nameAngle = "Sul";
    if (angle > 202.5 && angle < 247.5) nameAngle = "Sudoeste";
    if (angle > 247.5 && angle < 292.5) nameAngle = "Oeste";
    if (angle > 292.5 && angle < 337.5) nameAngle = "Noroeste";
    return setAngle(nameAngle);
  }
  function Actionitem(props) {
    return (
      <ItemList rotate={props.rotate}>
        <Tooltip
          placement="right"
          overlay={props.tooltip}
          mouseEnterDelay={0}
          mouseLeaveDelay={0}
          arrowContent={<div className="rc-tooltip-arrow-inner"></div>}
        >
          <img src={props.icon} alt="" />
        </Tooltip>
        <span>{props.text}</span>
      </ItemList>
    );
  }

  return (
    <ActionGrid>
      <ListContainer>
        <DriverContainer>
          <AvatarImage src="https://api.adorable.io/avatars/285/abott@adorable.pngCopy to Clipboard" />
          <div>
            <DriverTitle>
              {tracker.driver ? tracker.driver : "Motorista no identificado"}
            </DriverTitle>
            <DriverDetail>
              <div>
                <MdPhone size={15} />
                <span>
                  {tracker.driver_phone ? tracker.driver_phone : "---"}
                </span>
              </div>
              {/* <div>
                <MdTimer size={15} />
                <span>1 hora dirigindo</span>
              </div> */}
            </DriverDetail>
          </div>
        </DriverContainer>
        <div>
          <Actionitem
            icon={addressIcon}
            text="Rua bela vista, Domingos Martins"
            tooltip="Endereo"
          />
          <Actionitem
            icon={batteryIcon}
            text={`${tracker.power_voltage} V`}
            tooltip="Bateria"
          />
          <Actionitem
            icon={iginitionIcon}
            text={tracker.ignition ? "Ligada" : "Desligada"}
            tooltip="Iginio"
          />
          <Actionitem
            icon={lastComunicationIcon}
            text={moment.utc(tracker.date).format("DD/MM/YYYY HH:mm:ss")}
            tooltip="Ultima comunicao"
          />
          <Actionitem
            icon={odometerIcon}
            text={`${tracker.odometer} Km`}
            tooltip="Odometro"
          />
          <Actionitem
            icon={directionsIcon}
            rotate={tracker.angle}
            text={angleName}
            tooltip="Drieo"
          />
          <Actionitem
            icon={positionIcon}
            text={`${tracker.lat}, ${tracker.lng}`}
            tooltip="Posio"
          />
        </div>
      </ListContainer>
      <ActionsContainer>
        <ActionImage src={Alert} alt="" />
        <ActionImage src={Block} alt="" />
      </ActionsContainer>
    </ActionGrid>
  );
}

const mapStateToProps = state => ({
  buffer: state.bufferState.buffer
});

export default connect(mapStateToProps)(Action);
