import React, { useState } from "react";
import { Font } from "@react-pdf/renderer";
import {
  FieldContainer,
  LabelStyled,
  DateStyle,
  ButtonsFilter,
  FilterButton
} from "../Global";
import { BodyFilter } from "./styles";
import moment from "moment";
import { useFormik } from "formik";
import SelectTracker from "../Global/SelectTracker";
import el from "date-fns/locale/pt-BR";
import pt from "date-fns/locale/pt";
import { registerLocale } from "react-datepicker";
import { connect } from "react-redux";
import { Creators as ReportActions } from "../../store/ducks/reportSauce";
import { bindActionCreators } from "redux";
import InputMask from "react-input-mask";
import robotoBold from "../../assets/fonts/Roboto-Bold.ttf";
import robotoMediumItalic from "../../assets/fonts/Roboto-MediumItalic.ttf";
import robotoThinItalic from "../../assets/fonts/Roboto-ThinItalic.ttf";

function TraveledKmsFilter() {
  registerLocale("pt-BR", el);
  const [trackerOption, setTrackerOption] = useState();
  const formik = useFormik({
    initialValues: {
      tracker_id: "",
      start: "",
      end: ""
    },
    onSubmit: values => {
      console.log(values);
      setTrackerOption(values.tracker_id);
    }
  });

  Font.register({
    family: "Roboto",
    format: "sans-serif",
    src: robotoBold
  });

  Font.register({
    family: "RobotoThinItalic",
    format: "sans-serif",
    src: robotoThinItalic
  });

  Font.register({
    family: "RobotoMediumItalic",
    format: "sans-serif",
    src: robotoMediumItalic
  });

  Font.registerHyphenationCallback(word => {
    return [word];
  });

  console.log(formik.values.start);
  console.log(formik.values.end);
  console.log(formik.values.tracker_id);

  return (
    <form onSubmit={formik.handleSubmit}>
      <FieldContainer>
        <SelectTracker
          defaultValue={formik.values.tracker_id}
          parentCallback={option =>
            formik.setFieldValue("tracker_id", option.id)
          }
        />
      </FieldContainer>
      <FieldContainer>
        <LabelStyled>Data de Incio</LabelStyled>
        <DateStyle
          showTimeSelect
          dateFormat={"dd/MM/yyyy HH:mm"}
          selected={formik.values.start}
          onChange={value =>
            formik.setFieldValue("start", moment(value).toDate())
          }
          locale={pt}
          customInput={<InputMask mask={"99/99/9999 99:99"} />}
        />
      </FieldContainer>
      <FieldContainer>
        <LabelStyled>Data Final</LabelStyled>
        <DateStyle
          showTimeSelect
          dateFormat={"dd/MM/yyyy HH:mm"}
          selected={formik.values.end}
          onChange={value =>
            formik.setFieldValue("end", moment(value).toDate())
          }
          locale={pt}
          customInput={<InputMask mask={"99/99/9999 99:99"} />}
        />
      </FieldContainer>
      <ButtonsFilter>
        <FilterButton type="submit" color="#555">
          Visualizar
        </FilterButton>
      </ButtonsFilter>
    </form>
  );
}

const mapStateToProps = state => ({
  report: state.reportState.report,
  loading: state.reportState.loading
});

const mapDispatchToProps = dispatch =>
  bindActionCreators(ReportActions, dispatch);

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(TraveledKmsFilter);
