import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import { Creators as RouteActions } from "../../store/ducks/routeSauce";
import { bindActionCreators } from "redux";
import Table from "../Table";
import Loading from "../Loading";

function Route(props) {
  const [options, setOptions] = useState([]);

  async function getRoutes() {
    const response = await props.fetchRouteRequest("?type=trip");
    setOptions(response.data);
    console.log(props.routes, "teste");
  }

  useEffect(() => {
    getRoutes();
  }, []);

  return !props.loading ? (
    <Table
      data={props.routes || []}
      data={props.routes.map(item => ({
        ...item,
        origin: item.geojson.origin,
        destination: item.geojson.destination
      }))}
      labelsNames={["id", "name", "origin", "destination"]}
      header={["id", "nome", "origem", "destino"]}
      changeValue={data => props.history.push(`/route/form/${data.id}`)}
    />
  ) : (
    <Loading />
  );
}

const mapStateToProps = state => ({
  routes: state.routeState.routes,
  loading: state.routeState.loading
});

const mapDispatchToProps = dispatch =>
  bindActionCreators(RouteActions, dispatch);

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Route);
