import React from "react";
import {
  FieldContainer,
  LabelStyled,
  ContainerFilters,
  HeaderFilter,
  ButtonsFilter,
  BodyFilter,
  DateStyle,
  FilterButton
} from "../Global";
import InputMask from "react-input-mask";
import { Creators as MaintenanceActions } from "../../store/ducks/maintenanceSauce";
import { useFormik } from "formik";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { mountParams } from "../util/Mountparams";
import moment from "moment";
import pt from "date-fns/locale/pt";
import { toast } from "react-toastify";
import _ from "lodash";
import SelectTracker from "../Global/SelectTracker";

function MaintenanceFilter(props) {
  const formik = useFormik({
    initialValues: {
      tracker_id: "",
      date_start: "",
      date_end: ""
    },
    onSubmit: data => {
      console.log(data);
      const body = _.clone(data);
      if (data.date_start)
        body.date_start = moment(data.date_start).format("YYYY-MM-DD");
      if (data.date_end)
        body.date_end = moment(data.date_end).format("YYYY-MM-DD");
      const query = mountParams(body);
      props.fetchMaintenanceRequest(query);
    }
  });

  function clearButton() {
    props.fetchMaintenanceRequest();
  }

  return (
    <ContainerFilters>
      <form onSubmit={formik.handleSubmit}>
        <HeaderFilter>MANUTENO</HeaderFilter>
        <BodyFilter>
          <FieldContainer>
            <SelectTracker
              parentCallback={option =>
                formik.setFieldValue("tracker_id", option.id)
              }
            />
          </FieldContainer>
          <FieldContainer>
            <div>
              <LabelStyled>Data Inicial</LabelStyled>
            </div>
            <DateStyle
              dateFormat="dd/MM/yyyy"
              selected={formik.values.date_start}
              onChange={value =>
                formik.setFieldValue("date_start", moment(value).toDate())
              }
              locale={pt}
              customInput={<InputMask mask={"99/99/9999"} />}
            />
          </FieldContainer>
          <FieldContainer>
            <div>
              <LabelStyled>Data Final</LabelStyled>
            </div>
            <DateStyle
              dateFormat="dd/MM/yyyy"
              selected={formik.values.date_end}
              onChange={value =>
                formik.setFieldValue("date_end", moment(value).toDate())
              }
              locale={pt}
              customInput={<InputMask mask={"99/99/9999"} />}
            />
          </FieldContainer>
        </BodyFilter>
        <ButtonsFilter>
          <FilterButton
            front="#000000"
            back="#bfbfbf"
            onClick={() => clearButton()}
          >
            Limpar
          </FilterButton>
          <FilterButton
            color="#737373"
            onClick={() => props.history.push("/maintenance/form")}
          >
            Novo
          </FilterButton>
          <FilterButton color="#4A6971" type="submit">
            Pesquisar
          </FilterButton>
        </ButtonsFilter>
      </form>
    </ContainerFilters>
  );
}

const mapDispatchToProps = dispatch =>
  bindActionCreators(MaintenanceActions, dispatch);

export default connect(
  null,
  mapDispatchToProps
)(MaintenanceFilter);
