import React, { useState, useEffect } from "react";
import { DateStyle } from "./styles";
import {
  ContainerTitle,
  FormContainer,
  FormData,
  DataContainer,
  ContainerStyled,
  ButtonsContainer,
  Button,
  ProfileContainer,
  Row,
  ErrorMessage,
  FieldContainer,
  FormDocs,
  MaskMoneyStyle,
  MoneyDiv,
  TabContainer,
  TabView,
  LabelStyled,
  Input,
  SelectStyled,
  FormContainerWithoutTab
} from "../Global";
import pt from "date-fns/locale/pt";
import moment from "moment";
import _ from "lodash";
import { registerLocale } from "react-datepicker";
import el from "date-fns/locale/pt-BR";
import { useFormik } from "formik";
import { connect } from "react-redux";
import { Creators as SupplyActions } from "../../store/ducks/supplySauce";
import { bindActionCreators } from "redux";
import InputMask from "react-input-mask";
import SelectTracker from "../Global/SelectTracker";

function Forms(props) {
  registerLocale("pt-BR", el);
  const [tab, setTab] = useState(1);
  const [showValidation, setShowValidation] = useState(false);
  const [liters, setLiters] = useState("");

  const formik = useFormik({
    initialValues: {
      tracker_id: "",
      date: moment().toDate(),
      fuelPrice: "",
      totalPrice: "",
      fuelType: ""
    },

    validate: values => {
      let errors = {};
      const message = "Campo obrigatrio";
      if (values.tracker_id === "") errors.tracker_id = message;
      if (values.fuelType === "") errors.fuelType = message;
      if (!values.date) errors.date = message;
      if (values.fuelPrice === "") errors.fuelPrice = message;
      if (values.totalPrice === "") errors.totalPrice = message;
      return errors;
    },

    onSubmit: data => {
      console.log(data);
      if (data.id) {
        props.updateSupplyRequest(data);
      } else {
        props.createSupplyRequest(data);
        console.log(props.createSupplyRequest);
      }
      props.clearSupply();
    }
  });

  const fuelTypeSelect = [
    { value: "Gasolina comum", label: "Gasolina comum" },
    { value: "Gasolina aditivada", label: "Gasolina aditivada" },
    { value: "Etanol", label: "Etanol" },
    { value: "Diesel", label: "Diesel" },
    { value: "Gs Natural Veicular", label: "Gs Natural Veicular" }
  ];

  useEffect(() => {
    if (props.match.params.id) {
      props.fetchSupplyRequest(`/${props.match.params.id}`);
    }
  }, []);

  useEffect(() => {
    if (!_.isEmpty(props.supply)) {
      formik.setValues({
        ...props.supply,
        date: moment(props.supply.date).toDate()
      });
    }
  }, [props.supply]);

  useEffect(() => {
    console.log(formik.values, "formik");
    var litersResult = "";
    if (formik.values && formik.values.fuelPrice && formik.values.totalPrice) {
      litersResult = formik.values.totalPrice / formik.values.fuelPrice;
      console.log(litersResult, "litersResult");
    }
    if (litersResult !== "") {
    }
  }, [formik.values]);

  useEffect(() => {
    if (formik.values.fuelPrice && formik.values.totalPrice) {
      setLiters(
        (formik.values.totalPrice / formik.values.fuelPrice).toFixed(2)
      );
    }
  }, [formik.values.fuelPrice, formik.values.totalPrice]);

  function cancelButton() {
    props.history.push("/supply");
    props.clearSupply();
  }

  return (
    <ContainerStyled>
      <FormContainer>
        <TabContainer>
          <TabView active={tab === 1} onClick={() => setTab(1)}>
            DADOS
          </TabView>
          {/* <TabView active={tab === 2} onClick={() => setTab(2)}>
            RELATRIOS
          </TabView> */}
        </TabContainer>
        {tab === 1 ? (
          <form onSubmit={formik.handleSubmit}>
            <FormData>
              <DataContainer>
                <ContainerTitle>ABASTECIMENTO</ContainerTitle>
                <ProfileContainer>
                  <div>
                    <Row>
                      <FieldContainer>
                        {(props.match.params.id && formik.values.tracker_id) ||
                        !props.match.params.id ? (
                          <SelectTracker
                            defaultValue={formik.values.tracker_id}
                            parentCallback={option =>
                              formik.setFieldValue("tracker_id", option.id)
                            }
                            error={
                              formik.errors.tracker_id &&
                              formik.touched.tracker_id
                            }
                          />
                        ) : (
                          <></>
                        )}
                      </FieldContainer>
                      <FieldContainer>
                        <div>
                          <LabelStyled>DATA DE ABASTECIMENTO</LabelStyled>
                        </div>
                        <DateStyle
                          dateFormat="dd/MM/yyyy"
                          selected={formik.values.date}
                          onChange={value =>
                            formik.setFieldValue("date", moment(value).toDate())
                          }
                          locale={pt}
                          customInput={<InputMask mask={"99/99/9999"} />}
                        />
                      </FieldContainer>
                      <FieldContainer>
                        <div>
                          <LabelStyled>TIPO DE COMBUSTIVEL</LabelStyled>
                          {formik.errors.fuelType && formik.touched.fuelType ? (
                            <ErrorMessage>
                              {formik.errors.fuelType}
                            </ErrorMessage>
                          ) : (
                            <></>
                          )}
                        </div>
                        {(props.match.params.id && formik.values.fuelType) ||
                        !props.match.params.id ? (
                          <SelectStyled
                            options={fuelTypeSelect}
                            placeholder="Selecione o tipo de combustvel"
                            noOptionsMessage={() =>
                              "Nenhum tipo de combustvel encontrado"
                            }
                            onChange={option =>
                              formik.setFieldValue("fuelType", option.value)
                            }
                            defaultValue={
                              fuelTypeSelect[
                                fuelTypeSelect.findIndex(
                                  item => item.value === formik.values.fuelType
                                )
                              ]
                            }
                            error={
                              formik.errors.fuelType && formik.touched.fuelType
                            }
                          />
                        ) : (
                          <></>
                        )}
                      </FieldContainer>
                    </Row>
                    <Row>
                      <FieldContainer>
                        <div>
                          <LabelStyled>VALOR/LITRO</LabelStyled>
                          {formik.errors.fuelPrice &&
                          formik.touched.fuelPrice ? (
                            <ErrorMessage>Campo obrigatrio</ErrorMessage>
                          ) : (
                            <></>
                          )}
                        </div>
                        <div>
                          <MoneyDiv
                            error={
                              formik.errors.fuelPrice &&
                              formik.touched.fuelPrice
                            }
                          >
                            R$
                          </MoneyDiv>
                          <MaskMoneyStyle
                            type="number"
                            onChange={formik.handleChange}
                            value={formik.values.fuelPrice}
                            id="fuelPrice"
                            name="fuelPrice"
                            error={
                              formik.errors.fuelPrice &&
                              formik.touched.fuelPrice
                            }
                          />
                        </div>
                      </FieldContainer>
                      <FieldContainer>
                        <div>
                          <LabelStyled>VALOR TOTAL</LabelStyled>
                          {formik.errors.totalPrice &&
                          formik.touched.totalPrice ? (
                            <ErrorMessage>Campo obrigatrio</ErrorMessage>
                          ) : (
                            <></>
                          )}
                        </div>
                        <div>
                          <MoneyDiv
                            error={
                              formik.errors.totalPrice &&
                              formik.touched.totalPrice
                            }
                          >
                            R$
                          </MoneyDiv>
                          <MaskMoneyStyle
                            type="number"
                            onChange={formik.handleChange}
                            value={formik.values.totalPrice}
                            id="totalPrice"
                            name="totalPrice"
                            error={
                              formik.errors.totalPrice &&
                              formik.touched.totalPrice
                            }
                          />
                        </div>
                      </FieldContainer>
                      <FieldContainer>
                        <div>
                          <LabelStyled>LITROS</LabelStyled>
                        </div>
                        <Input disabled={true} value={liters} />
                      </FieldContainer>
                    </Row>
                  </div>
                </ProfileContainer>
              </DataContainer>
              <ButtonsContainer>
                <Button
                  onClick={() => cancelButton()}
                  type="button"
                  color="#E33939"
                >
                  Cancelar
                </Button>
                <Button
                  type="submit"
                  // onClick={() => setShowValidation(true)}
                  color="#555"
                >
                  Salvar
                </Button>
              </ButtonsContainer>
            </FormData>
          </form>
        ) : (
          <FormDocs>RELATRIOS</FormDocs>
        )}
      </FormContainer>
    </ContainerStyled>
  );
}
const mapStateToProps = state => ({
  supply: state.supplyState.supply,
  loading: state.supplyState.loading
});

const mapDispatchToProps = dispatch =>
  bindActionCreators(SupplyActions, dispatch);

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Forms);
