import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import { Creators as ViolationActions } from "../../store/ducks/violationSauce";
import { bindActionCreators } from "redux";
import Table from "../Table";
import moment from "moment";

function Violation(props) {
  useEffect(() => {
    props.fetchViolationRequest();
  }, []);

  return (
    <Table
      data={props.violation || []}
      data={props.violation.map(item => ({
        ...item,
        date: moment(item.date).format("DD/MM/YYYY"),
        expiry: moment(item.date).format("DD/MM/YYYY"),
        tracker_id: item.trackerItem.label,
        driver_id: item.driver.name
      }))}
      labelsNames={[
        "tracker_id",
        "driver_id",
        "level",
        "price",
        "date",
        "expiry"
      ]}
      header={[
        "veculo",
        "motorista",
        "gravidade",
        "valor",
        "data",
        "vencimento"
      ]}
      changeValue={data => props.history.push(`/violation/form/${data.id}`)}
    />
  );
}

const mapStateToProps = state => ({
  violation: state.violationState.violations,
  loading: state.violationState.loading
});

const mapDispatchToProps = dispatch =>
  bindActionCreators(ViolationActions, dispatch);

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Violation);
