import React from "react";
import {
  LabelStyled,
  Input,
  ErroMessage,
  ContainerLabel,
  MaskStyle
} from "./styles";

export default function Tracker(props) {
  return (
    <>
      <ContainerLabel>
        <LabelStyled>{props.labelName}</LabelStyled>
        {/* {props.metadata.touch && props.metadata.error && (
          <ErroMessage>{props.metadata.error}</ErroMessage>
        )} */}
      </ContainerLabel>
      {!props.mask ? (
        <Input
          disabled={props.disabled ? true : false}
          name={props.name}
          onChange={props.parentCallback}
          // onChange={props.onchange}
          // value={formik.values.label}
          // value={props.value}
          // error={props.metadata.touch && props.metadata.error}
        />
      ) : (
        <MaskStyle
          maskChar={null}
          mask={props.mask}
          disabled={props.disabled ? true : false}
          name={props.name}
          onChange={props.parentCallback}
          // error={props.metadata.touch && props.metadata.error}
        />
      )}
    </>
  );
}
