import React from "react";
import { connect } from "react-redux";
import { Switch, Route, Redirect } from "react-router-dom";
import { ConnectedRouter } from "connected-react-router";

import PrivateRoute from "./PrivateRoute";

import Navbar from "../components/Navbar";
import Side from "../components/Side";
import Login from "../components/Login";
import Forgot from "../components/Forgot";
import Monitoring from "../components/Monitoring";
import Password from "../components/Password";
import Profile from "../components/Profile";
import Driver from "../components/Driver";
import FormDriver from "../components/Driver/form";
import FormMaintenance from "../components/Maintenance/form";
import Dashboard from "../components/Dashboard";
import Alert from "../components/Alert";
import Tracker from "../components/Tracker";
import FormTracker from "../components/Tracker/form";
import Maintenance from "../components/Maintenance";
import Supply from "../components/Supply";
import FormSupply from "../components/Supply/form";
import Violation from "../components/Violation";
import FormViolation from "../components/Violation/form";
import Journey from "../components/Journey";
import FormJourney from "../components/Journey/form";
import Report from "../components/Report";
import Preferences from "../components/Preferences";
import Daily from "../components/Daily";
import Command from "../components/Command";
import Fence from "../components/Fence";
import Trip from "../components/Trip";
import Surveillance from "../components/Surveillance";
import MapTrip from "../components/Trip/map";

import history from "./history";

const Routes = props => (
  <ConnectedRouter history={history}>
    <Navbar history={history} />
    <Side history={history} />
    <Switch>
      <Redirect exact from="/" to="monitoring" />
      <Route path="/auth/login" component={Login} />
      <Route path="/auth/forgot" component={Forgot} />
      <Route path="/password/:token" component={Password} />
      <PrivateRoute path="/profile" component={Profile} token={props.token} />
      <PrivateRoute
        path="/driver/form/:id"
        component={FormDriver}
        token={props.token}
      />
      <PrivateRoute
        path="/driver/form"
        component={FormDriver}
        token={props.token}
      />
      <PrivateRoute
        path="/maintenance/form/:id"
        component={FormMaintenance}
        token={props.token}
      />
      <PrivateRoute
        path="/maintenance/form"
        component={FormMaintenance}
        token={props.token}
      />
      <PrivateRoute path="/driver" component={Driver} token={props.token} />
      <PrivateRoute
        path="/monitoring"
        component={Monitoring}
        token={props.token}
      />
      <PrivateRoute exact path="/alert" component={Alert} token={props.token} />
      <PrivateRoute
        exact
        path="/tracker/form/:id"
        component={FormTracker}
        token={props.token}
      />
      <PrivateRoute
        exact
        path="/tracker"
        component={Tracker}
        token={props.token}
      />
      <PrivateRoute
        exact
        path="/maintenance"
        component={Maintenance}
        token={props.token}
      />
      <PrivateRoute
        path="/supply/form/:id"
        component={FormSupply}
        token={props.token}
      />
      <PrivateRoute
        path="/supply/form"
        component={FormSupply}
        token={props.token}
      />
      <PrivateRoute
        exact
        path="/supply"
        component={Supply}
        token={props.token}
      />
      <PrivateRoute
        exact
        path="/command"
        component={Command}
        token={props.token}
      />
      <PrivateRoute
        path="/violation/form/:id"
        component={FormViolation}
        token={props.token}
      />
      <PrivateRoute
        path="/violation/form"
        component={FormViolation}
        token={props.token}
      />
      <PrivateRoute
        path="/violation"
        component={Violation}
        token={props.token}
      />
      <PrivateRoute
        path="/journey/form/:id"
        component={FormJourney}
        token={props.token}
      />
      <Route path="/journey/form" component={FormJourney} token={props.token} />
      <PrivateRoute
        path="/surveillance"
        component={Surveillance}
        token={props.token}
      />
      <PrivateRoute path="/journey" component={Journey} token={props.token} />
      <PrivateRoute path="/report" component={Report} token={props.token} />
      <PrivateRoute
        exact
        path="/preferences"
        component={Preferences}
        token={props.token}
      />
      <PrivateRoute exact path="/route" component={Trip} token={props.token} />
      <PrivateRoute
        exact
        path="/route/map"
        component={MapTrip}
        token={props.token}
      />
      <PrivateRoute
        path="/route/form/:id"
        component={MapTrip}
        token={props.token}
      />
      <PrivateRoute path="/fence" component={Fence} token={props.token} />
      <PrivateRoute exact path="/daily" component={Daily} token={props.token} />
    </Switch>
  </ConnectedRouter>
);

const mapStateToProps = state => ({
  token: state.authState.token
});

export default connect(
  mapStateToProps,
  null
)(Routes);
