import React, { useEffect } from "react";
import { DetailContainer, TimelineContainer } from "./styles";
import { connect } from "react-redux";
import { Timeline, TimelineEvent } from "react-event-timeline";
import moment from "moment";
import "react-perfect-scrollbar/dist/css/styles.css";

function Daily(props) {
  return (
    <DetailContainer>
      <TimelineContainer>
        <Timeline>
          {props.stops.map((item, index) =>
            item.event === "ON" ? (
              <TimelineEvent
                style={{ backgroundColor: "#e8f4f0" }}
                key={index}
                bubbleStyle={{
                  borderColor: "#4A6971",
                  backgroundColor: "#e8f4f0"
                }}
                title="Ligou o carro"
                // subtitle={item.odometer}
                createdAt={moment(item.date, "YYYY-MM-DDTHH:mm:ss.SSSZ").format(
                  "HH:mm:ss"
                )}
                // icon={<i className="material-icons md-18 md-dark"></i>}
              ></TimelineEvent>
            ) : (
              <TimelineEvent
                key={index}
                style={{ backgroundColor: "#f5f5f5" }}
                bubbleStyle={{
                  borderColor: "#777575",
                  backgroundColor: "#f5f5f5"
                }}
                title="Desligou o carro"
                subtitle={`Percorreu ${item.odometer -
                  props.stops[index + 1].odometer} km`}
                createdAt={moment(item.date, "YYYY-MM-DDTHH:mm:ss.SSSZ").format(
                  "HH:mm:ss"
                )}
                // icon={<i className="material-icons md-18"></i>}
              ></TimelineEvent>
            )
          )}
        </Timeline>
      </TimelineContainer>
    </DetailContainer>
  );
}

const mapStateToProps = state => ({
  stops: state.dailyState.stops
});

export default connect(mapStateToProps)(Daily);
