import React from "react";
import InputMask from "react-input-mask";

import { InputStyled } from "../Global";

export default function Mask(props) {
  return (
    <>
      <InputMask
        mask={props.mask}
        onChange={props.onChange}
        value={props.value}
        {...props}
      >
        {inputProps => <InputStyled {...inputProps} />}
      </InputMask>
    </>
  );
}
