import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import { Creators as SupplyActions } from "../../store/ducks/supplySauce";
import { bindActionCreators } from "redux";
import Table from "../Table";
import moment from "moment";

function Supply(props) {
  useEffect(() => {
    props.fetchSupplyRequest();
  }, []);

  return (
    <>
      <Table
        data={props.supplies || []}
        data={props.supplies.map(item => ({
          ...item,
          date: moment(item.date).format("DD/MM/YYYY"),
          tracker_id: item.trackerItem.label
        }))}
        labelsNames={["tracker_id", "date", "fuelPrice", "totalPrice"]}
        header={[
          "Item de Rastreador",
          "Data de Abastecimento",
          "Valor por Litro",
          "Preo Total"
        ]}
        changeValue={data => props.history.push(`/supply/form/${data.id}`)}
      />
    </>
  );
}

const mapStateToProps = state => ({
  supplies: state.supplyState.supplies,
  loading: state.supplyState.loading
});

const mapDispatchToProps = dispatch =>
  bindActionCreators(SupplyActions, dispatch);

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Supply);
