import { createActions, createReducer } from "reduxsauce";

export const { Types, Creators } = createActions({
  fetchViolationRequest: ["query"],
  fetchViolationSuccess: ["data"],
  fetchViolationIdSuccess: ["data"],
  fetchViolationFailure: [],
  updateViolationRequest: ["data"],
  deleteViolationRequest: ["id"],
  createViolationRequest: ["data"],
  clearViolation: []
});

const INITIAL_STATE = {
  violations: [],
  violation: {},
  violationByTracker: {},
  loading: false
};

const clearViolation = (state = INITIAL_STATE, actions) => ({
  ...state,
  violation: []
});

const fetchViolationRequest = (state = INITIAL_STATE, actions) => ({
  ...state,
  loading: true
});

const fetchViolationSuccess = (state = INITIAL_STATE, actions) => ({
  ...state,
  violations: actions.data,
  loading: false
});

const fetchViolationIdSuccess = (state = INITIAL_STATE, actions) => {
  return {
    ...state,
    violation: actions.data,
    loading: false
  };
};

const fetchViolationFailure = (state = INITIAL_STATE, actions) => {
  return {
    ...state,
    loading: false,
    violations: []
  };
};

const createViolationRequest = (state = INITIAL_STATE, actions) => ({
  ...state,
  loading: true
});

const updateViolationRequest = (state = INITIAL_STATE, actions) => ({
  ...state,
  loading: true
});

const deleteViolationRequest = (state = INITIAL_STATE, actions) => ({});

export default createReducer(INITIAL_STATE, {
  [Types.FETCH_VIOLATION_REQUEST]: fetchViolationRequest,
  [Types.FETCH_VIOLATION_SUCCESS]: fetchViolationSuccess,
  [Types.FETCH_VIOLATION_ID_SUCCESS]: fetchViolationIdSuccess,
  [Types.FETCH_VIOLATION_FAILURE]: fetchViolationFailure,
  [Types.UPDATE_VIOLATION_REQUEST]: updateViolationRequest,
  [Types.DELETE_VIOLATION_REQUEST]: deleteViolationRequest,
  [Types.CREATE_VIOLATION_REQUEST]: createViolationRequest,
  [Types.CLEAR_VIOLATION]: clearViolation
});
