import React, { useEffect } from "react";
import { connect } from "react-redux";
import { Creators as BufferActions } from "../../store/ducks/bufferSauce";
import { bindActionCreators } from "redux";
import {
  ModalContainer,
  ModalHeader,
  ModalDetail,
  ModalButtons,
  ButtonModal,
  InputName
} from "./styles";
// import { Input } from "./global";
import { toast } from "react-toastify";
import api from "../../services/api";
import Field from "../Global/Field";
import Modal from "react-awesome-modal";
import { useFormik } from "formik";

function Map(props) {
  const formik = useFormik({
    initialValues: {
      name: "",
      geometry: {}
    },
    onSubmit: value => {
      const data = {
        name: value.name,
        geojson: value.geometry,
        type: "fence"
      };
      try {
        api.createGeometry(data);
        console.log("Cerca criada com sucesso");
        toast.success("Cerca criada com sucesso!");
      } catch (error) {
        console.log("Ocorreu um erro");
        toast.error("Ocorreu um erro");
      }
      // const response = api.createGeometry(data);
      props.setViewModal();
    }
  });
  useEffect(() => {
    formik.setValues({
      ...formik.values,
      geometry: props.geoJson,
      name: ""
    });
  }, [props.geoJson]);

  function cancel() {
    props.setViewModal();
    formik.setValues({ name: "", geoJson: {} });
  }
  return (
    <Modal
      visible={props.viewModal}
      height="300"
      width="300"
      effect="fadeInUp"
      onClickAway={() => props.setViewModal()}
      // onRequestClose={() => props.setViewModal()}
    >
      <ModalContainer>
        <ModalHeader>
          {props.type === "marker"
            ? "Criar Ponto de Referncia"
            : "Criar cerca"}
        </ModalHeader>
        <form onSubmit={formik.handleSubmit}>
          <ModalDetail>
            <Field
              labelName="Nome"
              value={formik.values.name}
              parentCallback={formik.handleChange}
              name="name"
              // metadata={{ ...metadataName }}
            />
          </ModalDetail>
          <ModalButtons>
            <ButtonModal type="button" color="#d21212" onClick={cancel}>
              cancelar
            </ButtonModal>
            <ButtonModal type="submit" color="#4A6971">
              Criar
            </ButtonModal>
          </ModalButtons>
        </form>
      </ModalContainer>
    </Modal>
  );
}
const mapStateToProps = state => ({
  viewModal: state.bufferState.viewModal
});
const mapDispatchToProps = dispatch =>
  bindActionCreators(BufferActions, dispatch);

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Map);
