import React, { useState, useEffect } from "react";
import {
  FormContainer,
  FormData,
  ContainerTitle,
  DataContainer,
  ButtonsContainer,
  ContainerStyled,
  ProfileContainer,
  Row,
  FieldContainer,
  FormDocs,
  TabContainer,
  LabelStyled,
  TabView
} from "../Global/index";
import { toast } from "react-toastify";
import { ErrorMessage, DateStyle, SelectStyled, Button } from "../Global";
import moment from "moment";
import _ from "lodash";
import { registerLocale } from "react-datepicker";
import el from "date-fns/locale/pt-BR";
import InputMask from "react-input-mask";
import pt from "date-fns/locale/pt";
import { useFormik } from "formik";
import { connect } from "react-redux";
import { Creators as JourneyActions } from "../../store/ducks/journeySauce";
import { bindActionCreators } from "redux";
import api from "../../services/api";
import SelectDriver from "../Global/SelectDriver";
import SelectTracker from "../Global/SelectTracker";

function Forms(props) {
  registerLocale("pt-BR", el);
  const [tab, setTab] = useState(1);

  const formik = useFormik({
    initialValues: {
      tracker_id: "",
      driver_id: "",
      start: "",
      end: ""
    },
    validate: values => {
      console.log(values);
      const err = {};
      const message = "Campo obrigatrio";
      if (!values.tracker_id) err.tracker_id = message;
      if (!values.driver_id) err.driver_id = message;
      if (!values.start) err.start = message;
      if (!values.end) err.end = message;
      if (values.start !== "" && values.end !== "") {
        if (values.start > values.end) {
          toast.error("Data Inicial no pode ser superior  Data Final.");
          err.start = message;
          err.end = message;
        }
      }
      return err;
    },
    onSubmit: data => {
      if (data.id) {
        props.updateJourneyRequest(data);
      } else {
        props.createJourneyRequest(data);
      }
      props.clearJourney();
    }
  });

  function cancelButton() {
    props.history.push("/journey");
    props.clearJourney();
  }

  useEffect(() => {
    if (props.match.params.id) {
      props.fetchJourneyRequest(`/${props.match.params.id}`);
    }
  }, []);

  useEffect(() => {
    if (!_.isEmpty(props.journey)) {
      formik.setValues({
        ...props.journey,
        start: moment(props.journey.start).toDate(),
        end: moment(props.journey.end).toDate()
      });
    }
  }, [props.journey]);

  moment.defaultFormat = "DD.MM.YYYY HH:mm";

  return (
    <ContainerStyled>
      <FormContainer>
        <TabContainer>
          <TabView active={tab === 1} onClick={() => setTab(1)}>
            DADOS
          </TabView>
          {/* <TabView active={tab === 2} onClick={() => setTab(2)}>
            RELATRIOS
          </TabView> */}
        </TabContainer>
        {tab === 1 ? (
          <form onSubmit={formik.handleSubmit}>
            <FormData>
              <DataContainer>
                <ContainerTitle>JORNADA</ContainerTitle>
                <ProfileContainer>
                  <div>
                    <Row>
                      <FieldContainer>
                        {(props.match.params.id && formik.values.tracker_id) ||
                        !props.match.params.id ? (
                          <SelectTracker
                            defaultValue={formik.values.tracker_id}
                            parentCallback={option =>
                              formik.setFieldValue("tracker_id", option.id)
                            }
                            error={
                              formik.touched.tracker_id &&
                              formik.errors.tracker_id
                            }
                          />
                        ) : (
                          <></>
                        )}
                      </FieldContainer>
                      <FieldContainer>
                        {(props.match.params.id && formik.values.driver_id) ||
                        !props.match.params.id ? (
                          <SelectDriver
                            defaultValue={formik.values.driver_id}
                            parentCallback={option =>
                              formik.setFieldValue("driver_id", option.id)
                            }
                            error={
                              formik.touched.driver_id &&
                              formik.errors.driver_id
                            }
                          />
                        ) : (
                          <></>
                        )}
                      </FieldContainer>
                      <FieldContainer>
                        <div>
                          <LabelStyled>Incio da jornada</LabelStyled>
                          {formik.touched.start && formik.errors.start ? (
                            <ErrorMessage>{formik.errors.start}</ErrorMessage>
                          ) : (
                            ""
                          )}
                        </div>
                        <DateStyle
                          showTimeSelect
                          dateFormat={"dd.MM.yyyy HH:mm:ss"}
                          selected={formik.values.start}
                          onChange={value =>
                            formik.setFieldValue(
                              "start",
                              moment(value, moment.defaultFormat).toDate(
                                "DD.MM.YYYY HH:mm:ss"
                              )
                            )
                          }
                          locale={pt}
                          error={formik.touched.start && formik.errors.start}
                        />
                      </FieldContainer>
                      <FieldContainer>
                        <div>
                          <LabelStyled>Final da jornada</LabelStyled>
                          {formik.touched.end && formik.errors.end ? (
                            <ErrorMessage>{formik.errors.end}</ErrorMessage>
                          ) : (
                            ""
                          )}
                        </div>
                        <DateStyle
                          showTimeSelect
                          dateFormat={"dd.MM.yyyy HH:mm:ss"}
                          selected={formik.values.end}
                          onChange={value =>
                            formik.setFieldValue(
                              "end",
                              moment(value, moment.defaultFormat).toDate(
                                "DD.MM.YYYY HH:mm:ss"
                              )
                            )
                          }
                          locale={pt}
                          error={formik.touched.end && formik.errors.end}
                        />
                      </FieldContainer>
                    </Row>
                  </div>
                </ProfileContainer>
              </DataContainer>
              <ButtonsContainer>
                <Button
                  onClick={() => cancelButton()}
                  type="button"
                  color="#E33939"
                >
                  Cancelar
                </Button>
                <Button type="submit" color="#555">
                  Salvar
                </Button>
              </ButtonsContainer>
            </FormData>
          </form>
        ) : (
          <FormDocs>RELATRIOS</FormDocs>
        )}
      </FormContainer>
    </ContainerStyled>
  );
}
const mapStateToProps = state => ({
  journey: state.journeyState.journey,
  loading: state.journeyState.loading
});

const mapDispatchToProps = dispatch =>
  bindActionCreators(JourneyActions, dispatch);

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Forms);
