import React, { useState, useEffect } from "react";
import { connect } from "react-redux";
import {
  ContainerStyled,
  TableContainer,
  FieldContainer,
  LabelStyled,
  ContainerFilters,
  HeaderFilter,
  ButtonsFilter,
  BodyFilter,
  FilterButton,
  FilterContainer
} from "../Global";
import { AddButtonStyled, Input, Map, PositionContainer } from "./styles";
import { Creators as RouteActions } from "../../store/ducks/routeSauce";
import { bindActionCreators } from "redux";
import { FaMinus, FaPlus } from "react-icons/fa";
import { toast } from "react-toastify";
import mapStyle from "../../assets/map.json";

function RouteMap(props) {
  const [name, setName] = useState("");
  const [origin, setOrigin] = useState("");
  const [destination, setDestination] = useState("");
  const [map, setMap] = useState("");
  const [type, setType] = useState("trip");
  const [geometries, setGeometries] = useState("");
  const [id, setId] = useState("");
  const [newPosition, setNewPosition] = useState(""); // Adding one more position
  const [waypoints, setWaypoints] = useState([]); // Current positions
  const [routeNotFound, setRouteNotFound] = useState(false); // Current positions
  const [originInput, setOriginInput] = useState("");
  const [destinationInput, setDestinationInput] = useState("");

  var originAutocomplete = "";

  useEffect(() => {
    if (props.match.params.id) {
      props.fetchRouteRequest(`${props.match.params.id}`);
    }
  }, []);

  var directionsService = new window.google.maps.DirectionsService();
  var directionsDisplay = new window.google.maps.DirectionsRenderer({
    map: map
  });

  useEffect(() => {
    if (props.route.geojson !== undefined) {
      console.log(props.route, "Propsroute");
      setName(props.route.name);
      setOrigin(props.route.geojson.origin);
      setDestination(props.route.geojson.destination);
      setId(props.route.id);
      setGeometries(props.route.geojson);

      // Passing parameters for editing
      new AutocompleteDirectionsHandler(
        map,
        props.route.geojson.request.origin.placeId,
        props.route.geojson.request.destination.placeId
      );

      // Show Loaded Map
      directionsService.route(
        {
          origin: {
            placeId: props.route.geojson.request.origin.placeId
          },
          destination: {
            placeId: props.route.geojson.request.destination.placeId
          },
          // waypoints: [
          //   {
          //     stopover: true,
          //     location: {
          //       placeId: "ChIJRVj1dgPP20YRBWB4A_sUx_Q"
          //     }
          //   }
          // ],
          optimizeWaypoints: true,
          travelMode: window.google.maps.TravelMode.DRIVING
        },
        function(response, status) {
          if (status === "OK") {
            directionsDisplay.setDirections(response);
          } else {
            window.alert("Directions request failed due to " + status);
          }
        }
      );
    }
    console.log("PROPS.ROUTE");
  }, [props.route]);

  useEffect(() => {
    const map = new window.google.maps.Map(document.getElementById("map"), {
      center: { lat: -20.361797, lng: -40.660631 },
      fullscreenControl: false,
      mapTypeControl: false,
      zoomControl: true,
      scaleControl: true,
      streetViewControl: true,
      streetViewControlOptions: {
        position: window.google.maps.ControlPosition.RIGHT_BOTTOM
      },
      zoom: 15,
      controlSize: 25,
      styles: mapStyle
    });
    setMap(map);
    new AutocompleteDirectionsHandler(map);
  }, []);

  function AutocompleteDirectionsHandler(map, origin, destination) {
    console.log(origin, "recebi origin");
    console.log(destination, "recebi destination");

    this.map = map;
    this.originPlaceId = null;
    this.destinationPlaceId = null;
    this.travelMode = "DRIVING";
    this.directionsService = new window.google.maps.DirectionsService();
    this.directionsRenderer = new window.google.maps.DirectionsRenderer();
    this.directionsRenderer.setMap(map);

    // setOriginInput(document.getElementById("origin-input"));
    // setDestinationInput(document.getElementById("destination-input"));
    originAutocomplete = new window.google.maps.places.Autocomplete(
      document.getElementById("origin-input")
    );

    // Specify just the place data fields that you need.
    originAutocomplete.setFields(["place_id"]);

    var destinationAutocomplete = new window.google.maps.places.Autocomplete(
      document.getElementById("destination-input")
    );
    // Specify just the place data fields that you need.
    destinationAutocomplete.setFields(["place_id"]);

    this.setupPlaceChangedListener(originAutocomplete, "ORIG", destination);
    this.setupPlaceChangedListener(destinationAutocomplete, "DEST", origin);
    console.log(origin, "ORIGIN HENRIQUE");
    console.log(destination, "DESTINATION HENRIQUE");
  }

  AutocompleteDirectionsHandler.prototype.setupPlaceChangedListener = function(
    autocomplete,
    mode,
    placeId
  ) {
    var me = this;
    autocomplete.bindTo("bounds", this.map);

    autocomplete.addListener("place_changed", function() {
      var place = autocomplete.getPlace();

      if (!place.place_id) {
        window.alert("Please select an option from the dropdown list.");
        return;
      }

      if (mode === "ORIG") {
        // If updating ORIG, receive DESTINY value from props
        me.originPlaceId = place.place_id;
        setOrigin(autocomplete.gm_accessors_.place.dd.formattedPrediction);
        if (props.match.params.id) {
          console.log("ATENCAO PREENCHENDO ORIGEM");
          console.log(me.originPlaceId, "me.originPlaceId");
          console.log(placeId, "placeId");
          if (me.destinationPlaceId == null) {
            me.destinationPlaceId = placeId;
          }
        }
        console.log("ORIGEM");
        console.log(me.originPlaceId, "origem id");
        console.log(me.destinationPlaceId, "destino id");
      } else {
        // If updating DESTINATION, receive ORIG value from props
        me.destinationPlaceId = place.place_id;
        setDestination(autocomplete.gm_accessors_.place.dd.formattedPrediction);
        if (props.match.params.id) {
          console.log("ATENCAO PREENCHENDO DESTINATION");
          console.log(me.originPlaceId, "me.originPlaceId");
          console.log(placeId, "placeId");
          if (me.originPlaceId == null) {
            me.originPlaceId = placeId;
          }
        }
        console.log("DESTINATION");
        console.log(me.originPlaceId, "origem id");
        console.log(me.destinationPlaceId, "destino id");
      }
      console.log(me, "me enviado");
      me.route();
    });
  };

  AutocompleteDirectionsHandler.prototype.route = function() {
    if (!this.originPlaceId || !this.destinationPlaceId) {
      return;
    }
    var me = this;
    this.directionsService.route(
      {
        origin: { placeId: this.originPlaceId },
        destination: { placeId: this.destinationPlaceId },
        // waypoints: { placeId: "ChIJOWUb4ha1uQARcZlbYQ19F28" },
        // optimizeWaypoints: true,
        travelMode: "DRIVING"
      },
      function(response, status) {
        if (status === "OK") {
          me.directionsRenderer.setDirections(response);
          setGeometries(response);
          console.log(response, "geometries");
          directionsDisplay.setMap(null);
          setRouteNotFound(false);
        } else {
          toast.error("Rota no encontrada.");
          setRouteNotFound(true);
        }
      }
    );
  };

  const handleSubmit = evt => {
    if (evt !== undefined) {
      evt.preventDefault();
      if (geometries === "") {
        console.log("nenhuma geometria encontrada");
      }
      if (name === "") {
        console.log("nenhuma nome encontrada");
        toast.alert("Nome obrigatrio");
      }
      if (origin === "") {
        console.log("nenhuma origin encontrada");
        toast.toast.alert("Origem obrigatrio");
      }
      if (destination === "") {
        console.log("nenhuma destomatopm encontrada");
        toast.alert("Destino obrigatrio");
      } else {
        const newGeometries = {
          ...geometries,
          origin,
          destination
        };
        let data = { name: name, type: type, id: id, geojson: newGeometries };
        console.log(data, "data");
        // Validation
        if (!routeNotFound) {
          if (data.name && data.geojson) {
            if (data.id) {
              props.updateRouteRequest(data);
              console.log("ALTERAR");
            } else {
              console.log("CRIAR");
              props.createRouteRequest(data);
              // console.log(props.createFenceRequest);
            }
          } else {
            console.log("NAO  POSSIVEL SALVAR");
          }
        } else {
          toast.error("Rota no encontrada.");
        }
      }
      clearVariables();
    }
  };

  function handleDelete(position) {
    setWaypoints(waypoints.filter(item => item !== position));
    console.log(position);
  }

  function handleInclude(position) {
    setWaypoints([...waypoints, { location: position.value, stopover: true }]);
    console.log(position.value, "position");
    var s = document.getElementById("destination-input");
    s.value = "";
  }

  function backButton() {
    clearVariables();
    props.history.push("/route/");
  }

  function clearVariables() {
    props.clearRoute();
    setName("");
    setOrigin("");
    setDestination("");
    setId("");
    setOriginInput("");
    setDestinationInput("");
    originAutocomplete = "";
  }

  return (
    <ContainerStyled>
      <FilterContainer>
        <ContainerFilters>
          <form onSubmit={handleSubmit}>
            <HeaderFilter>ROTAS</HeaderFilter>
            <BodyFilter>
              <FieldContainer>
                <LabelStyled>Nome</LabelStyled>
                <div>
                  <Input
                    className="controls"
                    type="text"
                    value={name}
                    name="name"
                    onChange={e => setName(e.target.value)}
                  />
                </div>
              </FieldContainer>
              <FieldContainer>
                <LabelStyled>Origem</LabelStyled>
                <div>
                  <Input
                    id="origin-input"
                    className="controls"
                    type="text"
                    defaultValue={origin}
                  />
                </div>
              </FieldContainer>
              {/* {waypoints && waypoints.length > 0 ? (
                <LabelStyled>Paradas</LabelStyled>
              ) : (
                <></>
              )} */}
              {/* <>
                {waypoints.length > 0 ? (
                  waypoints.map(item => (
                    <PositionContainer>
                      <div>
                        <Input key={item} defaultValue={item.location} />
                        <AddButtonStyled
                          onClick={() => handleDelete(item)}
                          type="button"
                        >
                          <FaMinus />
                        </AddButtonStyled>
                      </div>
                    </PositionContainer>
                  ))
                ) : (
                  <></>
                )}
              </> */}
              <FieldContainer>
                <LabelStyled>Destino</LabelStyled>
                <div>
                  {/* <AddButtonStyled
                    onClick={() =>
                      handleInclude(
                        document.getElementById("destination-input")
                      )
                    }
                    type="button"
                  >
                    <FaPlus />
                  </AddButtonStyled> */}
                  <Input
                    id="destination-input"
                    className="controls"
                    type="text"
                    defaultValue={destination}
                  />
                </div>
              </FieldContainer>
            </BodyFilter>
            <ButtonsFilter>
              <FilterButton
                back="#bfbfbf"
                front="#000000"
                onClick={() => backButton()}
                type="button"
              >
                Voltar
              </FilterButton>
              <FilterButton
                color="#4A6971"
                type="submit"
                onclick={handleSubmit()}
              >
                Salvar
              </FilterButton>
            </ButtonsFilter>
          </form>
        </ContainerFilters>
      </FilterContainer>
      <TableContainer>
        <Map id="map"></Map>
      </TableContainer>
    </ContainerStyled>
  );
}

const mapStateToProps = state => ({
  routes: state.routeState.routes,
  route: state.routeState.route,
  loading: state.routeState.loading
});

const mapDispatchToProps = dispatch =>
  bindActionCreators(RouteActions, dispatch);

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(RouteMap);
