import { createActions, createReducer } from "reduxsauce";

export const { Types, Creators } = createActions({
  fetchReportRequest: ["query"],
  fetchReportSuccess: ["data"],
  fetchReportIdSuccess: ["data"],
  fetchReportFailure: [],
  updateReportRequest: ["data"],
  deleteReportRequest: ["id"],
  createReportRequest: ["data"],
  clearReport: []
});

const INITIAL_STATE = {
  reports: [],
  report: {},
  reportByTracker: {},
  loading: false
};

const clearReport = (state = INITIAL_STATE, actions) => ({
  ...state,
  report: []
});

const fetchReportRequest = (state = INITIAL_STATE, actions) => ({
  ...state,
  loading: true
});

const fetchReportSuccess = (state = INITIAL_STATE, actions) => ({
  ...state,
  reports: actions.data,
  loading: false
});

const fetchReportIdSuccess = (state = INITIAL_STATE, actions) => {
  return {
    ...state,
    report: actions.data,
    loading: false
  };
};

const fetchReportFailure = (state = INITIAL_STATE, actions) => {
  return {
    ...state,
    loading: false,
    reports: []
  };
};

const createReportRequest = (state = INITIAL_STATE, actions) => ({
  ...state,
  loading: true
});

const updateReportRequest = (state = INITIAL_STATE, actions) => ({
  ...state,
  loading: true
});

const deleteReportRequest = (state = INITIAL_STATE, actions) => ({});

export default createReducer(INITIAL_STATE, {
  [Types.FETCH_REPORT_REQUEST]: fetchReportRequest,
  [Types.FETCH_REPORT_SUCCESS]: fetchReportSuccess,
  [Types.FETCH_REPORT_ID_SUCCESS]: fetchReportIdSuccess,
  [Types.FETCH_REPORT_FAILURE]: fetchReportFailure,
  [Types.UPDATE_REPORT_REQUEST]: updateReportRequest,
  [Types.DELETE_REPORT_REQUEST]: deleteReportRequest,
  [Types.CREATE_REPORT_REQUEST]: createReportRequest,
  [Types.CLEAR_REPORT]: clearReport
});
