import React, { useEffect } from "react";
import { connect } from "react-redux";
import { defaults } from "react-chartjs-2";
import { Creators as ChartActions } from "../../store/ducks/chartSauce";
import { bindActionCreators } from "redux";
import { useLocation } from "react-router-dom";
import { LineStyled } from "./styles";

import _ from "lodash";
function useQuery() {
  return new URLSearchParams(useLocation().search);
}

function ChartSpeed(props) {
  let query = useQuery();
  const id_tracker = query.get("id");
  useEffect(() => {
    defaults.global.defaultFontSize = 15;
    props.fetchChartSpeedRequest(id_tracker);
  }, []);
  // GAMBS
  useEffect(() => {
    props.fetchChartSpeedRequest(id_tracker);
  }, [props.buffer]);
  useEffect(() => {
    props.fetchChartSpeedRequest(id_tracker);
  }, [id_tracker]);
  const data = {
    labels: props.speeds.map(x => ""),
    datasets: [
      {
        label: "Velocidade",
        fill: false,
        lineTension: 0.1,
        backgroundColor: "rgba(75,192,192,0.4)",
        borderColor: "rgba(75,192,192,1)",
        borderCapStyle: "butt",
        borderDash: [],
        borderDashOffset: 0.0,
        borderJoinStyle: "miter",
        pointBorderColor: "rgba(75,192,192,1)",
        pointBackgroundColor: "#fff",
        pointBorderWidth: 1,
        pointHoverRadius: 5,
        pointHoverBackgroundColor: "rgba(75,192,192,1)",
        pointHoverBorderColor: "rgba(220,220,220,1)",
        pointHoverBorderWidth: 2,
        pointRadius: 1,
        pointHitRadius: 10,
        data: props.speeds.map(item => item.speed).reverse()
      }
    ]
  };
  return (
    <LineStyled
      data={data}
      options={{ maintainAspectRatio: false, responsive: true }}
    />
  );
}

const mapStateToProps = state => ({
  speeds: state.chartState.speeds,
  buffer: state.bufferState.buffer
});

const mapDispatchToProps = dispatch =>
  bindActionCreators(ChartActions, dispatch);

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ChartSpeed);
