import React, { useEffect } from "react";
import { connect } from "react-redux";
import { Creators as TrackerActions } from "../../store/ducks/trackerSauce";
import { bindActionCreators } from "redux";
import Table from "../Table";

function Tracker(props) {
  useEffect(() => {
    props.fetchTrackerRequest();
  }, []);

  useEffect(() => {
    console.log(props.trackers.map(item => console.log(item, "item")));
  }, [props.trackers]);

  return (
    <Table
      // data={props.trackers || []}
      data={props.trackers.map(item => ({
        ...item,
        active: item.active ? "Ativo" : "Inativo"
      }))}
      labelsNames={["plate", "label", "color", "active", "year"]}
      header={["placa", "Identificador", "Cor", "Status", "Ano"]}
      changeValue={data => props.history.push(`/tracker/form/${data.id}`)}
    />
  );
}

const mapStateToProps = state => ({
  trackers: state.trackerState.trackers,
  loading: state.trackerState.loading
});

const mapDispatchToProps = dispatch =>
  bindActionCreators(TrackerActions, dispatch);

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Tracker);
