import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import { Creators as SurveillanceActions } from "../../store/ducks/surveillanceSauce";
import { bindActionCreators } from "redux";
import {
  ModalContainer,
  ModalHeader,
  ModalTextHeader,
  ModalButtonHeader
} from "./styles";
import Modal from "react-awesome-modal";
import { FaWindowClose } from "react-icons/fa";
import Iframe from "react-iframe";

function SurveillanceModal(props) {
  return (
    <Modal
      visible={props.viewModal}
      // visible={props.viewModal}
      height="340"
      width="560"
      effect="fadeInUp"
      onClickAway={() => props.setViewModal()}
      // onRequestClose={() => props.setViewModal()}
    >
      <ModalContainer>
        <ModalHeader>
          <ModalTextHeader>Vigilncia</ModalTextHeader>
          <ModalButtonHeader>
            <FaWindowClose size="6x" onClick={() => props.setViewModal()} />
          </ModalButtonHeader>
        </ModalHeader>
        <Iframe
          width="560"
          height="315"
          src="https://www.youtube.com/embed/oYEGFHyrcaU"
          frameborder="0"
          allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
          allowfullscreen
        ></Iframe>
        {/* <Player playsInline fluid={false} width={400} height={400}> */}
        {/* <source src="https://media.w3.org/2010/05/sintel/trailer_hd.mp4" /> */}
        {/* </Player> */}
      </ModalContainer>
    </Modal>
  );
}
const mapStateToProps = state => ({
  viewModal: state.surveillanceState.viewModal
});

const mapDispatchToProps = dispatch =>
  bindActionCreators(SurveillanceActions, dispatch);

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(SurveillanceModal);
