import React from "react";
import Tooltip from "rc-tooltip";
import { IconStyled, IconContainer } from "./styles";
import "rc-tooltip/assets/bootstrap.css";

export default function Side(props) {
  return (
    <Tooltip
      placement="right"
      overlay={props.tooltip}
      mouseEnterDelay={0}
      mouseLeaveDelay={0}
      arrowContent={<div className="rc-tooltip-arrow-inner"></div>}
    >
      <IconContainer onClick={() => props.history.push(props.to)}>
        <IconStyled selected={props.selected} src={props.icon} alt="" />
      </IconContainer>
    </Tooltip>
  );
}
