import React from "react";

import {
  Page,
  Text,
  View,
  Document,
  StyleSheet,
  Image
} from "@react-pdf/renderer";

const styles = StyleSheet.create({
  page: {
    flexDirection: "column",
    backgroundColor: "#E4E4E4",
    height: "100%",
    width: "100%"
  },
  header: {
    height: "3%",
    flexDirection: "row",
    justifyContent: "space-between",
    alignItems: "center",
    margin: 5,
    backgroundColor: "#fff"
  },
  detail: {
    height: `93%`,
    margin: 5
  },
  footer: {
    height: "4%",
    backgroundColor: "#0000ff"
  },
  image: {
    height: "30px",
    width: "30px"
  },
  headerName: {
    fontSize: 17
  },
  headerDetail: {
    fontSize: 7,
    color: "#333",
    marginLeft: 3
  },
  textHeader: {
    marginLeft: 5
  },
  headerLogo: {
    flexDirection: "row"
  },
  headerDateDetail: {
    fontSize: 7,
    color: "#333"
  },
  headerRigth: {
    flexDirection: "column",
    alignContent: "space-between"
  },
  reportName: {
    fontSize: 13
  },
  containerDate: {
    flexDirection: "row",
    justifyContent: "space-between"
  },
  tableHeader: {
    height: 15,
    backgroundColor: "#3F4D85",
    flexDirection: "row",
    alignItems: "center",
    borderTopLeftRadius: 3,
    borderTopRightRadius: 3
  },
  tableHeaderText: {
    fontSize: 10,
    color: "#fff"
  },
  tableDetail: {
    flexDirection: "column"
  },
  tableDetailLine: {
    flexDirection: "row",
    border: 1,
    margin: 0,
    borderColor: "#dfdfdf"
  },
  tableDetailText: {
    fontSize: 10
  },
  tableWidth: {
    width: 60
  },
  tableFooter: {
    height: 15,
    backgroundColor: "#3F4D85",
    borderBottomLeftRadius: 3,
    borderBottomRightRadius: 3
  }
});

export default function Pdf(props) {
  return (
    <View style={styles.detail}>
      <View style={styles.tableHeader} fixed>
        {props.header.map((item, index) => (
          <View style={{ width: props.labelWidth[index] }} key={item}>
            <Text style={styles.tableHeaderText}>{item}</Text>
          </View>
        ))}
      </View>
      <View style={styles.tableDetail}>
        {props.data.map(item => (
          <View style={styles.tableDetailLine}>
            {props.labelNames.map((teste, index) => (
              <View style={{ width: props.labelWidth[index] }}>
                <Text style={styles.tableDetailText}>
                  {item[teste] ? item[teste].toString() : ""}
                </Text>
              </View>
            ))}
          </View>
        ))}
      </View>
      <View style={styles.tableFooter} fixed></View>
    </View>
  );
}
