import React, { useState, useEffect } from "react";
import mapStyle from "../../assets/map.json";
import { MapContainer, Map, IconContainer } from "./styles";
import api from "../../services/api";
import { MdFullscreen, MdFullscreenExit } from "react-icons/md";
import { connect } from "react-redux";
import { Creators as DailyActions } from "../../store/ducks/dailySauce";
import { bindActionCreators } from "redux";
import markerStart from "../../assets/markerStart.png";
import markerFinish from "../../assets/markerFinish.svg";

function Daily(props) {
  const [map, setMap] = useState();
  const [markers, setMarkers] = useState([]);

  useEffect(() => {
    const map = new window.google.maps.Map(document.getElementById("map"), {
      center: { lat: -20.361797, lng: -40.660631 },
      fullscreenControl: false,
      mapTypeControl: false,
      zoomControl: false,
      scaleControl: false,
      streetViewControl: false,
      streetViewControlOptions: {
        position: window.google.maps.ControlPosition.RIGHT_BOTTOM
      },
      zoom: 13,
      controlSize: 25,
      styles: mapStyle
    });
    setMap(map);
  }, []);

  async function getDaily() {
    if (props.daily[0] && props.daily[0].st_asgeojson) {
      const coordinates = JSON.parse(props.daily[0].st_asgeojson);
      let geojson = {
        type: "Feature",
        geometry: coordinates
      };
      map.data.addGeoJson(geojson);
      map.data.setStyle({
        strokeColor: "#3AAAAA",
        strokeOpacity: 0.8,
        strokeWeight: 3
      });

      markers.push(
        new window.google.maps.Marker({
          position: new window.google.maps.LatLng(
            coordinates.coordinates[0][1],
            coordinates.coordinates[0][0]
          ),
          icon: {
            url: markerStart,
            origin: new window.google.maps.Point(0, 0),
            scaledSize: new window.google.maps.Size(20, 20),
            anchor: new window.google.maps.Point(0, 20)
          },

          map: map
        })
      );

      markers.push(
        new window.google.maps.Marker({
          position: new window.google.maps.LatLng(
            coordinates.coordinates[coordinates.coordinates.length - 1][1],
            coordinates.coordinates[coordinates.coordinates.length - 1][0]
          ),
          icon: {
            url: markerFinish,
            origin: new window.google.maps.Point(0, 0),
            scaledSize: new window.google.maps.Size(20, 20),
            anchor: new window.google.maps.Point(0, 20)
          },

          map: map
        })
      );
      let bounds = new window.google.maps.LatLngBounds();
      coordinates.coordinates.map(item => {
        bounds.extend(new window.google.maps.LatLng(item[1], item[0]));
      });
      map.fitBounds(bounds);
    }
  }

  useEffect(() => {
    if (map) {
      map.data.forEach(function(feature) {
        map.data.remove(feature);
      });
      markers.map(marker => {
        marker.setMap(null);
      });
      getDaily();
    }
  }, [props.daily]);
  return (
    <MapContainer detail={props.showDetail}>
      <IconContainer onClick={() => props.setShowDetail()}>
        {props.showDetail ? <MdFullscreen /> : <MdFullscreenExit />}
      </IconContainer>
      <Map id="map" />
    </MapContainer>
  );
}

const mapStateToProps = state => ({
  daily: state.dailyState.daily,
  showDetail: state.dailyState.showDetail
});

const mapDispatchToProps = dispatch =>
  bindActionCreators(DailyActions, dispatch);

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Daily);
