import React, { useEffect, useState } from "react";
import _ from "lodash";
import { HorizontalBarResize } from "../Global";
const axios = require("axios");

function DistanceChart(props) {
  const [traveledDistanceData, setTraveledDistanceData] = useState({});
  const [distance, setDistance] = useState([]);
  const [plate, setPlate] = useState([]);
  const [start, setStart] = useState([]);
  const [end, setEnd] = useState([]);

  function getDriverViolation() {
    axios
      .get("http://localhost:3001/distanceTraveled")
      .then(resp => {
        setTraveledDistanceData(resp.data);
      })
      .catch(error => {
        console.log(error);
      });
  }

  useEffect(() => {
    getDriverViolation();
  }, []);

  useEffect(() => {
    if (traveledDistanceData.length > 0) {
      setPlate(traveledDistanceData.map(item => item.plate));
      setDistance(traveledDistanceData.map(item => item.distance));
      setStart(traveledDistanceData.map(item => item.start));
      setEnd(traveledDistanceData.map(item => item.end));
    }
  }, [traveledDistanceData]);

  const data = {
    labels: plate,
    datasets: [
      {
        label: "KM Percorridos por Veculo",
        backgroundColor: "rgba(75,192,192,0.4)",
        borderColor: "rgba(75,192,192,1)",
        borderWidth: 1,
        hoverBackgroundColor: "rgba(75,192,192,1)",
        hoverBorderColor: "rgba(220,220,220,1)",
        data: distance
      }
    ]
  };

  return (
    <>
      {traveledDistanceData !== undefined ? (
        <HorizontalBarResize
          width={null}
          height={300}
          options={{ maintainAspectRatio: false, responsive: false }}
          // options={{ maintainAspectRatio: 1 }}
          data={data}
        />
      ) : (
        ""
      )}
    </>
  );
}

export default DistanceChart;
