import React, { useState, useEffect } from "react";
import {
  Page,
  Text,
  View,
  Image,
  Font,
  Document,
  StyleSheet
} from "@react-pdf/renderer";
import { PdfRender } from "./styles";
import el from "date-fns/locale/pt-BR";
import pt from "date-fns/locale/pt";
import { registerLocale } from "react-datepicker";
import { connect } from "react-redux";
import { Creators as TrackerActions } from "../../store/ducks/trackerSauce";
import { bindActionCreators } from "redux";
import { useFormik } from "formik";
import moment from "moment";
import api from "../../services/api";
import robotoBold from "../../assets/fonts/Roboto-Bold.ttf";
import robotoMediumItalic from "../../assets/fonts/Roboto-MediumItalic.ttf";
import robotoThinItalic from "../../assets/fonts/Roboto-ThinItalic.ttf";
import logo from "../../assets/GroupglobeOn.jpg";

function Maintenance(props) {
  registerLocale("pt-BR", el);
  const [trackers, setTrackers] = useState([]);
  const formik = useFormik({
    initialValues: {
      date: moment().format("DD/MM/YYYY - HH:mm:ss"),
      type: "Listagem de Manutenes",
      client: "Cliente",
      data: {}
    }
    // onSubmit: query => {
    //   console.log(query);
    //   formik.setValues({ data: { ...query } });
    // }
  });

  Font.register({
    family: "Roboto",
    format: "sans-serif",
    src: robotoBold
  });

  Font.register({
    family: "RobotoThinItalic",
    format: "sans-serif",
    src: robotoThinItalic
  });

  Font.register({
    family: "RobotoMediumItalic",
    format: "sans-serif",
    src: robotoMediumItalic
  });

  Font.registerHyphenationCallback(word => {
    return [word];
  });

  const styles = StyleSheet.create({
    page: {
      backgroundColor: "#fff"
    },
    displayHeader: {
      marginTop: 30,
      marginLeft: 10,
      height: 70,
      padding: 0
    },
    dateSection: {
      marginRight: 10,
      textAlign: "right",
      height: 100
    },
    imageSection: {
      marginTop: 30,
      position: "absolute"
    },
    styledDate: {
      marginTop: 10,
      fontSize: 10,
      textDecoration: "underline",
      fontFamily: "RobotoThinItalic"
    },
    styledReportType: {
      fontSize: 14,
      marginTop: 5
    },
    styledGeo: {
      color: "#4caf50",
      fontFamily: "RobotoMediumItalic",
      fontSize: 20
    },
    styledRastreamento: {
      color: "#1a237e",
      fontFamily: "RobotoMediumItalic"
    },
    styledClient: {
      fontSize: 12,
      marginTop: 5
    },
    styledLogo: {
      marginLeft: 30,
      height: 60,
      width: 60
    },
    landscapeBorderTopContainer: {
      marginTop: 20,
      height: 0,
      width: 842
    },
    landscapeBorderBottomContainer: {
      height: 0,
      width: 842
    },
    portraitBorderContainer: {
      marginTop: 110,
      height: 0,
      width: 600,
      position: "absolute"
    },
    table: {
      display: "flex"
    },
    tableRow: {
      flexDirection: "row",
      justifyContent: "center"
    },
    tableHeader: {
      width: 80,
      height: 28,
      color: "#ffffff",
      borderBottom: "1 solid #d1d1d1",
      textAlign: "center",
      justifyContent: "center",
      backgroundColor: "#4a6971"
    },
    tableHeaderContent: {
      fontSize: 9,
      width: 80,
      textTransform: "uppercase"
    },
    tableContent: {
      width: 70,
      borderLeft: "1 solid #d1d1d1",
      borderRight: "1 solid #d1d1d1",
      borderTop: "none"
    },
    tableInfoContent: {
      textAlign: "center",
      fontSize: 8,
      width: 79,
      height: 23,
      borderBottom: "1 solid #d1d1d1"
    },
    pageNumbers: {
      left: 0,
      right: 0,
      justifyContent: "center",
      textAlign: "center",
      fontSize: 10,
      marginTop: 1
    }
  });

  const getTrackers = async () => {
    const response = await api.fetchTracker();
    setTrackers(response.data);
  };

  useEffect(() => {
    getTrackers();
  }, []);

  return (
    <PdfRender>
      <Document
        style={styles.teste}
        title={`Listagem de Manutenes - Gerado em: ${formik.values.date}`}
      >
        <Page size="A4" style={styles.page} orientation="portrait" wrap>
          <View
            style={[styles.flex, { flexDirection: "row", display: "flex" }]}
            fixed
          >
            <View style={styles.dateSection}>
              <Text style={styles.styledDate}>{formik.values.date}</Text>
            </View>

            <View style={styles.imageSection}>
              <Image style={styles.styledLogo} src={logo} />
            </View>
            <View style={styles.displayHeader}>
              <Text style={styles.styledGeo}>
                GEO
                <Text style={styles.styledRastreamento}>RASTREAMENTO</Text>
              </Text>
              <Text style={styles.styledReportType}>{formik.values.type}</Text>
              <Text style={styles.styledClient}>{formik.values.client}</Text>
            </View>
          </View>
          <View
            style={[
              styles.landscapeBorderTopContainer,
              { borderBottom: "1 solid #d9d9d9" }
            ]}
            fixed
          ></View>
          <View fixed>
            <Text style={[styles.invisibleTopText, { color: "transparent" }]}>
              sadsad
            </Text>
          </View>
          <View style={styles.containerInfo}>
            <View style={styles.table}>
              <View style={styles.tableRow} fixed>
                <View style={styles.tableHeader}>
                  <Text style={styles.tableHeaderContent}>Placa</Text>
                </View>
                <View style={styles.tableHeader}>
                  <Text style={styles.tableHeaderContent}>Veculo</Text>
                </View>
                <View style={styles.tableHeader}>
                  <Text style={styles.tableHeaderContent}>
                    Tipo de Manuteno
                  </Text>
                </View>
                <View style={styles.tableHeader}>
                  <Text style={styles.tableHeaderContent}>Ao Realizada</Text>
                </View>
                <View style={styles.tableHeader}>
                  <Text style={styles.tableHeaderContent}>
                    Data da Manuteno
                  </Text>
                </View>
                <View style={styles.tableHeader}>
                  <Text style={styles.tableHeaderContent}>Custo Total</Text>
                </View>
              </View>
              <View style={styles.tableRow}>
                <View style={styles.tableContent}>
                  {trackers.map((item, index) => {
                    return (
                      <Text style={styles.tableInfoContent} key={index}>
                        {item.plate}
                      </Text>
                    );
                  })}
                </View>
                <View style={styles.tableContent}>
                  {trackers.map((item, index) => {
                    return (
                      <Text style={styles.tableInfoContent} key={index}>
                        {item.device_id}
                      </Text>
                    );
                  })}
                </View>
              </View>
            </View>
          </View>
        </Page>
      </Document>
    </PdfRender>
  );
}

const mapStateToProps = state => ({
  trackers: state.trackerState.trackers,
  loading: state.trackerState.loading
});

const mapDispatchToProps = dispatch =>
  bindActionCreators(TrackerActions, dispatch);

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Maintenance);
